import {Action} from '@ngrx/store';

export const updateNavigationContexts = {
  UPDATE_NAVIGATION_CONTEXTS: '[Navigation contexts] Update navigation contexts',
  CLEAR_NAVIGATION_CONTEXTS: '[Navigation contexts] Clear navigation contexts'
};

export class UpdateNavigationContexts implements Action {
  readonly type = updateNavigationContexts.UPDATE_NAVIGATION_CONTEXTS;
  constructor(public payload: Array<INavigationContext>) {}
}

export class ClearNavigationContexts implements Action {
  readonly type = updateNavigationContexts.CLEAR_NAVIGATION_CONTEXTS;
  constructor(public payload?) {}
}


export type NavigationContextsActions = UpdateNavigationContexts
  | ClearNavigationContexts;
