import { Actions, Effect, ofType } from '@ngrx/effects';
import { Injectable } from '@angular/core';
import { map, switchMap, withLatestFrom } from 'rxjs/operators';
import { select, Store } from '@ngrx/store';
import { PublishedSiteService } from '../../services/published-site.service';
import {
  LoadCurrentLanguageSuccess,
  LoadSiteResourcesByName,
  LoadSiteResourcesSuccess,
  multilanguageActions,
  UpdateCurrentLanguage,
  UpdateCurrentLanguageSuccess,
  UpdateLastBPMLanguage,
  UpdateLastBPMLanguageSuccess
} from './multilanguage.actions';
import { getCurrentSite } from '../site/site.selector';
import { TranslateService } from '@ngx-translate/core';
import { getSiteUser } from '../user/user.selector';
import { LoadUserSuccess } from '../user/user.actions';
import { UserService } from '../../../common/services/sites/user.service';
import { OAuthClientHelper } from '../../../common/services/oauth/oauth-client.helper';
import { CultureSettingsService } from 'app/common/services/sites/culture-settings.service';
import * as moment from 'moment';
import { UpdateLoading } from '../loading/loading.actions';
import { getResourcesByName } from './multilanguage.selector';
import { InternalResourcesService } from 'app/common/services/sites/internal-resources.service';
import { BizagiDataService } from 'app/render/services/bizagi-data.service';

@Injectable()
export class MultilanguageEffect {
  constructor(
    private actions$: Actions,
    private _publishedSiteService: PublishedSiteService,
    private _publishedSiteServices: PublishedSiteService,
    private _userService: UserService,
    private _oAuthClientHelper: OAuthClientHelper,
    private _translateService: TranslateService,
    private _store: Store<IRenderState>,
    private _cultureSettingsService: CultureSettingsService,
    private _internalLanguageResources: InternalResourcesService,
    private _bizagiDataService: BizagiDataService
  ) {
  }

  @Effect()
  loadCurrentLanguage$ = this.actions$.pipe(
    ofType(multilanguageActions.LOAD_CURRENT_LANGUAGE),
    withLatestFrom(this._store.pipe(select(getCurrentSite))),
    switchMap(async ([action, site]) => {
      let language:ILanguageResource;
      let siteUser;
      try {
        const username = await this._oAuthClientHelper.getUsername();
        if (username) {
          siteUser = await this._userService.getUserByUsername(username);
          this._store.dispatch(new LoadUserSuccess(siteUser));
        }
      } catch (error) {
      }
      const resources = await this._publishedSiteServices.getResources(site.id);
      this._store.dispatch(new LoadSiteResourcesSuccess(resources));
      const availableLanguages = await  this.setResourcesByName();
      this._store.dispatch(new LoadSiteResourcesByName(availableLanguages));
      const browserLang = this._translateService.getBrowserLang();
      let lang;
      if (siteUser && siteUser.preferences && siteUser.preferences.defaultLanguage) {
        const userLanguage = siteUser.preferences.defaultLanguage;
        const userResource = resources.find(c => c.language.name === userLanguage.substring(0, 2));
        if (userResource) {
          if (resources.length >= 2) {
            lang = userResource.language.name;
          } else {
            lang = browserLang.substring(0, 2);
          }
          language = await this._publishedSiteServices.getResource(userResource.id);
          this.setMomentLang(lang);
          if (_.has(availableLanguages, language.name.toLocaleLowerCase())) {
            lang = language.name.toLocaleLowerCase();
          }
          this._translateService.use(lang);
          this.setUserPreferences(siteUser, language);
          return new LoadCurrentLanguageSuccess(language);
        }
      }
      const browserResource = resources.find(c => c.language.name === browserLang.substring(0, 2));
      const languageId = localStorage.getItem(site.id + 'languageId')? localStorage.getItem(site.id + 'languageId') :browserResource?.id;
      language = languageId ? await this._publishedSiteServices.getResource(languageId) :
        await this._publishedSiteServices.getResource(site.globalSettings.defaultResource);
      if (resources.length >= 2) {
        lang = language.language.name;
      } else {
        lang = browserLang.substring(0, 2)
      }
      this.setMomentLang(lang);
      if (_.has(availableLanguages, language.name.toLocaleLowerCase())) {
        lang = language.name.toLocaleLowerCase();
      }
      this._translateService.use(lang);
      this.setUserPreferences(siteUser, language);
      return new LoadCurrentLanguageSuccess(language);
    }),

  );

  @Effect()
  updateCurrentLanguage$ = this.actions$.pipe(
    ofType(multilanguageActions.UPDATE_CURRENT_LANGUAGE),
    withLatestFrom(this._store.pipe(select(getSiteUser))),
    withLatestFrom(this._store.pipe(select(getResourcesByName))),
    switchMap(async ([[action, siteUser], resourcesByName]) => {
      const resourceId = (action as UpdateCurrentLanguage).payload;
      const currentResource = await this._publishedSiteService.getResource(resourceId);
      let lang;
      if (siteUser) {
        await this._userService.updateLanguagePreference(siteUser.username, currentResource.name);
        const sitePreferences = { ...siteUser.preferences, defaultLanguage: currentResource.name };
        siteUser.preferences = sitePreferences;
        this._store.dispatch(new UpdateLoading({ loadingUser: false }));
        this._store.dispatch(new LoadUserSuccess(siteUser));
        await this._bizagiDataService.getBizagiUser()
      }
      lang = currentResource.language.name;
      this.setMomentLang(lang);
      if (_.has(resourcesByName, currentResource.name.toLocaleLowerCase())) {
        lang = currentResource.name.toLocaleLowerCase();
      }
      this._translateService.use(lang);
      return new UpdateCurrentLanguageSuccess(currentResource);
    }),

  );

  @Effect()
  updateLastBPMLanguage$ = this.actions$.pipe(
    ofType(multilanguageActions.UPDATE_LAST_BPM_LANGUAGE),
    map(action => {
      this._store.dispatch(new UpdateLoading({ loadingChangeBPMLanguage: false }));
      return new UpdateLastBPMLanguageSuccess((action as UpdateLastBPMLanguage).payload);
    }),

  );

  private setMomentLang(lang){
    switch (lang) {
      case 'zh':
        moment.locale('zh_cn');
        break;

      default:
        moment.locale(lang);
        break;
    }
  }

  private async setUserPreferences(siteUser, language:ILanguageResource){
    if (siteUser && language) {
      siteUser.preferences = await this._cultureSettingsService.getFixedPreferences(siteUser.preferences, language);
      await this._userService.createOrUpdateUserPreferences(siteUser);
    }
  }

  private async setResourcesByName() {
    const response = await this._internalLanguageResources.getAvailableLanguages().toPromise();
    const dictionay = {};
    _.forEach(response, resource => {
      if (!_.has(dictionay, resource.DisplayName.toLocaleLowerCase())) {
        dictionay[resource.DisplayName.toLocaleLowerCase()] = resource;
      }
    });
    return dictionay;
  }
}
