import { BaseControlComponent } from './base.component';
import { ActivatedRoute, Router } from '@angular/router';
import 'rxjs/add/operator/toPromise';
import { PageRenderingService } from 'app/common/services/sites/page-rendering.service';
import { Global } from 'app/common/misc/global';
import { FormType } from 'app/render/bizagi-forms/bizagi-forms.component';
import { TranslateService } from '@ngx-translate/core';
import { DefaultValues } from '../misc/default-values';
import { ECultureDateTypes, EDateFormatStyles } from 'app/common/models/ECultureFormats';
import { Subject, Subscription } from 'rxjs';
import { debounceTime } from 'rxjs/operators';
import { ESearchTypeFields } from 'app/editor/models/ESearchTypeFields';


enum EControlDataSource {
  Fixed,
  Dynamic,
  Context
}

export abstract class BaseDataControlComponent extends BaseControlComponent {

  private _updateDataSubject = new Subject();
  private _updateDataSubscription: Subscription;
  private _data: any[] = [];
  private _fields: { [key: string]: any } = {};
  private _userPreferences: IUserPreferences;
  private _currentUserPreferences: IUserPreferences;
  protected xpathFilter: ISearchXPaths = {} as ISearchXPaths;
  protected actions: Array<IBizagiAction>;
  protected bizagiEntity: string;
  protected bizagiDataServices: IBizagiDataServices;
  protected pageRenderingService: PageRenderingService;
  protected translateService: TranslateService;
  protected route: ActivatedRoute;
  protected router: Router;
  protected maxElementHeight = -1;

  public isDataReady = false;
  public isProcessLoading = false;
  public isDataError = false;
  public canShowCaseLink = false;
  public pagination: IPagination = {
    currentPage: 0,
    currentPaginationSet: 0,
    from: 0,
    intervalSize: 3,
    pages: [0, 1, 2],
    rows: 3,
    totalPages: 3,
    totalItems: 0
  };

  public get data() {
    return this._data;
  }

  public get fields() {
    return this._fields;
  }

  public setServices(route: ActivatedRoute,
    router: Router,
    translateService: TranslateService,
    services: IBizagiDataServices,
    pageRenderingService?: PageRenderingService) {
    this.route = route;
    this.router = router;
    this.translateService = translateService;
    this.bizagiDataServices = services;
    this.pageRenderingService = pageRenderingService;
    this.initializeSubjectUpdateData();
  }

  public initialized() {
    if(!Global.isOnRenderMode()){
      this.initializeSubjectUpdateData();
    }

    // This method is called after all the controls in the page are fully initialized, and the page mappings are applied
    this._updateDataSubject.next();
  }

  private initializeSubjectUpdateData(){
    this._updateDataSubscription?.unsubscribe();
    this._updateDataSubscription = this._updateDataSubject.pipe(debounceTime(100)).subscribe(() => {
      this._loadData();
    });
  }

  public refresh(isLastActionNext?: boolean) {
    if (isLastActionNext) {
      const totalItems = this.pagination.totalItems - 1;
      if (this.pagination.from === totalItems) {
        this.pagination.from = this.pagination.from - this.pagination.rows;
        this.pagination.currentPage--;
      }
      if (this.pagination.from < 0 || this.pagination.currentPage < 0) {
        this.pagination.from = 0;
        this.pagination.currentPage = 0;
      }
    }
    super.refresh();
    this._updateDataSubject.next();
  }

  public setTypeSearch(type: number) {
    this.xpathFilter.typeFilter = type;
  }

  public setXpathFilter(output: string, xpathFilter: string) {
    this.xpathFilter[output] = this.xpathFilter[output] || {};
    this.xpathFilter[output][xpathFilter] = true;
    this.pagination.currentPage = 0;
    this.pagination.from = 0;
  }

  public createCase(idAction, idProcess: string, displayName: string, key: string) {
    if (this.isRenderingMode()) {
      const url = Global.getBizagiProjectUrl(this.stateSnapshotService.getSite(), this.parameters.data.bizagi.projectId);
      this.pageRenderingService.showForm({
        connection: url,
        title: displayName,
        type: FormType.New,
        parameters: {
          action: idAction,
          process: idProcess,
          bizagiEntity: this.bizagiEntity,
          key
        },
        refresh: this.refresh.bind(this)
      });
    }
  }

  public goToLink(item) {
    if (!this.isRenderingMode()) {
      return;
    }
    if (typeof(item._link) === 'string') {
      try {
        item._link = JSON.parse(item._link);
      } catch (error) {
        return;
      }
    }
    if (this.parameters.data.dataSource === EControlDataSource.Fixed && item._link && item._link.type !== 'none') {
      if (item._link.type === 'external') {
        item._link.url = !/^(http:|https:)/i.test(item._link.url) ? '//' + item._link.url : item._link.url;
        window.open(item._link.url, item._link.blank ? '_blank' : '_self');
      } else {
        const site = this.stateSnapshotService.getSite();
        if (item._link.blank) {
          const currentPage = this.stateSnapshotService.getPage();
          window.open(location.href.replace(site.url + '/' + currentPage.name, site.url + '/' + item._link.pageName), item._link.blank ? '_blank' : '_self');
        } else {
          if(item._link.pageName) {
            this.router.navigate([site.url, item._link.pageName]);
          }
        }
      }
    }
  }

  public hasLink(item) {
    const isRender = this.isRenderingMode();
    if (!isRender) {
      return false;
    }
    let link;
    if (typeof(item._link) === 'string') {
      try {
        link = JSON.parse(item._link);
      } catch (err) {
        return false;
      }
    } else {
      link = item._link;
    }
    if (!link) {
      return false;
    }
    const differentNone = link && link.type !== 'none';
    const testUrl = (link.url && !/^\s+$/.test(link.url));

    return isRender &&
      ((differentNone && (testUrl || link.pageName)) ||
        (this.parameters.data && this.parameters.data.bizagi && this.parameters.data.bizagi.navigation &&
          JSON.stringify(this.parameters.data.bizagi.navigation).match('page')));
  }

  public onPaginationEvent(paginator) {
    this.pagination.rows = paginator.pageSize;
    this.pagination.currentPage = paginator.pageIndex;
    this.pagination.from = paginator.pageIndex > 0 ? this.calculateFrom(paginator) : 0;
    this.setPagination(this.pagination);
  }

  public calculateFrom(paginator) {
    const pageIndex = paginator.pageIndex + 1;
    const start = (pageIndex * paginator.pageSize) - (paginator.pageSize - 1);
    return start - 1;
  }

  public setPagination(pagination: IPagination) {
    this.pagination = pagination;
    this._updateDataSubject.next();
  }

  public scrollToElement(element: HTMLElement) {
    element.scrollIntoView({ behavior: 'smooth', block: 'nearest' });
  }

  public async showCase(idCase: string, idTask?: string, processName?: string, caseState = null) {
    if (this.isRenderingMode()) {
      let url: string;
        url = Global.getBizagiProjectUrl(this.stateSnapshotService.getSite(), this.parameters.data.bizagi.projectId);
      const displayName = await this.translateService.get('sitemanager.global.VIEW_CASE_TITLE',
        { case: idCase }).toPromise();
      const parameters: any = {};
     parameters.case = idCase;
      parameters.processId = this.parameters.data.bizagi && this.parameters.data.bizagi.dataId ?
        this.parameters.data.bizagi.dataId : undefined;
      if (idTask) {
        parameters.idTask = idTask;
      }
      this.pageRenderingService.showForm({
        connection: url,
        title: processName || displayName,
        type: FormType.Existing,
        parameters,
        refresh: this.refresh.bind(this)
      });
    }
  }

  public refreshFixedPaginationData() {
    this.pagination.pages = this._refreshFixedPages(this._data['data']);
    this.pagination.totalPages = this.pagination.pages.length;
    const paginatedData = this._data['data'].slice(this.pagination.from, this.pagination.from + this.pagination.rows)
    this._data = {data: paginatedData, dataCount: this._data['dataCount'] } as any ;
    this.updateEllipsisFields();
  }

  public getTypeMappedField(field: string) {
    return this.control.parameters.data.bizagi && this.control.parameters.data.bizagi.mappingTypes
      ? this.control.parameters.data.bizagi.mappingTypes[field] : '';
  }

  protected onChanged(updatedControl: IControl) {
    super.onChanged(updatedControl);
    this._updateDataSubject.next();
  }

  protected navigate(item: any, section: string) {
    if (this.isRenderingMode()) {
      const data = this.parameters.data;
      if (!data.bizagi) {
        return;
      }
      if (!data.bizagi.navigation) {
        return;
      }
      if (!data.bizagi.navigation[section]) {
        return;
      }
      if (!data.bizagi.navigation[section].page) {
        return;
      }

      // Perform routing
      const site = this.stateSnapshotService.getSite();
      const page = this.stateSnapshotService.getPage();
      const pageName = (_.find(this.stateSnapshotService.getPages(),
        { originalId: data.bizagi.navigation[section].page }) as IBasicPage).name;
      const key = data.bizagi.type === 'cases' && item.idCase ? item.idCase : item.id;
      const navigationContextList: Array<INavigationContext> = page.isContextualized ?
        this.stateSnapshotService.getNavigationContext() : [];
      const newNavigationContext: INavigationContext = _.last(data.bizagi.navigation[section].navigationData);
      newNavigationContext.key = key;
      newNavigationContext.originPageId = this.stateSnapshotService.getPage().originalId;
      navigationContextList.push(newNavigationContext);
      this.router.navigate([site.url, pageName], {
        queryParams: {
          key, project: data.bizagi.projectId,
          context: Global.jsonToBase64(navigationContextList)
        }
      });
    }
  }

  protected resize(event: any) {
    if (
      event.target.height > this.maxElementHeight &&
      this.controlDefinition.defaultSize.dynamic &&
      this.controlDefinition.styles.layout &&
      this.styles.layout &&
      this.styles.multipageItemView !== 'Scroll') {
      this.maxElementHeight = event.target.height;
      const columns = this.controlDefinition.styles.layout.layouts[this.styles.layout].columns || 1;
      const rows = Math.ceil(this.pagination.rows / columns);
      const verticalSpacing = (this.controlDefinition.styles.layout.layouts[this.styles.layout].verticalItemSpacing || 3) * rows;
      const paginationsHeight = this.styles.multipageItemView === 'Pagination' ? 40 : 10;
      const itemHeight = $('[data-control-id=' + this.control.id + '] [bs-dynamic-size-item=true]').outerHeight(true);
      const dymanicSizeElement = $('[data-control-id=' + this.control.id + ']');
      const heightElement = (rows * (itemHeight + verticalSpacing)) + paginationsHeight;
      if (dymanicSizeElement && heightElement) {
        this.onResize.next({
          width: dymanicSizeElement.width(),
          height: heightElement
        });
      }
    }
  }

  private async loadBizagiCollectionsData(data, filter: ISearchData) {
    // BIZAGI DATA SOURCE MANAGEMENT
    const xpath = _.keys(this.xpathFilter.filter)[0];
    const xpathProp = _.findKey(data.bizagi.mappings, item => item === xpath);
    const parameters: ICollectionDataParams = {
      collectionId: data.bizagi.dataId,
      value: this._currentLanguageResources[filter.filter] || filter.filter,
      xpath,
      columns: _.compact(_.uniq(_.values(data.bizagi.mappings))) as string[],
      pagination: this.pagination,
      mappingType: data.bizagi.mappingTypes[xpathProp] || (data.bizagi.templateTypes ? data.bizagi.templateTypes[xpath] : '')
    };
    const url = Global.getBizagiProjectUrl(this.stateSnapshotService.getSite(), data.bizagi.projectId);
    const response = await this.bizagiDataServices.getCollection(url, parameters);
    if (response) {
      this.setResponseData(response);
    } else {
      this.isDataError = true;
      this.data['data'] = [];
    }
  }

  private async loadBizagiCasesData(data, filter: ISearchData) {
    // BIZAGI DATA SOURCE MANAGEMENT
    const xpath = _.keys(this.xpathFilter.filter)[0];
    const xpathProp = _.findKey(data.bizagi.mappings, item => item === xpath);
    const parameters: ICaseDataParams = {
      processId: data.bizagi.dataId,
      value: this._currentLanguageResources[filter.filter] || filter.filter,
      xpath,
      columns: _.compact(_.uniq(_.values(data.bizagi.mappings))) as string[],
      pagination: this.pagination,
      mappingType: data.bizagi.mappingTypes[xpathProp] || (data.bizagi.templateTypes ? data.bizagi.templateTypes[xpath] : '')
    };

    const url = Global.getBizagiProjectUrl(this.stateSnapshotService.getSite(), data.bizagi.projectId);
    const response = await this.bizagiDataServices.getCases(url, parameters);
    if (response) {
      this.setResponseData(response);
    } else {
      this.isDataError = true;
      this.data['data'] = [];
    }
  }

  private _loadData() {
    this._fields = this._loadFields(this.control.fields);
    this.maxElementHeight = -1;
    const data = this.parameters.data;
    this._updatePagination();
    let filter;
      filter = Global.isOnRenderMode() && this.parameters['filter'] ? this.parameters['filter'] : { filter: '' };
      filter.filter = filter.filter?.trim().replace(/\*/g, '');
   let state: string = Global.isOnRenderMode() && this.parameters.state ? this.parameters.state : '';
    state = state.trim().replace(/\*/g, '');
    let orderby: string = Global.isOnRenderMode() && this.parameters.orderby ? this.parameters.orderby : '';
    orderby = orderby.trim().replace(/\*/g, '');
    const ordertype: string = Global.isOnRenderMode() && this.parameters.ordertype ? this.parameters.ordertype : '';

    if (data.dataSource === EControlDataSource.Dynamic && data.bizagi && data.bizagi.mappingTypes) {
      this.actions = data.bizagi.actions;
      this.canShowCaseLink = data.bizagi.type === 'cases';
      if (!Global.isOnRenderMode()) {
          // BUILDING MODE DUMMIES
          if (_.isEmpty(this.controlDefinition.fields)) {
            this._data = DefaultValues.loadDefaultData(this.controlDefinition.parameters.data.mappings);
          } else {
            const fieldsMapping = [];
            _.each(this.controlDefinition.fields, (controlDefinitionField: IControlDefinitionField, controlDefinitionFieldKey: string) => {
              fieldsMapping[controlDefinitionFieldKey] = {
                label: controlDefinitionField.displayName, type: controlDefinitionField.type,
                typeData: data.bizagi.mappingTypes[controlDefinitionFieldKey]
              };
            });
            this._data = DefaultValues.loadDefaultData(fieldsMapping, this.pagination?.rows || 3);
          }
        this.isDataReady = true;
        return;
      }

      this.isDataReady = false;
      switch (data.bizagi.type) {
        case 'cases': {
          // DATA FROM BIZAGI CASES
          this.loadBizagiCasesData(data, filter);
          break;
        }
        case 'collections': {
          // DATA FROM BIZAGI COLLECTIONS
          this.loadBizagiCollectionsData(data, filter);
          break;
        }
        case 'searches': {
          // DATA FROM BIZAGI CASES
          this.loadBizagiSearchResultsData(data, filter);
          break;
        }
        case 'context': {
          // DATA FROM CONTEXTUALIZED DATA
          this.loadBizagiContextualizedResultsData(data, filter);
          break;
        }
     }
    } else if (data.fixed) {
      // FIXED DATA MANAGEMENT
      const fixedData = [];
      _.each(data.fixed, (item, i) => {
        _.each(item, (value, key) => {
          fixedData[i] = fixedData[i] ? fixedData[i] : {};
          fixedData[i][key.toString()] = this._currentLanguageResources[value] ? this._currentLanguageResources[value] : value;
        });
      });
      this._data = {data: fixedData, dataCount: fixedData,length} as any;
      if (filter) {
        const filterData =  _.filter(this.data['data'], (item: any) => this.filterFixedItem(item, filter));
        this._data = {data: filterData, dataCount: filterData.length} as any;
      }
      if (this.controlDefinition.parameters.data.type === 'data') {
        this.refreshFixedPaginationData();
      }
      this.isDataReady = true;
    }
  }

  private async loadBizagiSearchResultsData(data, filter: ISearchData) {
    // BIZAGI DATA SOURCE MANAGEMENT
    const xpath = _.keys(this.xpathFilter.filter)[0];
    const xpathProp = _.findKey(data.bizagi.mappings, item => item === xpath);
    const parameters: ISearchDataParams = {
      searchId: data.bizagi.dataId,
      value: this._currentLanguageResources[filter.filter] || filter.filter,
      xpath,
      pagination: this.pagination,
      mappingType: data.bizagi.mappingTypes[xpathProp] || (data.bizagi.templateTypes ? data.bizagi.templateTypes[xpath] : '')
    };

    const url = Global.getBizagiProjectUrl(this.stateSnapshotService.getSite(), data.bizagi.projectId);
    const response = await this.bizagiDataServices.getSearchResults(url, parameters);
    if (response) {
      this.setResponseData(response);
    } else {
      this.isDataError = true;
      this.data['data'] = [];
    }
  }

  private async loadBizagiContextualizedResultsData(data, filter: ISearchData) {

    const navigationContexts = this.stateSnapshotService.getNavigationContext();
    const navigationContext = _.first(navigationContexts) as INavigationContext;
    // Create a navigation context part
    const xpath = _.keys(this.xpathFilter.filter)[0];
    const xpathProp = _.findKey(data.bizagi.mappings, item => item === xpath);
    const parameters = {
      connectionEntityId: navigationContext.connectionId,
      rootConnectionType: navigationContext.connectionType,
      value: this._currentLanguageResources[filter.filter] || filter.filter,
      xpath,
      pagination: this.pagination,
      navigationLevels: [],
      key: navigationContext.key,
      mappingType: data.bizagi.mappingTypes[xpathProp]
    };
    const url = Global.getBizagiProjectUrl(this.stateSnapshotService.getSite(), data.bizagi.projectId);
    const page = this.stateSnapshotService.getPage();

    _.each(navigationContexts, (navigationContextItem: INavigationContext) => {
      if (navigationContextItem.navigationId && parameters.connectionEntityId !== navigationContextItem.navigationId) {
        parameters.navigationLevels.push({
          navigationId: navigationContextItem.navigationId, key: page.context.entity.value
            || navigationContextItem.key
        });
      }
    });
    parameters.navigationLevels.push({ navigationId: data.bizagi.dataId });

    const response = await this.bizagiDataServices.getContextNavigations(url, parameters, this.pagination);
    if (response) {
      this.setResponseData(response);
    } else {
      this.isDataError = true;
      this.data['data'] = [];
    }
  }


  private setResponseData(response: IBizagiDataResult, dataType?: string) {
    this.cultureService.getPreferences().subscribe((userPreferences: IUserPreferences) => {
      this._userPreferences = userPreferences;
      this._currentUserPreferences = this._userPreferences;
      switch (dataType) {
       default:
          this._setResponseData(response);
          break;
      }
    });
  }

  private _setResponseData(response: IBizagiDataResult) {
    const dataCount = response.count;
    this.pagination.totalItems = response.count;
    this.pagination.totalPages = response.count ? _.ceil(response.count / this.pagination.rows) : 0;
    this.pagination.pages = this.pagination.totalPages < this.pagination.intervalSize ?
      Array.from(Array(this.pagination.totalPages).keys()) : Array.from(Array(this.pagination.intervalSize).keys());
    this.bizagiEntity = response.bizagiEntity;
    this._data = this.transformData(response.instances, dataCount);
    this.isDataError = false;
    this.updateEllipsisFields();
  }

  private updateEllipsisFields() {
    setTimeout(() => {
      Global.ellipsisMultiline('.bs-collection-field__text-ellipsis');
    }, 0);
  }

  private filterFixedItem(item, filter: ISearchData) {
    const mappings = this.controlDefinition.fields;
    const result = {};
    _.forIn(filter, (value, filterName) => {
      if (filter[filterName] && this.xpathFilter[filterName]) {
        result[filterName] = filter[filterName] === '';
        for (const xpathFilter in this.xpathFilter[filterName]) {
          if (this.xpathFilter[filterName][xpathFilter]) {
            const mapping = mappings[xpathFilter];
            if ((mapping && mapping.type !== 'image') || this.control.additionalFields[xpathFilter]) {
              const fieldValue = item[xpathFilter];
              let isAMatch = true;
              try {
                if(!_.isNil(fieldValue)) {
                  if (this.xpathFilter.typeFilter === ESearchTypeFields.Specific && filterName === 'category') {
                    isAMatch = _.isEqual(fieldValue.toLowerCase(), filter[filterName].toLowerCase());
                  } else {
                    isAMatch = _.includes(fieldValue.toLowerCase(), filter[filterName].toLowerCase());
                  }
                } else{
                  isAMatch = false;
                }
              } catch (e) {
              }
              if (isAMatch && typeof(isAMatch) === 'boolean') {
                result[filterName] = true;
              }
            }
          }
        }
      }
    });
    return !JSON.stringify(result).match(':false');
  }

  private transformData(data, dataCount): any {
    const mappings = this.parameters.data.bizagi.mappings;
    const mappingTypes = this.parameters.data.bizagi.mappingTypes ? this.parameters.data.bizagi.mappingTypes : {};
    const result = [];
    data.forEach((row: IBizagiDataResultItem) => {
      const item = { id: row.id };
      for (const column in mappings) {
        if (mappings[column]) {
          const match = _.filter(row.parameters, { xpath: mappings[column] });
          if (match && match.length > 0) {
            let value;
            if ((match[0] as any).hasOwnProperty('value')) {
              value = (match[0] as any).value;
            } else {
              value = (match[0] as any).files;
            }
            item[column] = value;
            const formatType = mappingTypes[column];
            const mappingMetadata = this.controlDefinition.fields ? this.controlDefinition.fields :
              (this.controlDefinition.parameters as any).data.mappings;
            switch (formatType.toLowerCase()) {
              case 'image':
                // When the data is an image, add the prefix for image64
                if (mappingMetadata[column].type.toLowerCase && mappingMetadata[column].type.toLowerCase() === 'image'
                  && Global.isBizagiImage(value)) {
                  item[column] = value ? Global.getBizagiImageUrl(value) : null;
                }
                break;
              case 'datetime':
                item[column] = this.cultureService.dateTimeFormat(value, ECultureDateTypes.dateTime, this._currentUserPreferences);
                break;
              case 'money':
                item[column] = this.cultureService.currencyFormat(value, this._currentUserPreferences);
                break;

              case 'real':
                item[column] = this.cultureService.floatFormat(value, this._currentUserPreferences);
                break;

              case 'float':
                item[column] = this.cultureService.floatFormat(value, this._currentUserPreferences);
                break;
            }
          }
        }
      }
      if (row.actions) {
        item['actions'] = row.actions;
      }
      if (this.canShowCaseLink) {
        item['idCase'] = row['idCase'] || row.id;
        item['caseNumber'] = row['caseNumber'];
        item['id'] = row['@processEntityKey'];
      }
      result.push(item);
    });
    this.isDataReady = true;
    return {data: result, dataCount};
  }

  private transformDataMyCases(response): any {
    const data = response.data;
    let headers = response.headers;
    headers = _.filter(headers, (header)=> header.key !== '@ExpirationDate' && header.key !== '@TaskDisplayName');
    const dataCount = response.dataCount;
    const dataFormatted = [];
    _.forEach(data, (row: any) => {
      const keys = _.keys(row);
      const newRow = {};
      _.forEach(keys, (key => {
        const header = _.find(headers, (h) => h.key === key);
        if (header) {
          switch (header.type.toLowerCase()) {
            case 'datetime':
              newRow[key] = this.cultureService.dateTimeFormat(row[key], ECultureDateTypes.dateTime, this._currentUserPreferences);
              break;

            case 'money':
              newRow[key] = this.cultureService.currencyFormat(row[key], this._currentUserPreferences);
              break;

            case 'float':
              if (row[key] && this.cultureService.countDecimals(row[key]) > 2) {
                newRow[key] = parseFloat(row[key]).toFixed(2);
              }
              break;

            case 'image':
              // When the data is an image, add the prefix for image64
              if (Global.isBizagiImage(row[key])) {
                newRow[key] = row[key] ? Global.getBizagiImageUrl(row[key]) : null;
              }
              break;

            default:
              newRow[key] = row[key];
              break;
          }
        } else {
          newRow[key] = row[key];
        }
      }));
      dataFormatted.push(newRow);
    })
    return { data: dataFormatted, headers, dataCount };
  }

  private isText(column, item) {
    if (item === null) {
      return false;
    }
    const mappingMetadata = (this.controlDefinition.parameters as any).data.mappings;
    return mappingMetadata[column].type.toLowerCase() === 'text';
  }

  private _updatePagination() {
    this.pagination.rows = this.styles.displayedItems || 5;
  }

  private _refreshFixedPages(data: any) {
    const pages: any[] = [];
    for (let i = 0; i < Math.ceil(data.length / this.pagination.rows); i++) {
      pages.push(i);
    }
    return pages;
  }

  private _loadFields(fields: { [key: string]: any }): { [key: string]: any } {
    const translationFields: { [key: string]: any } = {};
    _.each(fields, (field: any, key: string) => {
      const tmpField = _.cloneDeep(field);
      if (tmpField.styles.fieldLabel) {
        tmpField.styles.fieldLabel.text = this._currentLanguageResources[field.styles.fieldLabel.text];
      }
      translationFields[key] = tmpField;
    });
    return translationFields;
  }
}
