import { Component, ViewChild } from '@angular/core';
import { BaseControlComponent } from 'app/common/controls/base.component';
import { ControlDefinition } from 'app/common/decorators/control-definition';
import {
  HTML_IFRAME_CONTROLS,
  CATEGORY,
  SUBCATEGORY_HTML_IFRAME,
  PROPERTY_SECTIONS,
} from '../controls-name-definition';

@Component({
    selector: 'bs-iframe-url-control',
    templateUrl: './iframe-url-control.component.html',
    styleUrls: ['./iframe-url-control.component.scss']
})
@ControlDefinition({
    type: HTML_IFRAME_CONTROLS.HtmlIframe.type,
    displayName: HTML_IFRAME_CONTROLS.HtmlIframe.displayName,
    category: CATEGORY.EMBEDS.displayName,
    subcategory: SUBCATEGORY_HTML_IFRAME.displayName,
    icon: HTML_IFRAME_CONTROLS.HtmlIframe.icon,
    styles: {
        showTopBar: {
          section: PROPERTY_SECTIONS.style.key,
            type: 'boolean',
            defaultValue: false
        },
        showUpdateButton: {
          section: PROPERTY_SECTIONS.style.key,
            type: 'boolean',
            defaultValue: false
        },
        border: {
          section: PROPERTY_SECTIONS.border.key,
            type: 'border',
            defaultValue: 'border-none'
        }
    },
    parameters: {
        iframeurl: {
            type: 'url'
        },
        title: {
            type: 'textShort'
        },
    },
    navigation: [],
    defaultSize: {
        width: 600,
        height: 404
    }
})

export class IframeUrlControlComponent extends BaseControlComponent {
    @ViewChild('iframeContent')
    private _iframeContent: any;

    public refresh() {
        if (this._iframeContent) {
            this._iframeContent.nativeElement.src = this._iframeContent.nativeElement.src;
        }
    }
    public encodeURL(url) {
        return encodeURI(url);
    }
}
