import {
  ChangeDetectorRef,
  Component,
  ElementRef,
  EventEmitter,
  Input,
  Output,
  SimpleChanges,
  ViewChild
} from '@angular/core';
import {ControlDefinition} from 'app/common/decorators/control-definition';
import {BaseControlComponent} from 'app/common/controls/base.component';
import {OAuthClientHelper} from 'app/common/services/oauth/oauth-client.helper';
import {CultureSettingsService} from 'app/common/services/sites/culture-settings.service';
import {Global} from 'app/common/misc/global';
import {ENavigationType} from 'app/editor/models/ENavigationType';
import {DefaultValues} from '../../misc/default-values';
import {Subscription} from 'rxjs';
import {select, Store} from '@ngrx/store';
import {getSiteNavigationPages, getCurrentHeader} from '../../../editor/site-editor/store/site/site.selector';
import {getCurrentSiteLanguage} from '../../../editor/site-editor/store/multilanguage/multilanguage.selector';
import {getSitePages} from '../../../editor/site-editor/store/page/page.selector';
import {getSiteLanguage} from '../../../render/store/multilanguage/multilanguage.selector';
import { Router, ActivatedRoute } from '@angular/router';
import { CATEGORY, PROPERTY_SECTIONS } from '../controls-name-definition';

@Component({
  selector: 'bs-header-control',
  templateUrl: './header-control.component.html',
  styleUrls: ['./header-control.component.scss']
})
@ControlDefinition({
  type: 'header',
  displayName: 'HEADER_CONTROL',
  icon: '',
  styles: {
    fontFamily: {
      section: PROPERTY_SECTIONS.fonts.key,
      type: 'font-family',
      defaultValue: 'inherit'
    },
    fontSize: {
      section: PROPERTY_SECTIONS.fonts.key,
      type: 'font-size',
      defaultValue: '14px',
      max: 20
    },
    backColor: {
      type: 'color',
      section: PROPERTY_SECTIONS.background.key,
    },
    fontColor: {
      section: PROPERTY_SECTIONS.fonts.key,
      type: 'color'
    },
    subNavBackColor: {
      section: PROPERTY_SECTIONS.background.key,
      type: 'color'
    },
    subNavFontColor: {
      section: PROPERTY_SECTIONS.fonts.key,
      type: 'color'
    },
    avatarBackColor: {
      section: PROPERTY_SECTIONS.background.key,
      type: 'color'
    },
    avatarFontColor: {
      section: PROPERTY_SECTIONS.fonts.key,
      type: 'color'
    },
    height: {
      section: PROPERTY_SECTIONS.style.key,
      type: 'range',
      defaultValue: 65,
      min: 65,
      max: 200
    },
    logoSize: {
      section: PROPERTY_SECTIONS.logo.key,
      type: 'range',
      defaultValue: 105,
      min: 70,
      max: 200
    },
    bold: {
      section: PROPERTY_SECTIONS.fonts.key,
      type: 'boolean'
    },
    loginColor: {
      section: PROPERTY_SECTIONS.login.key,
      type: 'color'
    },
    loginBold: {
      section: PROPERTY_SECTIONS.login.key,
      type: 'boolean'
    },
    showLogo: {
      section: PROPERTY_SECTIONS.logo.key,
      category: 'hidden',
      type: 'boolean'
    }
  },
  parameters: {
    login: {
      type: 'login',
      defaultValue: {
        login: false,
        navigation: ''
      }
    },
    logout: {
      type: 'logout',
      defaultValue: {
        logout: false,
        fromPrivate: '',
        fromPublic: ''
      }
    },
    siteLogo: {
      type: 'logo',
      defaultValue: 'https://upload.wikimedia.org/wikipedia/en/6/62/Bizagi_BPM_Suite_logo.png'
    }
  },
  navigation: []
})

export class HeaderControlComponent extends BaseControlComponent {
  @Input() control: IHeader;
  @Input() previewMode = false;
  @Input() renderMode = false;
  @Input() stakeholderContexts: Array<string> = [];
  @Input() user: IUserProfile;
  @Input() pages: Array<IBasicPage>;
  @Input() site: ISite;
  @Input() page: IPage;
  @Output() onControlManageContentSelected: EventEmitter<IControl> = new EventEmitter<IControl>();
  @ViewChild('headerNavigationContainer') navigationContainerRef: ElementRef;
  @ViewChild('pageNavigation') pageNavigationRef: ElementRef;
  // In order to notify the page change and then subscribe the changed event
  @Input() pageId: string = null;
  public windowWidth = $(window).width();
  public currentLanguage: ILanguageResource;
  public isDesktop: boolean;
  public menuIsActive: boolean;
  public currentItem = '';
  public email: string;
  public initials: string;
  public defaultPicture = DefaultValues.getDefaultPicture();
  public pageReferences: Array<IPageReference>;
  public showNavigationMenu = false;
  public showAvatarSubMenu = false;

  private navigationContainerWidth: number;
  private pageNavigationWidth: number;
  private navigationContainer: HTMLElement;
  private pageNavigation: HTMLElement;
  private hiddenNavItems: Array<IPageReference>;
  private _navigationPagesSubscription: Subscription;
  private _currentLanguageSubscription: Subscription;
  private _sitePagesSubscription: Subscription;
  private _originalPageREferences: Array<IPageReference>;

 constructor(
    private _cdr: ChangeDetectorRef,
    private _oAuthClient: OAuthClientHelper,
    private _cultureSettingsService: CultureSettingsService,
    private _store: Store<IEditorState>,
    private _storeRender: Store<IRenderState>,
    private _router: Router,
    private route: ActivatedRoute
  ) {
    super();
    this._currentLanguageResources = {};
  }

  ngOnChanges(changes?: SimpleChanges) {
    changes = changes || {};
    if (this.control) {
      // This control is not loaded dinamically, so we need to initialize the control after the input is set
      this.initializeControl();
      this.loadParameters(this.control.parameters, this.control.parametersBinding);
      this.currentItem = this.page.reference || this.page.id;
      if ((this.renderMode || this.previewMode) && ((changes.stakeholderContexts && this.user) ||
        !this.user)) {
        const navigation = _.cloneDeep(this.control.navigation);
        this._loadPageReferences(navigation).then((newPageReferences: Array<IPageReference>) => {
          this._originalPageREferences = newPageReferences;
          this.updateNavigationReferences();
          this.pageReferences = _.cloneDeep(this._originalPageREferences);
          this.hiddenNavItems = [];
          setTimeout(() => {
            this._updateNavigationMenu();
          }, 0);
        });
      }
    }
  }

  async ngOnInit() {

    // Set info from context
    const screen = Global.getScreenSize();
    if (screen === 'desktop' || screen === 'largedesktop') {
      this.isDesktop = true;
    }
    this.currentItem = this.page.reference || this.page.id;
    if (await this._oAuthClient.isLoggedOn()) {
      const username = this._oAuthClient.getUsername();
      this.initials = username.substring(0, 2).toUpperCase();
    }
    if (!this.isOnRenderMode) {
      this._navigationPagesSubscription = this._store
      .pipe(select(getSiteNavigationPages))
      .subscribe((navigations: Array<IPageReference>) => {
        this._originalPageREferences = _.filter(navigations, (pageReference: IPageReferencePage) => pageReference.isOnNavigation);
        this.pageReferences = _.cloneDeep(this._originalPageREferences);
        setTimeout(() => {
          this._updateNavigationMenu();
        }, 0);
      });
      this._currentLanguageSubscription = this._store.pipe(select(getCurrentSiteLanguage)).
      subscribe((languageResource: ILanguageResource) => {
        this.currentLanguage = languageResource;
        this.setCurrentLanguage(this.currentLanguage);
        if (this.currentLanguage.resources[this.control.parameters['siteLogo']] !== this.parameters['siteLogo']) {
          this.loadParameters(this.control.parameters, this.control.parametersBinding);
        }
      });
      this._sitePagesSubscription = this._store.pipe(select(getSitePages))
        .subscribe((sitePages: Array<IBasicPage>) => {
          this.pages = sitePages;
      });

    }
    else {
      this._currentLanguageSubscription = this._storeRender.pipe(select(getSiteLanguage)).
      subscribe((languageResource: ILanguageResource) => {
        this.currentLanguage = languageResource;
        this.setCurrentLanguage(this.currentLanguage);
        this.loadParameters(this.control.parameters, this.control.parametersBinding);
      });
    }


    this.loadParameters(this.control.parameters, this.control.parametersBinding);
    this._updateDefaultValues();
    this._cdr.detectChanges();
  }

  ngOnDestroy() {
    if (this._sitePagesSubscription) { this._sitePagesSubscription.unsubscribe(); }
    if (this._currentLanguageSubscription) { this._currentLanguageSubscription.unsubscribe(); }
    if(this._navigationPagesSubscription) { this._navigationPagesSubscription.unsubscribe(); }
  }

  public logoff() {
    this._oAuthClient.logoutToUrl(this.page.isPrivate);
  }

  public login() {
    // Fix login for render
    const routeSnapshot = this.route.snapshot;
    const redirectRoute = this.control?.parameters?.login?.navigation? this.site.url + '/' + this.control?.parameters?.login?.navigation :
     _.map(routeSnapshot.url, function(part) { return part.path; }).join('/');
    const queryParams = routeSnapshot.queryParams;
    this.goToLogin(this.site.identityServer, redirectRoute, queryParams);
  }

  goToLogin(identityServer: string, redirectRoute: string, queryParams: any) {
    let queryString: any = Global.getQueryString();
    if (Object.keys(queryParams).length > 0) {
        queryString = JSON.parse(JSON.stringify(queryParams));
    }
    queryString.identityServer = identityServer;
    queryString.redirectRoute = redirectRoute;
    this._router.navigate(['login'], {
        queryParams: queryString,
        skipLocationChange: true
    });
}

  public showMenu() {
    this.menuIsActive = !this.menuIsActive;
    if (this.menuIsActive) {
      $('.bs-page-body').addClass('bs-page-body--active-nav');
    } else {
      $('.bs-page-body').removeClass('bs-page-body--active-nav');
    }
  }

  public showNavigation(control: IControl) {
    this.onControlManageContentSelected.emit(control);
  }

  public getLink(pageNavigation: IPageReference): string {
    if (pageNavigation.type === ENavigationType[ENavigationType.Page]) {
      const newRef = location.href.substring(0, location.href.lastIndexOf('/' + this.page.name));
      const finalRef = location.href.substring(location.href.lastIndexOf('/' + this.page.name) + this.page.name.length + 1)
      return (newRef + '/' + (pageNavigation as IPageReferencePage).pageName + finalRef).split('?')[0];
    } else {
      let link = (pageNavigation as IPageReferenceLink).link;
      link = /^http(s)?\:\/\//gm.test(link) ? link : 'http://' + link;
      return link;
    }
  }


  public getInt(text: string): number {
    return parseInt(text, 0);
  }

  public openUserPreferences() {
    this._cultureSettingsService.openUserPreferences();
  }

  public toggleNavMenu() {
    this.showNavigationMenu = !this.showNavigationMenu;
  }

  public hideItem(pageId: string): boolean {
    const pageReference = _.find(this.hiddenNavItems, (page: IPageReferencePage) => {
      return page.pageId === pageId;
    });
    return pageReference ? true : false;
  }

  public showSubMenu(child, show, navigation, hiddenItems = false) {
    if (show) {
      $('ul .bs-header-site__nav__opt:nth-child(' + Number(child + 1) + ') ul').addClass('bs-header-site__nav__sub-nav--visible');
      navigation['showSubMenu'] = true;
      const otherReferences = hiddenItems? _.filter(this.hiddenNavItems, item => item.id !== navigation.id) :
       _.filter(this.pageReferences, item => item.id !== navigation.id);
      otherReferences.forEach(element => {
        element['showSubMenu'] = false;
      });
      for (let index = 0; index < this.pageReferences.length; index++) {
        if (index !== child ) {
          $('ul .bs-header-site__nav__opt:nth-child(' + Number(index + 1) + ') ul').removeClass('bs-header-site__nav__sub-nav--visible');
        }
      }
    } else {
      navigation['showSubMenu'] = false;
      $('ul .bs-header-site__nav__opt:nth-child(' + Number(child + 1) + ') ul').removeClass('bs-header-site__nav__sub-nav--visible');
    }
  }

  public changeSubMenuVisibility(event) {
    if(this.windowWidth < 769 && event !== 'mouseover') {
      this.showAvatarSubMenu = !this.showAvatarSubMenu;
    }
  }

  public showSubAvatarMenu() {
    if ( $(window).width() < 769) {
      return this.showAvatarSubMenu;
    }
    return true;
  }

  public navigate(pageNavigation) {
    if (pageNavigation?.type === ENavigationType[ENavigationType.Page]) {
      const newRef = location.href.substring(0, location.href.lastIndexOf('/' + this.page.name));
      const finalRef = location.href.substring(location.href.lastIndexOf('/' + this.page.name) + this.page.name.length + 1);
      const link = (newRef + '/' + (pageNavigation as IPageReferencePage).pageName + finalRef).split('?')[0];
      if (pageNavigation.blank) {
        window.open(link, '_blank');
      } else {
        this._router.navigate([this.site.url, pageNavigation.pageName]);
      }
    } else {
      let link = (pageNavigation as IPageReferenceLink).link;
      link = /^http(s)?\:\/\//gm.test(link) ? link : 'http://' + link;
      const blank = pageNavigation?.blank? '_blank' : '';
      window.open(link, blank)
    }
  }

  private _updateNavigationMenu() {
    const windowWidth = $(window).width();
    if (windowWidth < 769) {
      return;
    }
    if (this.navigationContainerRef && (!this.hiddenNavItems?.length || !this.renderMode)) {

      this.navigationContainer = this.navigationContainerRef.nativeElement;
      this.pageNavigation = this.pageNavigationRef.nativeElement;
      this.navigationContainerWidth = this.navigationContainer.offsetWidth;
      this.hiddenNavItems = [];
      const navElements = document.getElementsByClassName('bs-header-site__nav__opt');
      _.each(navElements, (el: Element) => {
        el.removeAttribute('style');
      });
      const pagesToFilter = this.isOnRenderMode ? _.cloneDeep(this._originalPageREferences) : this.control.navigation;
      this._fixNavigation(navElements, 1, pagesToFilter);
    }
  }

  private _fixNavigation(navElements: HTMLCollectionOf<Element>, posDiff = 1, pagesToFilter) {
    if (this.navigationContainer && this.pageNavigation) {
      this.pageNavigationWidth = this.pageNavigation.offsetWidth;
      if (this.pageNavigationWidth > (this.navigationContainerWidth - 190)) {
        const navigationItems = _.filter(pagesToFilter, item => item.isOnNavigation)
        const elementToHide = navElements.item((navigationItems.length - posDiff)) as HTMLElement;
        if (elementToHide) {
          elementToHide.setAttribute('style', 'display:none');
          const pageReference = navigationItems[(navigationItems.length - posDiff)];
          const index = this.hiddenNavItems.indexOf(pageReference);
          if (index < 0) {
            this.hiddenNavItems.push(pageReference);
            const page = _.find(this.pageReferences, item =>  item.id === pageReference.id);
            if (page) {
              this.pageReferences.splice(this.pageReferences.indexOf(page), 1);
            }
          }
          posDiff = posDiff + 1;
          this._fixNavigation(navElements, posDiff, pagesToFilter);
        }
      }
    }
  }

  private async _loadPageReferences(navigation: Array<IPageReference>): Promise<Array<IPageReference>> {
    const newPageReferences = [];
    return new Promise<Array<IPageReference>>((resolve, reject) => {
      _.forEach(navigation, (pageReference: IPageReferencePage) => {
        if (!pageReference.pageId) {
          if (pageReference.isOnNavigation) {
            newPageReferences.push(pageReference);
          }
        } else {
          const targetPage = _.find(this.pages, (page: IBasicPage) => page.originalId === pageReference.pageId);
          if (targetPage) {
            if (pageReference.isOnNavigation === null || pageReference.isOnNavigation === undefined) {
              pageReference.isOnNavigation = !targetPage.isContextualized;
            }
            if (targetPage && (!targetPage.isContextualized && pageReference.isOnNavigation)) {
              newPageReferences.push(pageReference);
            }
          }
        }
      });
      resolve(newPageReferences);
    });
  }

  private updateNavigationReferences() {
    const controlNav = _.cloneDeep(this.control.navigation);
    this._originalPageREferences = _.filter(controlNav, (pageReference: IPageReferencePage) => {
      const targetPage = _.find(this.pages, (page: IBasicPage) => page.originalId === pageReference.pageId);
      const isContextualized = targetPage ? targetPage.isContextualized : false;
      if (this.stakeholderContexts?.length) {
        return  pageReference.isOnNavigation &&
        ((Global.isVisibleTo(pageReference.visibleTo, this.stakeholderContexts)
        && !isContextualized) || (!targetPage?.isPrivate));
      } else {
        return !targetPage?.isPrivate && !isContextualized && pageReference.isOnNavigation;
      }
    });

    _.each(this._originalPageREferences, (navigation: IPageReferencePage) => {
      navigation.references = _.filter(navigation.references, (pageReference: IPageReferencePage) => {
        const targetPage = _.find(this.pages, (page: IBasicPage) => page.originalId === pageReference.pageId);
        if (this.stakeholderContexts?.length) {
          return pageReference.isOnNavigation
          && (Global.isVisibleTo(pageReference.visibleTo, this.stakeholderContexts) || !targetPage.isPrivate);
        } else {
          return !targetPage?.isPrivate && pageReference.isOnNavigation;
        }
      });
    });

  }

  private _updateDefaultValues() {
    for (const style in this.controlDefinition.styles) {
      if (this.controlDefinition.styles.hasOwnProperty(style)) {
        if (!this.styles[style]) {
          this.control.styles[style] = this.controlDefinition.styles[style].defaultValue || null;
        }
      }
    }
  }

  public isSubMenuSelected(pageNavigation: IPageReferencePage) {
    return pageNavigation.references.some((reference: any) => {
      return reference.pageId === this.currentItem;
    })
  }

}
