import * as PageContextActions from './page-context.actions';

export function pageContextReducer(state = null, action: PageContextActions.UpdatePageContext) {
  switch (action.type) {
    case PageContextActions.UPDATE_PAGE_CONTEXT: {
      const pageContext = _.cloneDeep(action.payload);
      return {
        ...state,
        ...pageContext,
      };
    }
    default:
      return state;
  }
}
