import {Injectable} from '@angular/core';

declare var _: any;

@Injectable()
export class ControlsService {

  static controlComponents: Array<any> = [];

  static registerControlComponent(controlComponent: any) {
    this.controlComponents.push(controlComponent);
  }

  static getControlComponents(): Array<any> {
    return this.controlComponents;
  }

  public getControlDefinitions(): Array<IControlDefinition> {
    return _.filter(_.map(ControlsService.controlComponents, (controlComponent: any) => {
      return controlComponent.controlDefinition;
    }), (control: IControl) => {
      return control.type !== 'header' && control.type !== 'footer' && control.type !== 'hero';
    });
  }

  public getControlDefinitionByType(type: string): IControlDefinition {
    const controlComponent = _.find(ControlsService.controlComponents, (controlComponentItem: any) => {
      return controlComponentItem.controlDefinition.type === type;
    });
    if (controlComponent) {
      return controlComponent.controlDefinition;
    }
    return null;
  }


  public getControlComponents(): Array<IControlDefinition> {
    return ControlsService.controlComponents;
  }

  public getControlComponentByType(type: string): any {
    const controlComponent = _.find(ControlsService.controlComponents, (controlComponentItem: any) => {
      return controlComponentItem.controlDefinition.type === type;
    });
    if (controlComponent) {
      return controlComponent;
    }
    return null;
  }

  public getCollectionFieldsSelectors() {
    return  {
      listView: {
        image: '.bs-list-view__img',
        textTitle: '.bs-list-view__content__head__title > .bs-collection-field__container > div',
        labelTitle: '.bs-list-view__content__head__title > .bs-collection-field__container > label',
        text: '.bs-list-view__content__body__content',
        caption: '.bs-list-view__content__body__caption'
      },
      galleryLink: {
        image: '.bs-gallery-link__item__link__img',
        title: '.bs-gallery-link__item__text'

      },
      imageCarousel: {
        image: '.bs-carousel-item__image',
        title: '.bs-carousel-item__title'
      },
      cardLink: {
        image: '.bs-card-link__item__img',
        title: '.bs-card-link__item__content__title',
        text: '.bs-card-link__item__content__subtitle'
      },
      dynamic: {
        image: '.bs-image-section',
        header_footer: '.bs-header-footer-section',
        title: '.bs-title-section',
        subtitle: '.bs-subtitle-section',
        body_1: '.bs-bodytext-section.bs-bodytext1-section',
        body_2: '.bs-bodytext-section.bs-bodytext2-section',
        body_3: '.bs-bodytext-section.bs-bodytext3-section',
        heading_1: '.bs-heading1-section',
        heading_2: '.bs-heading2-section',
        caption: '.bs-caption-section'
      }
    }
  }


}
