import {NgModule} from '@angular/core';
import {RenderComponent} from './render.component';
import {BizagiDataService} from 'app/render/services/bizagi-data.service';
import {PublishedSiteService} from 'app/render/services/published-site.service';
import {PageRenderComponent} from './page-render/page-render.component';
import {GridRenderComponent} from './page-render/grid-render/grid-render.component';
import {GridCellRenderComponent} from 'app/render/page-render/grid-render/grid-cell-render.component';
import {RenderRoutingModule} from 'app/render/render-routing.module';
import {BizagiFormsComponent} from './bizagi-forms/bizagi-forms.component';
import {SiteRenderComponent} from 'app/render/site-render.component';
import {OAuthClientHelper} from 'app/common/services/oauth/oauth-client.helper';
import {HashLocationStrategy, LocationStrategy} from '@angular/common';
import {SiteListRenderComponent} from './site-list-render/site-list-render.component';
import {HTTP_INTERCEPTORS, HttpClientModule} from '@angular/common/http';
import {CultureSettingsService} from '../common/services/sites/culture-settings.service';
import {NgProgressHttpModule} from '@ngx-progressbar/http';
import {NgProgressModule} from '@ngx-progressbar/core';
import {UserPreferencesRenderComponent} from './user-preferences-render/user-preferences-render.component';
import {UserPreferencesRenderModalComponent} from './user-preferences-render/user-preferences-render-modal/user-preferences-render-modal.component'
import {StoreModule, Store} from '@ngrx/store';
import {StoreDevtoolsModule} from '@ngrx/store-devtools';
import {BreadcrumbRenderComponent} from './breadcrumb-render/breadcrumb-render.component';
import {LanguagesMenuRenderComponent} from './languages-menu-render/languages-menu-render.component';
import {INITIAL_STATE_STORE, ROOT_REDUCER} from './store/reducer';
import {RestApiRenderInterceptorService} from './services/rest-api-render-interceptor.service';
import {BSRenderCommonModule} from '../common/render.common.module';
import {FontsServiceCommon} from 'app/common/services/sites/fonts.service';
import {TranslationService} from 'app/common/services/sites/translation.service';
import {EffectsModule} from '@ngrx/effects';
import {SiteEffects} from './store/site/site.effect';
import {PageEffects} from './store/page/page.effect';
import {PagesEffects} from './store/pages/pages.effect';
import {StateSnapshotService} from '../common/services/sites/state-snapshots.service';
import { BzgLoadingModule, BzgTabsModule, PaginatorLabelsService } from 'bz-genome-angular';
import {UserEffects} from './store/user/user.effect';
import {MultilanguageEffect} from './store/multilanguage/multilanguage.effect';
import { TranslateService } from '@ngx-translate/core';
import { CustomPaginatorLabels } from 'app/common/services/genome/custom-paginator-labels';
import {SessionEffects} from './store/session/session.effect';
import { SessionHandlerService } from 'app/common/services/oauth/session-handler.service';
import {InternalResourcesService} from'../common/services/sites/internal-resources.service';
import { environment } from 'environments/environment';
@NgModule({
  imports: [
    BSRenderCommonModule,
    RenderRoutingModule,
    HttpClientModule,
    BzgTabsModule,
    BzgLoadingModule,
    EffectsModule.forRoot([SiteEffects, PageEffects, PagesEffects, UserEffects, MultilanguageEffect, SessionEffects]),
    StoreModule.forRoot(ROOT_REDUCER, {
      initialState: INITIAL_STATE_STORE,
    }),
    !environment.production ? StoreDevtoolsModule.instrument({
      maxAge: 5
    }) : [],
    NgProgressModule,
    NgProgressHttpModule
  ],
  declarations: [
    RenderComponent,
    PageRenderComponent,
    GridRenderComponent,
    GridCellRenderComponent,
    SiteRenderComponent,
    BizagiFormsComponent,
    SiteListRenderComponent,
    UserPreferencesRenderComponent,
    UserPreferencesRenderModalComponent,
    BreadcrumbRenderComponent,
    LanguagesMenuRenderComponent,
  ],
  providers: [
    BizagiDataService,
    StateSnapshotService,
    PublishedSiteService,
    CultureSettingsService,
    InternalResourcesService,
    OAuthClientHelper,
    { provide: LocationStrategy, useClass: HashLocationStrategy },
    { provide: HTTP_INTERCEPTORS, useClass: RestApiRenderInterceptorService, multi: true },
    FontsServiceCommon,
    TranslationService,
    {
      provide: PaginatorLabelsService, useFactory: (translateService: TranslateService, store: Store<IRenderState>) => {
        return new CustomPaginatorLabels(store, translateService)
      }, deps: [TranslateService, Store]
    },
    SessionHandlerService
  ],
  bootstrap: [RenderComponent],
})
export class RenderModule {
  constructor() {}
}
