import { Component, ViewChild, HostBinding, SimpleChanges, ElementRef } from '@angular/core';
import { ControlDefinition } from 'app/common/decorators/control-definition';
import { CATEGORY, PROPERTY_SECTIONS, SUBCATEGORY_MY_CASES } from './../controls-name-definition';
import { BizagiDataService } from '../../../render/services/bizagi-data.service';
import { Global } from 'app/common/misc/global';
import { BzgOptionComponent } from 'bz-genome-angular/core/option/option.component';
import { Subject, Subscription } from 'rxjs';
import { debounceTime } from 'rxjs/operators';
import { BzgPaginatorComponent } from 'bz-genome-angular';
import { BaseInboxControlComponent, EDataType } from '../base-inbox.component';
import { BzgAutocompleteComponent } from 'bz-genome-angular/autocomplete/autocomplete/autocomplete.component';
import { select } from '@ngrx/store';
import { getSiteLanguage } from 'app/render/store/multilanguage/multilanguage.selector';
import { TranslateService } from '@ngx-translate/core';
import { BzgSearchComponent } from 'bz-genome-angular/search/search/search.component';

enum ViewTypeEnum {
  card,
  table
};
enum CaseStateEnum {
  Completed,
  Running,
  Aborted,
  All
};

@Component({
  selector: 'bs-my-cases-control',
  templateUrl: './my-cases-control.component.html',
  styleUrls: ['./my-cases-control.component.scss']
})
@ControlDefinition({
  type: 'my-cases',
  displayName: 'MY_CASES_CONTROL',
  category: CATEGORY.CASES.displayName,
  subcategory: SUBCATEGORY_MY_CASES.displayName,
  icon: SUBCATEGORY_MY_CASES.controls.Inbox.icon,
  styles: {
    cardDisplay: {
      type: 'card',
      defaultValue: 10,
      category: 'hidden',
      min: 1,
      max: 100,
      step: 1,
      isFilter: false
    },
    tableDisplay: {
      type: 'table',
      defaultValue: 10,
      category: 'hidden',
      min: 1,
      max: 100,
      step: 1,
      isFilter: false
    },
    layout: {
      type: 'layout',
      defaultValue: 'four',
      category: 'hidden',
      layouts: {
        one: {
          icon: 'one-column-thin',
          text: 'ONE_COLUMN',
          columns: 1
        },
        two: {
          icon: 'two-columns-thin',
          text: 'TWO_COLUMNS',
          min: 40,
          columns: 2
        },
        three: {
          icon: 'three-columns-thin',
          text: 'THREE_COLUMNS',
          min: 75,
          columns: 3
        },
        four: {
          icon: 'four-columns-thin',
          text: 'FOUR_COLUMNS',
          min: 90,
          columns: 4
        }
      }
    },
    statusColor: {
      section: PROPERTY_SECTIONS.style.key,
      type: 'boolean',
      defaultValue: true
    },
    backColor: {
      section: PROPERTY_SECTIONS.background.key,
      type: 'color',
    },
    filtersFontSize: {
      section: PROPERTY_SECTIONS.fonts.key,
      type: 'font-size',
      defaultValue: '12px',
      max: 32
    },
    filtersFontColor: {
      section: PROPERTY_SECTIONS.fonts.key,
      type: 'color',
      defaultValue: '#272727'
    },
    filtersFontFamily: {
      section: PROPERTY_SECTIONS.fonts.key,
      type: 'font-family',
      defaultValue: 'inherit',
    },
    headerFontSize: {
      section: PROPERTY_SECTIONS.fonts.key,
      type: 'font-size',
      defaultValue: '12px',
      max: 36
    },
    headerFontColor: {
      section: PROPERTY_SECTIONS.fonts.key,
      type: 'color',
      defaultValue: '#272727'
    },
    headerFontFamily: {
      section: PROPERTY_SECTIONS.fonts.key,
      type: 'font-family',
      defaultValue: 'inherit',
    },
    contentFontSize: {
      section: PROPERTY_SECTIONS.fonts.key,
      type: 'font-size',
      defaultValue: '12px',
      max: 36
    },
    contentFontColor: {
      section: PROPERTY_SECTIONS.fonts.key,
      type: 'color',
      defaultValue: '#272727'
    },
    contentFontFamily: {
      section: PROPERTY_SECTIONS.fonts.key,
      type: 'font-family',
      defaultValue: 'inherit',
    },
    paginationFontSize: {
      section: PROPERTY_SECTIONS.fonts.key,
      type: 'font-size',
      defaultValue: '12px',
      max: 24
    },
    paginationFontColor: {
      section: PROPERTY_SECTIONS.fonts.key,
      type: 'color',
      defaultValue: '#272727'
    },
    paginationFontFamily: {
      section: PROPERTY_SECTIONS.fonts.key,
      type: 'font-family',
      defaultValue: 'inherit',
    }
  },
  parameters: {
    data: {
      type: 'data',
      size: 3,
      defaultValue: {
      }
    }
  },
  defaultSize: {
    width: 1100,
    height: 995,
    minWidth: 326,
    minHeight: 400,
    maxHeight: 1200
  },
  navigation: [],
  hiddenOptions: {
    dataManager: false
  }
})

export class MyCasesControlComponent extends BaseInboxControlComponent {
  public processes;
  public ViewTypeEnum = ViewTypeEnum;
  public initialProcesses;
  public select: JQuery<HTMLElement>;
  public view: ViewTypeEnum = ViewTypeEnum.table;
  public caseNumberFilter: string;
  private _searchByCaseNumberDebounce = new Subject();
  private _updateDebounceSubscription: Subscription;
  private _languageSubscription: Subscription;
  private noOrderColumns = [
    '@CurrentUserFullName',
    '@CurrentUserLocation',
    '@CurrentUserUsername',
    '@IdCase',
    '@GuidProcess',
    '@ProcessType',
    '@IdWorkFlow',
    '@HasSummaryForm',
    '@IdWorkItem',
    '@IdTaskType',
    '@Category',
    '@CaseState'
  ]
  public isRenderMode = Global.isOnRenderMode();
  public cellSpacingMode = 'normal';
  public stylesComponent;
  public caseStates = CaseStateEnum;
  public pageSize = 10;
  public selectedOrderColumn: any;
  public filterStyles = {};
  public isTooltipDisabled = false;
  @ViewChild('myCasesPaginator') myCasesPaginator: BzgPaginatorComponent;
  @ViewChild('search') searchInput: BzgSearchComponent;
  public searchCaseField;
  public previousValueSearch = '';
  @HostBinding('style.pointer-events') pointerEvent = Global.isOnRenderMode() ? 'all' : 'none';

  @ViewChild('autocomplete') autocomplete: BzgAutocompleteComponent;

  constructor(private _bizagiDataService: BizagiDataService,
    private _element: ElementRef,
    private _translateService: TranslateService) {
    super();
    this.dataType = EDataType.MyCases;
    this.unsupportedColumns = ['@ExpirationDate', '@TaskDisplayName', '@CurrentUserFullName',
      '@CurrentUserLocation', '@CurrentUserUsername', '@GraphicQuery'];
  }

  async ngOnInit() {
    this.isDataReady = false;
    this.view = ViewTypeEnum.table;

    this.filterStyles = {'font-size': this.styles['filtersFontSize'],
    'font-family': this.styles['filtersFontFamily'],
    'color': this.styles['filtersFontColor']};
    this.stylesComponent = this.styles;
    this.pagination.rows = this.pageSize;
    this.setPagination(this.pagination);

    this.parameters['orderby'] = '@CreationDate';
    this.parameters['ordertype'] = 'desc';
    this._updateDebounceSubscription = this._searchByCaseNumberDebounce.pipe(debounceTime(300)).subscribe((caseNumber: string) => {
      this.parameters.filter = caseNumber;
      this.pagination.from = 0;
      this.setPagination(this.pagination);
    });
    if (Global.isOnRenderMode()) {
      this._languageSubscription = this.baseStore.pipe(select(getSiteLanguage)).subscribe(() => {
        setTimeout(async () => {
          this.calculateSearchTooltip();
          this.autocomplete.panelOpen = false;
          this.autocomplete.closePanel();
        }, 200);
      })
    }
  }

  async ngAfterViewInit() {
    if (Global.isOnRenderMode()) {

      setTimeout(async () => {
        this.autocomplete.panelOpen = false;
        this.autocomplete.closePanel();
        this.processes = await this._bizagiDataService.getProcesses().toPromise();
        const processes: Array<IBizagiProcess> = this.control.parameters['data'].processes;
        if (!!processes) {
          const processesIds: string[] = processes.map(p => p.id);
          this.processes = _.filter(this.processes, (p) => _.includes(processesIds, p.id));
        }
        this.initialProcesses = _.cloneDeep(this.processes);
        $(this._element.nativeElement)
          .find('bzg-paginator .bz-dropdown__select')
          .css('padding-right', 'calc(1.6rem + ' + this.stylesComponent['paginationFontSize'] + ')');
        Global.modifyWidhSelect(
          '.bz-autocomplete-searchbox__input',
          'keydown',
          '.cdk-overlay-container .bz-autocomplete-panel', 200);
        this.select = Global.modifyWidhSelect(
          '.bz-autocomplete-searchbox__input',
          'focus',
          '.cdk-overlay-container .bz-autocomplete-panel', 0);
        this.autocomplete.panelOpen = false;
        this.autocomplete.closePanel();
      }, 200);
    }
  }

  ngOnDestroy() {
    this._updateDebounceSubscription?.unsubscribe();
    this._languageSubscription?.unsubscribe();
    this.select?.unbind('keydown focus');
  }

  private async calculateSearchTooltip() {
    const placeholderSearch = await this._translateService.get('controls.my-cases.SEACH_BY_CASE').toPromise();
    const textWidth = Global.getHtmlTextWidth(placeholderSearch,
      this.stylesComponent['filtersFontSize'],
      this.stylesComponent['filtersFontFamily']);
    this.isTooltipDisabled = textWidth < $(this._element.nativeElement).find('bzg-search .bz-search__input').width();
  }

  public changeView(view: ViewTypeEnum) {
    this.view = view;
  }

  public filterProcesses(value) {
    this.myCasesPaginator?.firstPage();
    this.parameters['orderby'] = this.selectedOrderColumn ? this.selectedOrderColumn.id : '@CreationDate';
    this.parameters['ordertype'] = this.selectedOrderColumn ? this.selectedOrderColumn.direction : 'desc';
    if (!value) {
      this.processes = _.cloneDeep(this.initialProcesses);
      this.parameters.processId = '';
      this.refresh();
    } else {
      this.processes = _.filter(this.initialProcesses, (process) => {
        return process.displayName.toLowerCase().indexOf(value.toLowerCase()) !== -1;
      });
    }
  }

  public processSelected(selectedOption: BzgOptionComponent) {
    this.parameters.filter = '';
    this.parameters.processId = selectedOption.value.id;
    this.parameters['orderby'] = this.selectedOrderColumn ? this.selectedOrderColumn.id : '@CreationDate';
    this.parameters['ordertype'] = this.selectedOrderColumn ? this.selectedOrderColumn.direction : 'desc';
    this.pagination.from = 0;
    this.setPagination(this.pagination);
  }

  public filterByCaseNumber(caseNumber: string) {
    if (this.containInvalidCharacter(caseNumber)) {
      this.searchInput.queryField.setValue(this.previousValueSearch);
    } else if (this.previousValueSearch !== caseNumber) {
      if (!caseNumber.length) {
        this.calculateSearchTooltip();
      } else {
        this.isTooltipDisabled = true;
      }
      this.previousValueSearch = caseNumber;
      this._searchByCaseNumberDebounce.next(caseNumber);
    }

  }

  private containInvalidCharacter(text: string) {
    return _.some(['"','\'','#','&','=','\\'], (value) => {
      return text.indexOf(value) > -1;
    })
  }

  public displayFn(process?: any): string | undefined {
    return process ? process.displayName : undefined;
  }

  public async showCaseEvent(process: any) {
    const site = this.stateSnapshotService.getSite();
    const url = (_.first(_.values(site.environments[site.defaultEnvironment].projects)) as any).url;
    const data = await this.bizagiDataServices.getDataByCaseId(url, process.id)
    this.showCase(process.id, null, process['@ProcessDisplayName'], process['@CaseState'],
      process['@CaseNumber'], !!data?.instances[0]?.id);
  }

  public async changeOrderColumnGrid(event) {
    if (!_.includes(this.noOrderColumns, event.id)) {
      this.selectedOrderColumn = event;
      this.parameters['orderby'] = event.id;
      this.parameters['ordertype'] = event.direction;
    }
    this.refresh();
  }

  public onPaginationEvent(paginator) {
    this.pagination.rows = paginator.pageSize;
    this.pagination.from = paginator.pageIndex > 0 ? this.calculateFrom(paginator) : 0;
    this.pagination.currentPage = paginator.pageIndex;
    this.setPagination(this.pagination);
  }

  public filterByCaseState(state: CaseStateEnum) {
    if (state === CaseStateEnum.All) {
      this.parameters['state'] = '';
    } else {
      this.parameters['state'] = CaseStateEnum[state];
    }
    this.pagination.from = 0;
    this.pagination.currentPage = 0;
    this.refresh();
  }

}
