import { createSelector } from '@ngrx/store';

const siteEditorSelector = (state: IEditorState) => state.siteEditor;

export const getCurrentSiteLanguage = createSelector(
  siteEditorSelector,
  (state: ISiteEditorState) =>
    state.currentSiteLanguage
);

export const getResourcesToSync = createSelector(
  siteEditorSelector,
  (state: ISiteEditorState) =>
    state.languageResourceToSync
);

export const getAvailableLanguages = createSelector(
  siteEditorSelector,
  (state: ISiteEditorState) =>
    state.availableLanguages
);

