import { Component, EventEmitter, Input, Output, ViewChild } from '@angular/core';
import { ModalWebPartsComponent } from 'app/common/components/modal-webparts/modal-webparts.component';
import { CultureSettingsService } from 'app/common/services/sites/culture-settings.service';
import { UserPreferencesComponent } from 'app/common/components/user-preferences/user-preferences.component';
import { UserService } from 'app/common/services/sites/user.service';
import { PublishedSiteService } from 'app/render/services/published-site.service';
import { BzgModalRef, BzgModalService } from 'bz-genome-angular';
import { UserPreferencesRenderModalComponent } from './user-preferences-render-modal/user-preferences-render-modal.component';
import { select, Store } from '@ngrx/store';
import { getSiteLanguage, getSiteResources } from '../store/multilanguage/multilanguage.selector';
import { Subscription } from 'rxjs';
import { getSiteUser } from '../store/user/user.selector';
import { LoadUserSuccess, UpdateUser } from '../store/user/user.actions';

@Component({
  selector: 'bs-user-preferences-render',
  templateUrl: './user-preferences-render.component.html',
  styleUrls: ['./user-preferences-render.component.scss'],
})
export class UserPreferencesRenderComponent {
  public buttons = [];
  public currentUserPreferences: IUserPreferences;
  public formatOptions: ICultureFormatOptions;
  private modalRef: BzgModalRef;

  @Input() currentLanguage: IBasicLanguageResource;
  public user: IUserSiteProfile;

  private _userSubscription: Subscription;
  private _resourcesSubscription: Subscription;
  private _resources: ISiteResource[];
  @Output() onUserUpdate = new EventEmitter();

  constructor(
    private _cultureSettingsService: CultureSettingsService,
    private _userService: UserService,
    private _store: Store<IRenderState>,
    private _modalService: BzgModalService
  ) { }

  ngOnInit() {
    this._initialize();
    this._userSubscription = this._store.pipe(select(getSiteUser)).subscribe((siteUser: IUserSiteProfile) => {
      if (siteUser) {
        this.user = siteUser;
        this.currentUserPreferences = _.cloneDeep(this.user.preferences);
      }
    });
    this._resourcesSubscription = this._store.pipe(select(getSiteResources)).subscribe((resources) => {
      this._resources = resources;
    })

  }

  ngOnDestroy() {
    this._cultureSettingsService.userPreferencesStatus.unsubscribe();
    this._userSubscription.unsubscribe();
  }

  private async _initialize() {
    const maxWidth = $(window).width() > 769 ? '750px' : '100%';
    const maxHeight = $(window).width() > 769 ? '680px' : '100%';
    this._cultureSettingsService.userPreferencesStatus.subscribe((state: any) => {
      if (state.isOpen) {
        this.modalRef = this._modalService.open(UserPreferencesRenderModalComponent, {
          backdropClass: 'cdk-overlay-dark-backdrop-custom',
          closeOnBackdropClick: false,
          maxWidth,
          maxHeight,
          data: {
            currentLanguage: this.currentLanguage,
            currentUserPreferences: this.currentUserPreferences,
            resources: this._resources
          }
        });
        this.modalRef.afterClosed().subscribe(async result => {
          if (result && result.type === 'save') {
            this.user.preferences = result.userPreferences;
            this._store.dispatch(new UpdateUser(this.user));
          }
        });
      }
    });
  }
}
