import { first } from 'rxjs/operators';
import { ChangeDetectorRef, Component, ViewChild } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { Global } from 'app/common/misc/global';
import { PageRenderingService } from 'app/common/services/sites/page-rendering.service';
import { PublishedSiteService } from 'app/render/services/published-site.service';
import { BizagiDataService } from 'app/render/services/bizagi-data.service';
import { ThemeService } from 'app/common/services/sites/theme.service';
import { BizagiFormsComponent, FormType } from 'app/render/bizagi-forms/bizagi-forms.component';
import { UserService } from 'app/common/services/sites/user.service';
import { Subscription } from 'rxjs';
import { select, Store } from '@ngrx/store';
import { TranslateService } from '@ngx-translate/core';
import { FontsServiceCommon } from 'app/common/services/sites/fonts.service';
import { UpdateGridSize } from '../store/grid/grid.actions';
import { ClearNavigationContexts } from '../store/navigation-contexts/navigation-contexts.actions';
import { LoadCurrentLanguage } from '../store/multilanguage/multilanguage.actions';
import { UpdatePageContext } from '../store/page-context/page-context.actions';
import { LoadSite } from '../store/site/site.actions';
import { getCurrentSite } from '../store/site/site.selector';
import { getCurrentPage } from '../store/page/page.selector';
import { getPages } from '../store/pages/pages.selector';
import { LoadPages } from '../store/pages/pages.actions';
import { LoadPage } from '../store/page/page.actions';
import { SetPageData } from '../store/page-data/page-data.actions';
import { getSiteLanguage } from '../store/multilanguage/multilanguage.selector';
import { StateSnapshotService } from 'app/common/services/sites/state-snapshots.service';
import { OAuthClientHelper } from '../../common/services/oauth/oauth-client.helper';
import { getSiteUser } from '../store/user/user.selector';
import { UpdateUser } from '../store/user/user.actions';

const objectFitImages = require('object-fit-images');
@Component({
  selector: 'bs-page-render',
  templateUrl: './page-render.component.html',
  styleUrls: ['./page-render.component.scss'],
})
export class PageRenderComponent {
  public controls: Array<IControl>;
  public page: IPage;
  public pagesList: Array<IBasicPage>;
  public site: ISite;
  public user: IUserProfile;
  public userSite: IUserSiteProfile;
  public isLoadingPreferences = false;
  public navigationContexts: Array<INavigationContext>;
  public pageName: string;
  public siteName: string;
  public currentLanguage: ILanguageResource;
  private _currentLanguageSubscription: Subscription;
  private _currentSiteSubscription: Subscription;
  private _currentPageSubscription: Subscription;
  private _pagesSubscription: Subscription;
  private _userSubscription: Subscription;
  @ViewChild('headerControl') headerControl: any;


  // Form settings
  @ViewChild('form', { static: false })
  private form: BizagiFormsComponent;
  private formParameters: IFormRenderingParams = {
    connection: '',
    title: '',
    type: FormType.New,
    parameters: {},
    refresh: null,
  };
  _fonts: Blob[];

  constructor(
    private _route: ActivatedRoute,
    private _publishedSiteServices: PublishedSiteService,
    private _pageRenderingService: PageRenderingService,
    private _bizagiDataServices: BizagiDataService,
    private _cdr: ChangeDetectorRef,
    private _themeServices: ThemeService,
    private _userService: UserService,
    private _store: Store<IRenderState>,
    private _translateService: TranslateService,
    private _fontsServiceCommon: FontsServiceCommon,
    private _oAuthClientHelper: OAuthClientHelper,
    private _stateSnapshots: StateSnapshotService,
    private _router: Router
  ) { }

  ngOnInit() {
    this._route.params.subscribe(async (params) => {
      this.page = null;
      this.controls = [];
      this._cdr.detectChanges();

      this.siteName = params['siteName'];
      this.pageName = params['pageName'];
      if (_.isEmpty(this.site)) {
        this._store.dispatch(new LoadSite(this.siteName));
      } else {
        this._store.dispatch(new LoadPage({ siteName: this.siteName, pageName: this.pageName }));
      }
      if (!this.pagesList || !this.pagesList.length) {
        this._store.dispatch(new LoadPages(this.siteName));
      }
    });

    this._currentLanguageSubscription = this._store.pipe(select(getSiteLanguage))
      .subscribe((currentLanguage: ILanguageResource) => {
        if (currentLanguage) {
          this.currentLanguage = currentLanguage;
          if (!this.page) {
            this._store.dispatch(new LoadPage({ siteName: this.siteName, pageName: this.pageName }));
          }
        }
      });
    this._currentSiteSubscription = this._store.pipe(select(getCurrentSite)).subscribe(async (site: ISite) => {
      if (!_.isEmpty(site)) {
        this.site = site;
        this._oAuthClientHelper.setRedirectHeader(this.site?.header?.parameters?.logout?.fromPrivate, 
          this.site?.header?.parameters?.logout?.fromPublic);
        this._stateSnapshots.setSite(site);
        this._loadSiteResources(site);
        if (!this.page) {
          this._store.dispatch(new LoadPage({ siteName: this.siteName, pageName: this.pageName }));
        }
      }
    });
    this._currentPageSubscription = this._store.pipe(select(getCurrentPage)).subscribe((currentPage: IPageState) => {
      if (!_.isEmpty(currentPage)) {
        if (this.site && currentPage && this.currentLanguage) {
          this.page = currentPage;
          this._stateSnapshots.setPage(currentPage);
          this._loadPageResources();
        }
      }
    });
    this._pagesSubscription = this._store.pipe(select(getPages)).subscribe((pages: Array<IBasicPage>) => {
      if (pages) {
        this._stateSnapshots.setPages(pages);
        _.forEach(pages, (page: IBasicPage) => {
          if (!page.originalId) {
            page.originalId = page.id;
          }
        });
        this.pagesList = pages;
      }
    });

    this._userSubscription = this._store.pipe(select(getSiteUser)).subscribe((userSite: IUserSiteProfile) => {
      if (!_.isEmpty(userSite)) {
        this.userSite = userSite;
      }
    });

    // Attach to rendering service events
    // if (this._oAuthClientHelper.isLoggedOn()) {
    this._pageRenderingService.on('showForm', parameters => this.showForm(parameters));
    // }
  }

  ngAfterViewInit() {
    objectFitImages();
  }

  ngOnDestroy() {
    this._pagesSubscription.unsubscribe();
    this._currentPageSubscription.unsubscribe();
    this._currentSiteSubscription.unsubscribe();
    this._currentLanguageSubscription.unsubscribe();
  }

  public updateUserPreferences(user: IUserSiteProfile) {
    this.isLoadingPreferences = true;
    setTimeout(() => {
      this.isLoadingPreferences = false;
    }, 500);
  }

  private async loadPageContext(page: IPage) {
    try {
      const url = (_.values(this.site.environments[this.site.defaultEnvironment].projects) as Array<IBizagiProject>)[0].url;
      const stakeholders = await this._bizagiDataServices.getStakeholders(url);
      _.each(stakeholders, (stakeholder: IStakeholder) => {
        _.each(stakeholder.contexts, (context: IStakeholderContext) => {
          this.user.contexts = [context.id, ...this.user.contexts];
        });
      });
      if ((Global.isVisibleTo(page.visibleTo, this.user.contexts) && stakeholders.length > 0) || !page.isPrivate) {
        this.controls = page.isPrivate ?
          Global.getControlsByStakeholderContext(this.user.contexts, this.page.controls) : this.page.controls;
        this._route.queryParams.pipe(first()).subscribe(queryParams => {
          const project = queryParams['project'] || _.first(_.keys(this.site.environments[this.site.defaultEnvironment].projects));
          const contextStr = queryParams['context'];
          if (project && contextStr) {
            this.processNavigationContext(project, contextStr);
          } else {
            this._store.dispatch(new ClearNavigationContexts());
          }
        });
      } else {
        let navigateTo;
        for (let index = 0; index < this.site.header.navigation.length; index++) {
          if(this.site.header.navigation[index].type !== 'Page'){
            continue;
          }
          navigateTo = await this._getNavigateToNextPage(this.site.header.navigation[index], stakeholders);

          if (!navigateTo && this.site.header.navigation[index].references?.length) {
            for (let idx = 0; idx < this.site.header.navigation[index].references.length; idx++) {
              if(this.site.header.navigation[index].references[idx].type !== 'Page'){
                continue;
              }
              navigateTo = await this._getNavigateToNextPage(this.site.header.navigation[index].references[idx], stakeholders);
              if(navigateTo) {
                break;
              }
            }
          }

          if (navigateTo) {
            break;
          }

        }
        if (navigateTo) {
          this._router.navigate([this.siteName, navigateTo]);
        } else {
          this._router.navigate(['unauthorized'], { queryParams: { mode: 'render', url: location.href } });
        }

      }
    } catch (err) { }
  }

  private async _getNavigateToNextPage(navigation: IPageReference, stakeholders) {
    const pageNav = await this._publishedSiteServices.getPage(this.siteName, navigation['pageName']);
    if (!pageNav.isPrivate) {
      return pageNav.name;
    }
    if (Global.isVisibleTo(navigation.visibleTo, this.user.contexts) && stakeholders.length > 0) {
      return pageNav.name;
    }
  }

  private _loadPageResources() {
    if (this.page.grid) {
      this._store.dispatch(new UpdateGridSize(this.page.grid.size));
    }
    this._loadPageContext();
  }

  private async _loadSiteResources(site: ISite) {
    this._store.dispatch(new LoadCurrentLanguage(site.globalSettings.defaultResource));
    this._themeServices.loadSiteTheme(site.theme);
    this._fontsServiceCommon.loadAllFonts(site.globalSettings.defaultFontFamily);
    this._fonts = await this._fontsServiceCommon.loadFontsFilesByFamilyName(site.globalSettings.defaultFontFamily);
  }

  private async _loadPageContext() {
    if (!this.userSite && await this._oAuthClientHelper.isLoggedOn()) {
      try {
        this.user = await this._bizagiDataServices.getUserProfile(this.site.identityServer);
        const userFromSites = await this._userService.getUserByUsername(this.user.userName);
        if (userFromSites) {
          const notPreferencesCustom = !userFromSites.preferences;
          const defaultPreferences = _.cloneDeep(this.currentLanguage.culture);
          const preferences = notPreferencesCustom
            ? _.cloneDeep(this.currentLanguage.culture)
            : userFromSites.preferences.isCustom
              ? userFromSites.preferences
              : defaultPreferences;
          preferences.isCustom = !notPreferencesCustom ? userFromSites.preferences.isCustom : false;
          userFromSites.preferences = preferences;
          this.userSite = (userFromSites as IUserSiteProfile);
        } else {
          const newUserSites = { ...this.user, preferences: this.currentLanguage.culture };
          this.userSite = (await this._userService.createOrUpdateUserPreferences(newUserSites) as IUserSiteProfile);
        }
        this._store.dispatch(new UpdateUser(this.userSite));
      } catch (error) {
      }
    }
    if (!this.page.isPrivate) {
      this.controls = this.page.controls;
    }
    if (this.page.isPrivate || this.userSite) {
      this.user = await this._bizagiDataServices.getUserProfile(this.site.identityServer);
      if (Global.isBizagiImage(this.user.picture)) {
        this.user.picture = Global.getBizagiImageUrl(this.user.picture);
      }
      this.user.contexts = [];
      await this.loadPageContext(this.page);
    }

  }

  // Form management
  private showForm(parameters: IFormRenderingParams) {
    this.formParameters = parameters;

    // This is to allow angular to redraw the view and add the parameters to the form EditTemplateComponent
    // Sound weird isn"t it?
    setTimeout(() => {
      this.form.show(this._fonts);
      this.form.registerCloseFunction(this.formParameters.refresh);
    });
  }

  private async processNavigationContext(project: string, contextStr: string) {
    if (!project || !contextStr) {
      return;
    }

    // Get all context parts
    this.navigationContexts = (Global.base64ToJson(contextStr) as Array<INavigationContext>);
    this._stateSnapshots.setNavigationContext(this.navigationContexts);
    const lastNavigation = _.last(this.navigationContexts);
    const newNavigation: any = lastNavigation.connectionType === 'context' ? this.navigationContexts : [lastNavigation];
    const url = Global.getBizagiProjectUrl(this.site, project);
    const data = await this._bizagiDataServices.getNavigationData(url, newNavigation);

    if (data) {
      this.page.context.entity.value = data.id;
      const pageData = _.mapValues(_.keyBy(data['fields'], 'xpath'), 'value');
      this._store.dispatch(new UpdatePageContext(this.page.context));
      this._store.dispatch(new SetPageData(pageData));
      this._store.dispatch(new LoadPages(this.siteName));
    }

    this.page.isContextualized = true;
  }



}
