import {Component, ElementRef, ViewChild, HostBinding} from '@angular/core';
import {ControlDefinition} from 'app/common/decorators/control-definition';
import {CATEGORY, SUBCATEGORY_INBOX, PROPERTY_SECTIONS} from './../controls-name-definition';
import {EDeviceMode} from 'app/editor/models/EDeviceMode';
import {InboxControlGridViewComponent} from 'app/common/controls/inbox-control/grid-view/inbox-grid-view.component';
import {BaseInboxControlComponent, EDataType} from '../base-inbox.component';
import { Global } from 'app/common/misc/global';

@Component({
  selector: 'bs-inbox-control',
  templateUrl: './inbox-control.component.html',
  styleUrls: ['./inbox-control.component.scss']
})
@ControlDefinition({
  type: 'inbox',
  displayName: 'INBOX_CONTROL',
  category: CATEGORY.EXPERIENCE_ELEMENTS.displayName,
  subcategory: SUBCATEGORY_INBOX.displayName,
  icon: SUBCATEGORY_INBOX.controls.Inbox.icon,
  styles: {
    displayedItems: {
      type: 'range',
      defaultValue: 10,
      category: 'hidden',
      min: 1,
      max: 10
    },
    fontFamily: {
      section: PROPERTY_SECTIONS.fonts.key,
      type: 'font-family',
      defaultValue: 'inherit',
    },
    layout: {
      type: 'layout',
      defaultValue: 'three',
      category: 'hidden',
      layouts: {
        one: {
          icon: 'one-column-thin',
          text: 'ONE_COLUMN',
          columns: 1
        },
        two: {
          icon: 'two-columns-thin',
          text: 'TWO_COLUMNS',
          min: 50,
          columns: 2
        },
        three: {
          icon: 'three-columns-thin',
          text: 'THREE_COLUMNS',
          min: 75,
          columns: 3
        }
      }
    }
  },
  parameters: {
    data: {
      type: 'data',
      size: 3,
      defaultValue: {
       },
      mappings: {
        processName: { type: 'title', label: 'DATA_PROPERTY_IMAGE' },
        caseNumber: { type: 'bizagi-number', label: 'DATA_PROPERTY_TITLE' },
        workitems: { type: 'workitem', label: 'DATA_PROPERTY_TEXT' },
        creationDate: { type: 'date', label: 'DATA_PROPERTY_TEXT' },
        solutionDate: { type: 'date', label: 'DATA_PROPERTY_TEXT' }
      }
    }
  },
  defaultSize: {
    width: 1100,
    height: 995,
    minWidth: 320,
    minHeight: 995,
    maxHeight: 995
  },
  navigation: [],
  bizagiConnections: {
    inbox: true
  },
  hiddenOptions: {
    dataManager: true,
    copy: true
  }
})



export class InboxControlComponent extends BaseInboxControlComponent {

  public viewOptions = {
    datatable: {
      active: false,
      blocked: false
    },
    cards: {
      active: true,
      blocked: false
    }
  };

  public dataSearchMode = 'state';
  public stateFilterLocalization = '';
  public orderedBy = 'asc';

  @ViewChild('caseNumberInput', { static: true })
  public caseNumberInput;

  @ViewChild('inboxGridView') inboxGridView: InboxControlGridViewComponent;

  @HostBinding('style.pointer-events') pointerEvent = Global.isOnRenderMode() ? 'all': 'none';

  constructor(private _eref: ElementRef) {
    super();
    this.dataType = EDataType.Inbox;
  }

  ngOnInit() {
   this.onChanged();
    this.changeView('cards');
  }

  public changeView(option: String) {
    switch (option) {
      case 'datatable':
        this.viewOptions.datatable.active = true;
        this.viewOptions.cards.active = false;
        this.styles.displayedItems = 16;
        break;
      case 'cards':
        this.viewOptions.datatable.active = false;
        this.viewOptions.cards.active = true;
        this._setCardPaginationSize();
        break;
    }
    setTimeout(() => {
      this.refresh();
    }, 600);
    this.pagination.currentPage = _.ceil(this.pagination.from / this.pagination.rows);
  }

  public async filterByStatus(status: string) {
    if (this.isRenderingMode()) {
      this.stateFilterLocalization = (await this._getLocalizationState(status)).toLowerCase();
      this.dataSearchMode = 'state';
      this.parameters.filter = '';
      this.parameters.state = status;
      this.caseNumberInput.nativeElement.value = '';
      this.pagination.from = 0;
      this.pagination.currentPage = 0;
      this.refresh();
    }
  }

  public filterByCaseNumber(filter: string) {
    if (this.isRenderingMode()) {
      if (!filter) {
        this.filterByStatus('');
        return;
      }
      this.dataSearchMode = 'filter';
      this.parameters.filter = filter;
      this.parameters.state = '';
      this.pagination.from = 0;
      this.pagination.currentPage = 0;
      this.refresh();
    }
  }

  public orderByColumnName(columnName: string) {
    this.orderedBy = this.orderedBy === 'asc' ? 'desc' : 'asc';
    this.parameters.orderby = `${columnName} ${this.orderedBy}`;
    this.refresh();
  }


  public clearSearch(input: any) {
    input.value = '';
    this.filterByStatus('');
  }

  protected onChanged() {
    const width = this.control.location.width;
    if (width < 700 && !this.viewOptions.datatable.blocked) {
      this.viewOptions.datatable.blocked = true;
      this.changeView('cards');
    } else if (width >= 700 && this.viewOptions.datatable.blocked) {
      this.viewOptions.datatable.blocked = false;
    }
  }

  public isDesktopMode() {
    return this.deviceMode === EDeviceMode.Desktop;
  }

  public showCaseEvent(caseParams: string[]) {
    this.showCase(caseParams[0], caseParams[1], caseParams[2], null, caseParams[3]);
  }

  private async _getLocalizationState(state: string) {
    switch (state) {
      case 'red':
        return this.translateService.get('controls.inbox.OVERDUE').toPromise();
      case 'green':
        return this.translateService.get('controls.inbox.ON_TIME').toPromise();
      case 'yellow':
        return this.translateService.get('controls.inbox.AT_RISK').toPromise();
      default:
        return '';
    }
  }

  private _setCardPaginationSize() {
    switch (this.styles.layout) {
      case 'one': {
        this.styles.displayedItems = 3;
        break;
      }
      case 'two': {
        this.styles.displayedItems = 6;
        break;
      }
      case 'three': {
        this.styles.displayedItems = 9;
        break;
      }
    }
  }
}
