import { Injectable, ElementRef } from '@angular/core';
import { OAuthClientHelper } from 'app/common/services/oauth/oauth-client.helper';
import { Observable, Subject, Subscription, timer, interval } from 'rxjs';
import { debounceTime, tap } from 'rxjs/operators';
import { ConfirmDialogModalComponent } from 'app/common/components/confirm-dialog-modal/confirm-dialog-modal.component';
import { BzgModalService, BzgModalRef } from 'bz-genome-angular';
import { StateSnapshotService } from '../sites/state-snapshots.service';
import { Global } from 'app/common/misc/global';

@Injectable()
export class SessionHandlerService {
    private expirationTimer: Observable<number>;
    private endingTimer: Observable<number>;
    private bpmSessionTimer: Observable<number>;
    private expirationTimerUnsubscribe: Subject<void> = new Subject<void>();
    private expirationTimerSessionBPMUnsubscribe: Subject<void> = new Subject<void>();

    public targetOrigin: string;
    public showIframe: boolean;
    public elementReference: ElementRef;
    public iframeFakeLoaded: boolean;

    public restartSessionSubject = new Subject();
    public startSessionSubject = new Subject();
    private _modalRefSessionIdle: BzgModalRef;
    private _modalRefSessionTimeout: BzgModalRef;

    constructor(private _oAuthClient: OAuthClientHelper,
        private _modalService: BzgModalService, private _stateSnapshots: StateSnapshotService) {
        this.restartSessionSubject.pipe(debounceTime(30000)).subscribe(
            async () => {
                const session = await this.refreshToken();
                if (!session) {
                    return;
                }
                this.startTimerSession(session.expiration);
            });

        this.startSessionSubject.pipe(debounceTime(1000)).subscribe(
            () => {
                const expiration = this._oAuthClient.getExpiration();
                this.startTimerSession(expiration);
            });
    }

    public initializeIframe(showIframe: boolean, targetOrigin: string, elementReference: ElementRef, iframeFakeLoaded: boolean) {
        this.showIframe = showIframe;
        this.targetOrigin = targetOrigin;
        this.elementReference = elementReference;
        this.iframeFakeLoaded = iframeFakeLoaded;
    }

    public async refreshToken(): Promise<any> {
        if (Global.isOnRenderMode()) {
            const page = this._stateSnapshots.getPage();
            if(!page.isPrivate) {
                return '';
            }
        }
        const newToken = await this._oAuthClient.refreshToken();
        const iframe: any = this.getIframe();

        if (this.showIframe) {
            if (!!iframe && iframe.length) {
                this.sendMessageToIframe(iframe, 'update-token', { token: newToken.accessToken });
            }
        }

        const iframeFake: any = this.getIframeFake();

        if (this.iframeFakeLoaded) {
            if (!!iframeFake && iframeFake.length) {
                this.sendMessageToIframe(iframeFake, 'update-token', { token: newToken.accessToken });
            }
        }
        
        return newToken;
    }

    public startTimerSession(expirationDateValue: string) {
        const expirationDate = new Date(expirationDateValue);
        const currentDate = new Date();
        let difference = expirationDate.getTime() - currentDate.getTime();
        const oneDayMilliseconds = 86400000;
        if (difference > oneDayMilliseconds) {
            difference = oneDayMilliseconds;
        }
        console.log('Milliseconds difference for debug purposes', difference);
        this.unsubscribeTimer();
        this.initTimers(difference / 2);
    }

    public startTimerBPMSession(sessionTimeout: number) {
        let timeoutMilliseconds = sessionTimeout * 60000
        const oneDayMilliseconds = 86400000;
        if (timeoutMilliseconds > oneDayMilliseconds) {
            timeoutMilliseconds = oneDayMilliseconds;
        }
        this.unsubscribeTimerSessionBPM();
        this.bpmSessionTimer = interval(timeoutMilliseconds / 2);
        this.bpmSessionTimer
            .takeUntil(this.expirationTimerSessionBPMUnsubscribe)
            .subscribe(() => {
                const iframe = this.getIframe();
                this.sendMessageToIframe(iframe, 'keep-alive-session', {});
            });
    }

    public unsubscribeTimer() {
        this.expirationTimerUnsubscribe.next();
    }

    public unsubscribeTimerSessionBPM() {
        this.expirationTimerSessionBPMUnsubscribe.next();
    }

    private initTimers(value: number) {
        this.expirationTimer = timer(value);
        this.endingTimer = timer(value);
        this.startExpirationTimer();
    }

    private startExpirationTimer() {
        this.expirationTimer
            .takeUntil(this.expirationTimerUnsubscribe)
            .subscribe(() => {
                this.showAlertIdleSession();
                this.startEndingTimer();
            });
    }

    private startEndingTimer() {
        this.endingTimer
            .takeUntil(this.expirationTimerUnsubscribe)
            .subscribe(() => {
                this.showAlertTimeoutSession();
            });
    }



    private sendMessageToIframe(iframe, eventType, message = null) {
        const data = {
            event: eventType,
            message,
            sitesEl: this._oAuthClient.getToken()
        };
        console.log('------- MESSAGE OUT -------', data.event, data.message);
        iframe[0]?.contentWindow.postMessage(data, this.targetOrigin);
    }

    private getIframeFake() {
        const element = this.elementReference?.nativeElement;
        return $('#bs-bizagi-process-fake', element);
    }

    private getIframe() {
        const element = this.elementReference?.nativeElement;
        return $('#bs-bizagi-process', element);
    }
    private showAlertIdleSession() {
        this.closeOpenModals();
        this._modalRefSessionIdle = this._modalService.open(ConfirmDialogModalComponent, {
            backdropClass: 'cdk-overlay-dark-backdrop-custom',
            maxWidth: '500px',
            closeOnBackdropClick: false,
            data: {
                message: 'messages.session.MSG_IDLE',
                title: 'messages.session.TITLE_IDLE',
                confirmButtonText: 'messages.session.BTN_CONTINUE',
                cancelButton: false,
                icon: 'icon-warning',
                iconColor: 'warning'
            }
        });

        this._modalRefSessionIdle.afterClosed().subscribe(async result => {
            if (result && result.type === 'confirm') {
                this.unsubscribeTimer();
                const newToken = await this.refreshToken();
                this.startTimerSession(newToken.expiration);
                this._modalRefSessionIdle.close();
            } else {
                this._modalRefSessionIdle.close();
            }
        });
    }

    private showAlertTimeoutSession() {
        this.closeOpenModals();
        this._modalRefSessionTimeout = this._modalService.open(ConfirmDialogModalComponent, {
            backdropClass: 'cdk-overlay-dark-backdrop-custom',
            maxWidth: '500px',
            closeOnBackdropClick: false,
            data: {
                message: 'messages.session.MSG_TIMEOUT',
                title: 'messages.session.TITLE_TIMEOUT',
                confirmButtonText: 'messages.session.BTN_LOGIN',
                cancelButton: false,
                icon: 'icon-warning',
                iconColor: 'warning'
            }
        });

        this._modalRefSessionTimeout.afterClosed().subscribe(async result => {
            if (result && result.type === 'confirm') {
                this._modalRefSessionTimeout.close();
                this.unsubscribeTimer();
                this._oAuthClient.logout();
            } else {
                this._oAuthClient.logout();
                this._modalRefSessionTimeout.close();
            }
        });
    }

    private closeOpenModals() {
        _.each(this._modalService.openModals, (modal) => {
            modal.close();
        });
    }

}