import { BaseProcessControlComponent } from 'app/common/controls/base-process.component';
import { ControlDefinition } from 'app/common/decorators/control-definition';
import { Component } from '@angular/core';
import {
  CATEGORY,
  SUBCATEGORY_BUTTON_REGULAR,
  BUTTON_REGULAR_CONTROLS,
  PROPERTY_SECTIONS,
} from './../controls-name-definition';

@Component({
  selector: 'bs-button-control',
  templateUrl: './button-control.component.html',
  styleUrls: ['./button-control.component.scss']
})
@ControlDefinition(
  {
    type: BUTTON_REGULAR_CONTROLS.button.type,
    category: CATEGORY.BUTTONS.displayName,
    subcategory: SUBCATEGORY_BUTTON_REGULAR.displayName,
    displayName: BUTTON_REGULAR_CONTROLS.button.displayName,
    icon: BUTTON_REGULAR_CONTROLS.button.icon,
    styles: {
      fontFamily: {
        section: PROPERTY_SECTIONS.fonts.key,
        type: 'font-family',
        defaultValue: 'inherit'
      },
      fontSize: {
        section: PROPERTY_SECTIONS.fonts.key,
        type: 'font-size',
        defaultValue: '14px',
        max: 72
      },
      fontColor: {
        section: PROPERTY_SECTIONS.fonts.key,
        type: 'color',
      },
      backColor: {
        section: PROPERTY_SECTIONS.background.key,
        type: 'color',
      },
      align: {
        section: PROPERTY_SECTIONS.style.key,
        type: 'text-align',
        defaultValue: 'center'
      },
      fullWidth: {
        section: PROPERTY_SECTIONS.style.key,
        type: 'boolean',
        defaultValue: false
      },
      border: {
        section: PROPERTY_SECTIONS.border.key,
        type: 'border',
      },
      borderRadius: {
        section: PROPERTY_SECTIONS.border.key,
        type: 'range',
        defaultValue: 5,
        min: 0,
        max: 25
      }
    },
    parameters: {
      text: {
        type: 'title',
        defaultValue: BUTTON_REGULAR_CONTROLS.button.displayName
      },
      iconSelector: {
        type: 'icon-selector'
      },
      showLeftIcon: {
        type: 'boolean',
        defaultValue: true
      },
      showRightIcon: {
        type: 'boolean',
        defaultValue: false
      }
    },
    defaultSize: {
      width: 170,
      height: 59
    },
    links: {
      staticPages: true,
      internalPages: true,
      processes: true
    }
  }
)
export class ButtonControlComponent extends BaseProcessControlComponent {
  public fontColor: string;
  public backColor: string;

  public hasIcon(paramName) {
    return (this.parameters[paramName] && (this.parameters[paramName] === true || 
      (typeof (this.parameters[paramName]) === 'string' && this.parameters[paramName].toLowerCase() === 'true')));
  }
}
