import { Component } from '@angular/core';
import { BaseDataControlComponent } from 'app/common/controls/base-data.component';
import { ControlDefinition } from 'app/common/decorators/control-definition';
import { CATEGORY, SUBCATEGORY_LIST_VIEW, LIST_VIEW_CONTROLS, PROPERTY_SECTIONS } from './../controls-name-definition';
import { DefaultValues } from '../../misc/default-values';
import { Global } from 'app/common/misc/global';

@Component({
  selector: 'bs-list-view-control',
  templateUrl: './list-view-control.component.html',
  styleUrls: ['./list-view-control.component.scss']
})
@ControlDefinition(
  {
    type: LIST_VIEW_CONTROLS.listView.type,
    category: CATEGORY.BUNDLES.displayName,
    subcategory: SUBCATEGORY_LIST_VIEW.displayName,
    displayName: LIST_VIEW_CONTROLS.listView.displayName,
    icon: LIST_VIEW_CONTROLS.listView.icon,
    styles: {
      designCompressed: {
        type: 'design',
        category: 'design',
        defaultValue: 'false'
      },
      designDisplay: {
        type: 'design',
        defaultValue: 'basic-right',
        category: 'hidden',
        displays: {
          'basic-right': {
            icon: 'dynamic_basic_right',
            text: 'siteeditor.layouts.DESIGN_DISPLAY_RIGHT',
          },
          'basic-left': {
            icon: 'dynamic_basic_left',
            text: 'siteeditor.layouts.DESIGN_DISPLAY_LEFT',
          }
        }
      },
      designActions: {
        type: 'design',
        defaultValue: 'bottom',
        category: 'hidden',
        actions: {
          top: {
            icon: 'dynamic_top',
            text: 'siteeditor.layouts.DESIGN_ACTIONS_TOP',
          },
          bottom: {
            icon: 'dynamic_botton',
            text: 'siteeditor.layouts.DESIGN_ACTIONS_BOTTOM',
          }
        }
      },
      layout: {
        type: 'layout',
        defaultValue: 'two',
        category: 'hidden',
       layouts: {
            one: {
              icon: 'one-column-thin',
              text: 'ONE_COLUMN',
              columns: 1
            },
            two: {
              icon: 'two-columns-thin',
              text: 'TWO_COLUMNS',
              min: 50,
              columns: 2
            },
            three: {
              icon: 'three-columns-thin',
              text: 'THREE_COLUMNS',
              min: 75,
              columns: 3
            }
          }
      },
      multipageItemView: {
        type: 'radio',
        defaultValue: 'Pagination',
        category: 'layout',
        values: ['Pagination']
      },
      verticalItemSpacing: {
        type: 'range',
        defaultValue: 12,
        category: 'layout',
        step: 4,
        min: 4,
        max: 40,
        unitValue: 'px',
        headerName: 'Rows'
      },
      displayedItems: {
        type: 'range',
        defaultValue: 3,
        category: 'layout',
        min: 1,
        max: 10
      },
      backColor: {
        section: PROPERTY_SECTIONS.background.key,
        type: 'color',
        defaultValue: '#FFF'
      },
      boxShadow: {
        section: PROPERTY_SECTIONS.border.key,
        type: 'text',
        defaultValue: '0 1px 1px 0 rgba(0,0,0,.5), 0 1px 6px 0 rgba(0,0,0,.02)'
      },
      border: {
        section: PROPERTY_SECTIONS.border.key,
        type: 'border',
        defaultValue: '1px solid #e4e8ea'
      },
      cornerRadius: {
        section: PROPERTY_SECTIONS.border.key,
        type: 'range',
        defaultValue: 0,
        min: 6,
        max: 50
      },
      showActions: {
        section: PROPERTY_SECTIONS.style.key,
        type: 'boolean',
        defaultValue: 'true'
      }
    },
    parameters: {
      data: {
        type: 'data',
        size: 10
      },
      filter: {
        type: 'filter',
        defaultValue: '',
        isFilter: true
      }
    },
    inputs: ['parameters.filter'],
    defaultSize: {
      width: 950,
      height: 620,
      minWidth: 260,
      minHeight: 145,
      dynamic: true
    },
    bizagiConnections: {
      cases: true,
      collections: true,
      searches: true,
      config: {
        image: 'list-view-config.png',
        coords: {
          image: {
            w: 75,
            h: 75,
            x: 7,
            y: 7
          },
          title: {
            w: 136,
            h: 13,
            x: 90,
            y: 7
          },
          text: {
            w: 180,
            h: 10,
            x: 90,
            y: 30
          },
          info: {
            w: 80,
            h: 18,
            x: 190,
            y: 50
          }
        }
      }
    },
    fields: {
      image: {
        type: 'image',
        displayName: 'siteeditor.properties.IMAGE',
        styles: {
          imagesSize: {
            section: PROPERTY_SECTIONS.style.key,
            type: 'image-size',
            defaultValue: 'scale'
          },
          opacity: {
            section: PROPERTY_SECTIONS.background.key,
            type: 'range',
            defaultValue: 100,
            min: 0,
            max: 100
          },
          background: {
            section: PROPERTY_SECTIONS.background.key,
            type: 'color',
            defaultValue: 'rgba(0, 0, 0, 0)'
          },
          border: {
            section: PROPERTY_SECTIONS.border.key,
            type: 'border',
            defaultValue: 'border-none'
          }
        }
      },
      title: {
        type: 'title',
        displayName: 'siteeditor.properties.TITLE',
        styles: {
          fieldLabel: {
            type: 'label-field',
            defaultValue: {
              isActive: false,
              text: 'Label: '
            }
          },
          fontFamily: {
            section: PROPERTY_SECTIONS.fonts.key,
            type: 'font-family',
            defaultValue: 'inherit'
          },
          fontSize: {
            section: PROPERTY_SECTIONS.fonts.key,
            type: 'font-size',
            defaultValue: '28px',
            max: 48
          },
          letterSpacing: {
            section: PROPERTY_SECTIONS.fonts.key,
            type: 'range',
            defaultValue: 0,
            min: 0,
            max: 60
          },
          align: {
            section: PROPERTY_SECTIONS.fonts.key,
            type: 'text-align',
            defaultValue: 'left'
          },
          fontColor: {
            section: PROPERTY_SECTIONS.fonts.key,
            type: 'color',
            defaultValue: 'color-first'
          },
          backColor: {
            section: PROPERTY_SECTIONS.background.key,
            type: 'color',
            defaultValue: 'rgba(0, 0, 0, 0)'
          },
          fontWeight: {
            section: PROPERTY_SECTIONS.fonts.key,
            type: 'font-weight',
            defaultValue: 'normal'
          }
        }
      },
      text: {
        type: 'text',
        displayName: 'siteeditor.properties.TEXT',
        styles: {
          fieldLabel: {
            type: 'label-field',
            defaultValue: {
              isActive: false,
              text: 'Label: '
            }
          },
          fontFamily: {
            type: 'font-family',
            defaultValue: 'inherit'
          },
          fontSize: {
            type: 'font-size',
            defaultValue: '12px',
            max: 48
          },
          letterSpacing: {
            type: 'range',
            defaultValue: 0,
            min: 0,
            max: 60
          },
          align: {
            type: 'text-align',
            defaultValue: 'left'
          },
          fontColor: {
            type: 'color',
            defaultValue: 'color-sixth'
          },
          backColor: {
            type: 'color',
            defaultValue: 'rgba(0, 0, 0, 0)'
          },
          fontWeight: {
            type: 'font-weight',
            defaultValue: 'normal'
          }
        }
      },
      caption: {
        type: 'title',
        displayName: 'siteeditor.properties.CAPTION',
        styles: {
          fieldLabel: {
            type: 'label-field',
            defaultValue: {
              isActive: false,
              text: 'Label: '
            }
          },
          fontFamily: {
            type: 'font-family',
            defaultValue: 'inherit'
          },
          fontSize: {
            type: 'font-size',
            defaultValue: '28px',
            max: 48
          },
          letterSpacing: {
            type: 'range',
            defaultValue: 0,
            min: 0,
            max: 60
          },
          align: {
            type: 'text-align',
            defaultValue: 'left'
          },
          fontColor: {
            type: 'color',
            defaultValue: 'color-first'
          },
          backColor: {
            type: 'color',
            defaultValue: 'rgba(0, 0, 0, 0)'
          },
          fontWeight: {
            type: 'font-weight',
            defaultValue: 'normal'
          }
        }
      }
    },
    hiddenOptions: {
      modifyFields: true,
      designOption: false
    }
  }
)
export class ListViewControlComponent extends BaseDataControlComponent {
  public isOnRenderMode = Global.isOnRenderMode();
  public defaultPicture = DefaultValues.getDefaultPicture();

  public getCustomStyles() {
    const element = document.getElementById(this.control.id);
    if(this.control.fields.text.active && !this.control.fields.caption.active) {
      const height =  140;
      const lineHeight = this._getLineHeight(element, this.control.fields.text.styles.fontFamily,
        this.control.fields.text.styles.fontSize);
      const calc = _.floor(height/lineHeight);
      $('#' + this.control.id + ' .bs-list-view__content__body__content__text').css({'-webkit-line-clamp': '' + calc + ''});
      $('#' + this.control.id + ' .bs-list-view__content__body__content__text').css({'max-height': '' + height + 'px'});
    }
    if(!this.control.fields.text.active && this.control.fields.caption.active) {
      const lineHeight = this._getLineHeight(element, this.control.fields.caption.styles.fontFamily,
        this.control.fields.caption.styles.fontSize);
      const height =  140;
      const calc = _.floor(height/lineHeight);
      $('#' + this.control.id +' .bs-list-view__content__body__caption__text').css({'-webkit-line-clamp': '' + calc + ''});
      $('#' + this.control.id +' .bs-list-view__content__body__caption__text').css({'max-height': '' + height + 'px'});
    }
    if (this.control.fields.text.active && this.control.fields.caption.active) {
      const parentHeight = 140;
      const textHeight = parentHeight * 0.6;
      const captionHeight =  parentHeight * 0.4;
      const lineTexHeight = this._getLineHeight(element, this.control.fields.text.styles.fontFamily,
        this.control.fields.text.styles.fontSize);
      const lineCaptionHeight = this._getLineHeight(element, this.control.fields.caption.styles.fontFamily,
          this.control.fields.caption.styles.fontSize);
      const linesText = _.floor(textHeight/lineTexHeight);
      const linesCaption = _.floor(captionHeight/lineCaptionHeight);
      $('#' + this.control.id +' .bs-list-view__content__body__content__text').css({'-webkit-line-clamp': '' + linesText + ''});
      $('#' + this.control.id +' .bs-list-view__content__body__content__text').css({'max-height': '' + textHeight + 'px'});
      $('#' + this.control.id +' .bs-list-view__content__body__caption__text').css({'-webkit-line-clamp': '' + linesCaption + ''});
      $('#' + this.control.id +' .bs-list-view__content__body__caption__text').css({'max-height': '' + captionHeight + 'px'});
    }
    return {
      'max-height': '140px'
    }
  }

  private _getLineHeight(element, fontFamily, fontSize){
    let temp = document.createElement('span');
    temp.setAttribute('style','margin:0px;padding:0px;opacity:0;font-family:'+ fontFamily+';font-size:'+ fontSize);
    temp.innerHTML = 'test';
    temp = element.parentNode.appendChild(temp);
    const ret = temp.offsetHeight;
    temp.parentNode.removeChild(temp);
    return ret;
  }

}
