import { CATEGORY, SUBCATEGORY_HEADER, HEADER_CONTROLS, PROPERTY_SECTIONS } from './../controls-name-definition';
import {Component} from '@angular/core';
import {BaseControlComponent} from 'app/common/controls/base.component';
import {ControlDefinition} from 'app/common/decorators/control-definition';

@Component({
  selector: 'bs-title-bold-control',
  templateUrl: './title-bold-control.component.html',
  styleUrls: ['./title-bold-control.component.scss']
})
@ControlDefinition(
  {
    type: HEADER_CONTROLS.titleBold.type,
    category: CATEGORY.TEXT.displayName,
    subcategory: SUBCATEGORY_HEADER.displayName,
    displayName: HEADER_CONTROLS.titleBold.displayName,
    icon: HEADER_CONTROLS.titleBold.icon,
      styles: {
        fontFamily: {
          section: PROPERTY_SECTIONS.fonts.key,
          type: 'font-family',
          defaultValue: 'inherit'
        },
        fontSize: {
          section: PROPERTY_SECTIONS.fonts.key,
          type: 'font-size',
          defaultValue: '24px',
          max: 72
        },
        letterSpacing: {
          section: PROPERTY_SECTIONS.fonts.key,
          type: 'range',
          defaultValue: 0,
          min: 0,
          max: 60
        },
        align: {
          section: PROPERTY_SECTIONS.style.key,
          type: 'text-align',
          defaultValue: 'left'
        },
        fontColor: {
          section: PROPERTY_SECTIONS.fonts.key,
          type: 'color'
        },
        backColor: {
          section: PROPERTY_SECTIONS.background.key,
          type: 'color',
          defaultValue: 'rgba(0, 0, 0, 0)'
        },
        bold: {
          section: PROPERTY_SECTIONS.fonts.key,
          type: 'boolean',
          defaultValue: true
        }
      },
      parameters: {
        text: {
          type: 'title',
          defaultValue: HEADER_CONTROLS.titleBold.displayName
        }
      },
      defaultSize: {
        width: 280,
        height: 45
      }
  }
)

export class TitleBoldControlComponent extends BaseControlComponent {

  fontWeight() {
    const bold = this.styles.bold;
    return bold === true ? '600' : '100';
  }
}
