import { Component } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { Store, select } from '@ngrx/store';
import { getCurrentSite } from '../store/site/site.selector';
import { Subscription } from 'rxjs';
@Component({
  selector: 'bs-site-list-render',
  templateUrl: './site-list-render.component.html',
  styleUrls: ['./site-list-render.component.scss'],
})
export class SiteListRenderComponent {
  private _currentSiteSubscription:Subscription;
  private _site;

  constructor(private _router: Router, private _store: Store<IRenderState>) {
    this._currentSiteSubscription = this._store.pipe(select(getCurrentSite)).subscribe((site) => {
      this._site = site;
    })
  }

  async ngOnInit() {
    this._router.navigate([this._site && this._site.url ? this._site.url : '404']);
  }

  ngOnDestroy(){
    this._currentSiteSubscription?.unsubscribe();
  }
}
