import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Global } from 'app/common/misc/global';
import { UserService } from '../sites/user.service';
import { BizagiOAuthManager } from './bizagi-oauth-manager';
import {Cookie} from 'ng2-cookies/ng2-cookies';

@Injectable()
export class OAuthClientHelper {

  protected token: string;
  public loggedOn: boolean;
  protected type: string; // accounts | bizagi
  protected sso: boolean;
  private _redirectPrivateUrl: string;
  private _redirectPublicUrl: string;

  constructor(private _bizagiOauthManager: BizagiOAuthManager, private _httpClient: HttpClient,
    private _userService: UserService) {
  }

  public setRedirectHeader(privateUrl: string, publicUrl: string) {
    this._redirectPrivateUrl = privateUrl;
    this._redirectPublicUrl = publicUrl;
  }

  public async login(params: any, type: string) {
    params = params || {};
    const result = await this._bizagiOauthManager.initAndLogin(params);
    this.loggedOn = true;
    return result;
  }

  public logoutToUrl(isPrivatePage) {
    const pageToRedirect = isPrivatePage ? this._redirectPrivateUrl : this._redirectPublicUrl;
    if (pageToRedirect) {
      let url = escape(location.href.split('/#/')[0] + '/#/' + location.hash.split('/')[1]);
      url += '/' + pageToRedirect;
      this.logoutOtherUrl(url);
    } else {
      this.logout();
    }
  }


  public logout() {
    this._removeStorage();
    this._bizagiOauthManager.logout(true);
  }

  public logoutOtherUrl(url: string) {
    this._removeStorage();
    this._bizagiOauthManager.logout(true, url);
  }

  public getToken(): string {
    return this.token;
  }

  public async isLoggedOn() {
    if (!this.loggedOn) {
      const authenticatedUser = await this._httpClient.get<any>('api/oauth/authenticatedUser').toPromise();
      this.loggedOn = !!authenticatedUser;
      if (this.loggedOn) {
        this._bizagiOauthManager.setProfile(authenticatedUser.profile);
        this._bizagiOauthManager.setExpiration(authenticatedUser.expiration);
      }
    }
    return this.loggedOn;
  }

  public getUserEmail(): string {
    const profile = this.getProfile();
    return profile.email;
  }

  public getUsername(): string {
    const profile = this.getProfile();
    return profile?.username;
  }

  public getIsAdministrator(): boolean {
    const profile = this.getProfile();
    return profile.isAdministrator;
  }

  public hasEditorRole(): boolean {
    const profile = this.getProfile();
    return !!_.find(profile.roles, (role: { Name: string }) => {
      return role.Name === 'BASiteEditor';
    });
  }

  public getUserId(): string {
    const profile = this.getProfile();
    return profile.sub;
  }

  public getProfile() {
    const profile = this._bizagiOauthManager.getProfile();
    return profile;
  }

  public getExpiration() {
    return this._bizagiOauthManager.getExpiration();
  }

  public refreshToken(): any {
    return this._bizagiOauthManager.refreshToken();
  }

  public setToken(token) {
    this.token = token;
  }

  private _removeStorage() {
    sessionStorage.removeItem('userLoggedOn');
    if(Global.isInternetExplorer()) {
      Cookie.delete('USERLOGGEDON');
    }
  }
}
