import {Action} from '@ngrx/store';

export const fontActions = {
  LOAD_FONTS: '[Fonts Actions] Load fonts',
  LOAD_FONTS_SUCCESS: '[Fonts Actions] Load fonts success',
  PRE_SAVE_FONTS: '[Fonts Actions] Pre save fonts',
  SAVE_FONTS: '[Fonts Actions] Save fonts',
  SAVE_FONTS_SUCCESS: '[Fonts Actions] Save fonts success',
  SAVE_FONTS_FAILED: '[Fonts Actions] Save fonts failed',
  REMOVE_FONTS: '[Fonts Actions] Remove fonts',
  REMOVE_FONTS_SUCCESS: '[Fonts Actions] Remove fonts success',
  LOAD_STATIC_FONTS: '[Workspace Actions] Load static fonts',
  LOAD_STATIC_FONTS_SUCCESS: '[Workspace Actions] Load static fonts success'
};

export class LoadFonts implements Action {
  readonly type = fontActions.LOAD_FONTS;
  constructor(public payload?: string) { }
}

export class LoadFontsSuccess implements Action {
  readonly type = fontActions.LOAD_FONTS_SUCCESS;
  constructor(public payload: {}) { }
}

export class PreSaveFonts implements Action {
  readonly type = fontActions.PRE_SAVE_FONTS;
  constructor(public payload: Array<any>) { }
}

export class SaveFonts implements Action {
  readonly type = fontActions.SAVE_FONTS;
  constructor(public payload: Array<FormData>) { }
}

export class SaveFontsSuccess implements Action {
  readonly type = fontActions.SAVE_FONTS_SUCCESS;
  constructor(public payload: Array<any>) { }
}

export class SaveFontsFailed implements Action {
  readonly type = fontActions.SAVE_FONTS_FAILED;
  constructor(public payload: string) { }
}

export class RemoveFonts implements Action {
  readonly type = fontActions.REMOVE_FONTS;
  constructor(public payload: Array<string>) { }
}

export class RemoveFontsSuccess implements Action {
  readonly type = fontActions.REMOVE_FONTS_SUCCESS;
  constructor(public payload: Array<any>) { }
}

export class LoadStaticFonts implements Action {
  readonly type = fontActions.LOAD_STATIC_FONTS;
  constructor() {}
}

export class LoadStaticFontsSuccess implements Action {
  readonly type = fontActions.LOAD_STATIC_FONTS_SUCCESS;
  constructor(public payload?: any) {}
}

export type FontActions = LoadFonts
  | LoadFontsSuccess
  | SaveFonts
  | SaveFontsSuccess
  | SaveFontsFailed
  | RemoveFonts
  | RemoveFontsSuccess
  | LoadStaticFonts
  | LoadStaticFontsSuccess
  ;
