import {Component, ElementRef, Input} from '@angular/core';
import {Subscription} from 'rxjs';
import {select, Store} from '@ngrx/store';
import {Router} from '@angular/router';
import {Global} from '../../common/misc/global';
import {getCurrentPage} from '../store/page/page.selector';
import {getPages} from '../store/pages/pages.selector';
import {getCurrentSite} from '../store/site/site.selector';
import { getGrid } from '../store/grid/grid.selector';

interface IBreadCrumbPart {
  navigation: INavigationContext;
  page: IBasicPage;
}

@Component({
  selector: 'bs-breadcrumb-render',
  templateUrl: './breadcrumb-render.component.html',
  styleUrls: ['./breadcrumb-render.component.scss'],
})
export class BreadcrumbRenderComponent {
  @Input() navigationContexts: Array<INavigationContext>;
  @Input() hero: IControl;
  @Input() header: IControl;
  @Input() currentLanguage: ILanguageResource;
  public grid: IGrid;
  public top = 65;
  public pageNavigationList: Array<IBreadCrumbPart> = [];
  public currentPage: IPage;
  private _site: ISite;
  private gridSizeSubscription: Subscription;
  private _currentPageSubscription: Subscription;
  private _pagesSubscription: Subscription;
  private _currentSiteSubscription: Subscription;

  constructor(
    private _router: Router,
    private _store: Store<IRenderState>,
    private _elementRef: ElementRef
  ) {
  }

  ngOnInit() {
    this._currentPageSubscription = this._store.pipe(select(getCurrentPage)).subscribe((currentPage: IPageState) => {
      if (currentPage) {
        this.currentPage = currentPage;
       }
    });
    this._pagesSubscription = this._store.pipe(select(getPages)).subscribe((pages: Array<IBasicPage>) => {
      if (pages) {
        this.pageNavigationList = [];
        _.each(this.navigationContexts, (navigationContext: INavigationContext) => {
          const page = _.find(pages, { originalId: navigationContext.originPageId });
          if (this.currentLanguage.resources[page.displayName]) {
            page.displayName = this.currentLanguage.resources[page.displayName];
          }
          this.pageNavigationList.push({ navigation: navigationContext, page });
        });
      }
    });
    this._currentSiteSubscription = this._store.pipe(select(getCurrentSite)).subscribe(async (site: ISite) => {
      if (site) {
        this._site = site;
      }
    });

    this.gridSizeSubscription = this._store.pipe(select(getGrid)).subscribe((grid: IGrid) => {
      let boundariesSize = 0;
      const screenSize = window.innerWidth;
      if (screenSize > 1200) {
        boundariesSize = screenSize - 1200;
        grid.layoutSize = 1200;
      } else if (screenSize > 960) {
        boundariesSize = screenSize - 960;
      }
      $('.bs-breadcrumb-render__container .bs-breadcrumb-render').css('padding-left', boundariesSize / 2);
    });
  }

  ngAfterViewInit() {
    setTimeout(() => {
        this.top = Number(this.header.styles.height);
    });
  }

  ngOnDestroy() {
    this._pagesSubscription.unsubscribe();
    this._currentPageSubscription.unsubscribe();
  }

  public navigate(breadCrumbPart: IBreadCrumbPart) {
    const indexOfNavigation = _.findIndex(this.pageNavigationList, breadCrumbPart);
    const newNavigationContextList = _.slice(this.navigationContexts, 0, indexOfNavigation);
    if (newNavigationContextList.length) {
      this._router.navigate([this._site.url, breadCrumbPart.page.name], {
        queryParams: { context: Global.jsonToBase64(newNavigationContextList) },
      });
    } else {
      this._router.navigate([this._site.url, breadCrumbPart.page.name]);
    }
  }
}
