import { Component, OnInit, Input, EventEmitter, Output, HostListener } from '@angular/core';
import { InboxControlComponent } from 'app/common/controls/inbox-control/inbox-control.component';

@Component({
    selector: 'bs-inbox-control-grid-view',
    templateUrl: './inbox-grid-view.component.html',
    styleUrls: ['./inbox-grid-view.component.scss']
})

export class InboxControlGridViewComponent {
    public currentTooltipActivities = { caseId: '', open: false };
    public placeholderItems = [];

    @Input() pagination: IPagination;
    @Input() data: any;
    @Input() styles: any;
    @Output() orderByColumn: EventEmitter<string> = new EventEmitter();
    @Output() showCaseEvent: EventEmitter<string[]> = new EventEmitter();

    public orderByColumnName(columnName: string) {
        this.orderByColumn.emit(columnName);
    }

    public showTooltipActivities(open, row?) {
        if (row && row.workitems.length > 1) {
            this.currentTooltipActivities.caseId = row.caseNumber;
            this.currentTooltipActivities.open = open;
        } else {
            this.currentTooltipActivities.caseId = '';
            this.currentTooltipActivities.open = false;
        }
    }

    public showCase(caseId: string, taskId?: string, processName?: string, caseNumber?:string) {
        this.showCaseEvent.emit([caseId, taskId, processName, caseNumber]);
    }

    public getGridPlaceholderItems() {
        if ((this.pagination.currentPage + 1) === this.pagination.totalPages) {
            const placeholderSize = this.styles.displayedItems - this.data.length;
            if (placeholderSize && this.placeholderItems.length < placeholderSize) {
                this.placeholderItems = new Array(placeholderSize).fill({
                    processName: { type: 'title', label: 'DATA_PROPERTY_IMAGE' },
                    caseNumber: { type: 'bizagi-number', label: 'DATA_PROPERTY_TITLE' },
                    workitems: { type: 'workitem', label: 'DATA_PROPERTY_TEXT' },
                    creationDate: { type: 'date', label: 'DATA_PROPERTY_TEXT' },
                    solutionDate: { type: 'date', label: 'DATA_PROPERTY_TEXT' }
                });
            }
            return this.placeholderItems;
        } else {
            return [];
        }
    }

    @HostListener('document:click', ['$event'])
    public onClickDocument(event) {
        this.showTooltipActivities(false);
    }

}
