import { Component } from '@angular/core';
import { OAuthClientHelper } from '../../services/oauth/oauth-client.helper';
import { ActivatedRoute } from '@angular/router';

@Component({
  selector: 'bs-unauthorized',
  templateUrl: './unauthorized.component.html',
  styleUrls: ['./unauthorized.component.scss']
})
export class UnauthorizedComponent {

  public mode = 'editor';
  public url;

  constructor(private _oAuthClient: OAuthClientHelper, private route: ActivatedRoute) { 
    const routeSnapshot = this.route.snapshot;
    const queryParams = routeSnapshot.queryParams;
    console.log(queryParams);
    if(queryParams.mode){
      this.mode = queryParams.mode;
      this.url = queryParams.url;
    }
  }

  public goBack() {
    if(this.mode === 'render'){
      this._oAuthClient.logoutOtherUrl(`${encodeURIComponent(this.url)}`);
    } else{
      this._oAuthClient.logoutOtherUrl(`${encodeURIComponent(location.origin + location.pathname + '#')}`);
    }
  }
}
