import { getLoadingRender } from './../../store/loading/loading.selector';
import {
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  Component,
  ComponentFactoryResolver,
  EventEmitter,
  Input,
  Output,
  ReflectiveInjector,
  ViewChild,
  ViewContainerRef,
} from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { PageRenderingService } from 'app/common/services/sites/page-rendering.service';
import { ActivatedRoute, Router } from '@angular/router';
import { BizagiDataService } from 'app/render/services/bizagi-data.service';
import { ControlsService } from 'app/common/services/sites/controls.service';
import { CultureSettingsService } from 'app/common/services/sites/culture-settings.service';
import { BaseDataControlComponent } from 'app/common/controls/base-data.component';
import { BaseProcessControlComponent } from 'app/common/controls/base-process.component';
import { select, Store } from '@ngrx/store';
import { Subscription, Subject } from 'rxjs';
import { BaseSearchComponent } from '../../../common/controls/base-search.component';
import { StateSnapshotService } from '../../../common/services/sites/state-snapshots.service';
import { getSiteLanguage, getLastBpmLanguage } from '../../store/multilanguage/multilanguage.selector';
import { getPageContext } from '../../store/page/page.selector';
import { getPageData } from '../../store/page-data/page-data.selector';
import { getSiteUser } from 'app/render/store/user/user.selector';
import { BaseInboxControlComponent } from '../../../common/controls/base-inbox.component';
import { debounceTime, share, takeUntil } from 'rxjs/operators';
import { EControlDataSource } from 'app/editor/models/EControlDataSource';

// TODO: This and grid.panel.cell.component are almost the same, we nned to find a way to remove duplicate code
@Component({
  selector: 'bs-grid-cell-renderer',
  templateUrl: './grid-cell-render.component.html',
  styleUrls: ['./grid-render.component.scss'],
  changeDetection: ChangeDetectionStrategy.Default,
})
export class GridCellRenderComponent {
  @Input() currentLanguage: ILanguageResource;
  @ViewChild('gridrendercell', { read: ViewContainerRef, static: true }) viewContainerRef: ViewContainerRef;
  @Output() loadingWebparts: EventEmitter<any> = new EventEmitter();

  public onSetCurrentControl: EventEmitter<IControl> = new EventEmitter<IControl>();
  public currentComponent;
  public loading: any;
  public showCustomLoader = false;
  private _currentLanguageSubscription: Subscription;
  private _pageContextSubscription: Subscription;
  private _pageDataSubscription: Subscription;
  private _lastBPMLanguageSubscription: Subscription;
  private _loadingSubscription: Subscription;
  private unsubscribeCurrentUser$: Subject<void> = new Subject<void>();

  @Input() set control(control: IControl) {
    if (!control) {
      return;
    }
    // Inputs need to be in the following format to be resolved properly
    const inputProviders = Object.keys(control).map(inputName => {
      return { provide: inputName, useValue: control[inputName] };
    });
    const resolvedInputs = ReflectiveInjector.resolve(inputProviders);

    // We create an injector out of the data we want to pass down and this components injector
    const injector = ReflectiveInjector.fromResolvedProviders(resolvedInputs, this.viewContainerRef.parentInjector);

    // We create a factory out of the component we want to create
    const controlComponent = this._controlsServices.getControlComponentByType(control.type);
    const factory = this._resolver.resolveComponentFactory(controlComponent);

    // We create the component using the factory and the injector
    const component = factory.create(injector);

    // We insert the component into the dom container
    this.viewContainerRef.insert(component.hostView);

    // We can destroy the old component is we like by calling destroy
    if (this.currentComponent && this.currentComponent.destroy) {
      this.currentComponent.destroy();
    }

    this.currentComponent = component.instance;

    if (this.currentComponent instanceof BaseDataControlComponent
      || this.currentComponent instanceof BaseInboxControlComponent) {
      this.currentComponent.setStore(this._store);
      this.currentComponent.setServices(
        this._route,
        this._router,
        this._translateService,
        this._bizagiDataServices,
        this._pageRenderingService
      );
    } else if (this.currentComponent instanceof BaseProcessControlComponent) {
      this.currentComponent.setServices(this._pageRenderingService, this._bizagiDataServices);
    } else if (this.currentComponent instanceof BaseSearchComponent) {
      this.currentComponent.setStore(this._store);
    }


    this.currentComponent.setBaseServices(this._stateSnapshots, null, this._cultureService);

    // Set the control information
    this.currentComponent.enableRenderingMode();
    this.currentComponent.setCurrentLanguage(this.currentLanguage);
    this.currentComponent.setControl(control, this._changeDetectorRef);
    this.currentComponent.loadParameters(control.parameters, control.parametersBinding);
    this._startSupscriptions(control);
  }

  constructor(
    private _resolver: ComponentFactoryResolver,
    private _controlsServices: ControlsService,
    private _bizagiDataServices: BizagiDataService,
    private _route: ActivatedRoute,
    private _router: Router,
    private _changeDetectorRef: ChangeDetectorRef,
    private _pageRenderingService: PageRenderingService,
    private _stateSnapshots: StateSnapshotService,
    private _translateService: TranslateService,
    private _store: Store<IRenderState>,
    private _cultureService: CultureSettingsService
  ) { }

  ngOnInit() {

    this._pageDataSubscription = this._store
      .pipe(select(getPageData))
      .subscribe((pageData: any) => {
        if (pageData) {
          this.currentComponent.setBoundParameters(pageData);
        }
      });

    const currentUser = this._store.pipe(debounceTime(300), select(getSiteUser), share());

    if (this.currentComponent instanceof BaseDataControlComponent) {
      currentUser.pipe(takeUntil(this.unsubscribeCurrentUser$.asObservable()))
        .subscribe(() => {
          this.currentComponent.refreshSubject.next();
        })
    }

    if (this.currentComponent instanceof BaseInboxControlComponent) {
      this._lastBPMLanguageSubscription = this._store
        .pipe(debounceTime(300), select(getLastBpmLanguage))
        .subscribe((lastBpmLanguage: string) => {
          if (lastBpmLanguage) {
            this.currentComponent.refreshSubject.next(this.loading);
          }
        });

      currentUser.pipe(takeUntil(this.unsubscribeCurrentUser$.asObservable()))
        .subscribe(() => {
          this.currentComponent.refreshSubject.next(this.loading);
        });
    }

    if (this.currentComponent instanceof BaseInboxControlComponent ||
      this.currentComponent instanceof BaseDataControlComponent ||
      this.currentComponent instanceof BaseProcessControlComponent) {
      this._loadingSubscription = this._store.pipe(select(getLoadingRender))
        .subscribe((loading: any) => {
          this.loading = loading;
          if (this.currentComponent instanceof BaseInboxControlComponent ||
            this.currentComponent.control?.parameters?.data?.dataSource === EControlDataSource.Dynamic ||
            this.currentComponent.link?.type === 'processes') {
            this.currentComponent.isProcessLoading = this.loading.loadingWebparts;
            this.showCustomLoader = true;
          }
        })
    }

  }

  ngOnDestroy() {
    this._pageDataSubscription?.unsubscribe();
    this._pageContextSubscription?.unsubscribe();
    this._currentLanguageSubscription?.unsubscribe();
    this._lastBPMLanguageSubscription?.unsubscribe();
    this._loadingSubscription?.unsubscribe();
    this.unsubscribeCurrentUser$?.next();
    this.unsubscribeCurrentUser$?.unsubscribe();
  }

  public setCurrentControl(control: IControl) {
    this.onSetCurrentControl.emit(control);
  }

  private _startSupscriptions(control: IControl) {
    // Current language state suscription
    this._currentLanguageSubscription = this._store.pipe(select(getSiteLanguage))
      .subscribe((currentLanguage: ILanguageResource) => {
        this.currentComponent.setCurrentLanguage(currentLanguage);
        // Load parameters
        this.currentComponent.loadParameters(control.parameters, control.parametersBinding);
        if (
          this.currentComponent.controlDefinition.category === 'bundle' ||
          this.currentComponent.controlDefinition.subcategory === 'Inbox'
        ) {
          if (location.href.indexOf('parameters.filter') < 0) {
            this.currentComponent.refreshSubject.next();
          }
          if (location.href.indexOf('parameters.filter') >= 0 && location.href.indexOf(this.currentComponent.control.id) < 0) {
            this.currentComponent.refreshSubject.next();
          }
        }
      });

    if (this.currentComponent instanceof BaseDataControlComponent) {
      this._pageContextSubscription = this._store
        .pipe(select(getPageContext))
        .subscribe(() => {
          if (location.href.indexOf('parameters.filter') < 0) {
            this.currentComponent.refreshSubject.next();
          }
          if (location.href.indexOf('parameters.filter') >= 0 && location.href.indexOf(this.currentComponent.control.id) < 0) {
            this.currentComponent.refreshSubject.next();
          }
        });
    }
    if (this.currentComponent instanceof BaseSearchComponent) {
      this.currentComponent.setServices(this._pageRenderingService);
    }

  }
}
