import { INITIAL_STATE } from './initial-state';
import { ActionReducerMap } from '@ngrx/store';
import {multilanguageReducer} from './multilanguage/multilanguage.reducer';
import {gridReducer} from './grid/grid.reducer';
import {navigationContextsReducer} from './navigation-contexts/navigation-contexts.reducer';
import {pageContextReducer} from './page-context/page-context.reducer';
import {pageReducer} from './page/page.reducer';
import {siteReducer} from './site/site.reducer';
import {pagesReducer} from './pages/pages.reducer';
import {pageDataReducer} from './page-data/page-data.reducer';
import {userReducer} from './user/user.reducer';
import {loadingReducer} from './loading/loading.reducer';
import {sessionReducer} from './session/session.reducer';

export const INITIAL_STATE_STORE: IRenderState = INITIAL_STATE;

export const ROOT_REDUCER: ActionReducerMap<IRenderState> = {
  multilanguage: multilanguageReducer,
  grid: gridReducer,
  navigationContexts: navigationContextsReducer,
  pageContext: pageContextReducer,
  currentPage: pageReducer,
  currentSite: siteReducer,
  pages: pagesReducer,
  pageData: pageDataReducer,
  user: userReducer,
  loading: loadingReducer,
  session: sessionReducer
};
