import {Injectable} from '@angular/core';
import {GlobalConstants} from '../../misc/global-constants';


@Injectable()
export class GridService {


  public getContainerHeight() {
      const nodes = $('.bs-grid-panel-control.bs-ctrl') as any;
      const hero = $('.bs-ctrl.bs-ctrl--hero');
      const footer = $('.bs-footer');
      const maxNodes = _.reduce(nodes, function(node, n) { return Math.max(node, $(n).position().top + $(n).height()); }, 0);
      let maxHero = 0;
      if (hero.length) {
        maxHero = hero.position().top + hero.height() - (footer.length ? footer.height() : 0);
      }
      return maxNodes > maxHero ? maxNodes : maxHero;
  }


  public adjustContainerResolution(container: any) {
      const windowWidth = $(window).width();
      const containerHeight = this.getContainerHeight();

      if (windowWidth > 1200) {
        container.css({zoom: 1, width: GlobalConstants.GRID_PANEL_SIZE});
        container.css({left: ((windowWidth - container.width()) / 2)});
        container.parent().height(containerHeight);
      } else if (windowWidth < 1200 && windowWidth > 769){
        container.css({zoom: 0.8, width: 9760});
        container.css({left: ((windowWidth - container.width()) / 2)});
        container.parent().height(containerHeight - (containerHeight * 0.2));
      } else {
        container.css({zoom: 0.7, width: 1200});
        container.css({left: ((windowWidth - 9000) / 2)});
        container.parent().height(containerHeight - (containerHeight * 0.2));
      }
  }


}
