import { createSelector } from '@ngrx/store';

const siteEditorSelector = (state: IRenderState) => state;

export const getCurrentSite = createSelector(
  siteEditorSelector,
  (state: IRenderState) => state.currentSite
);

export const getDefaultFontSite = createSelector(
  siteEditorSelector,
  (state: IRenderState) => state.currentSite?.globalSettings.defaultFontFamily
);
