import { BaseDataControlComponent } from 'app/common/controls/base-data.component';
import { Dictionary } from 'lodash';
import { BaseControlComponent } from './base.component';
import { ActivatedRoute, Router } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { PageRenderingService } from '../services/sites/page-rendering.service';
import { ESearchType } from '../../editor/models/ESearchType';

declare let Highcharts: any;
declare let $: any;

export class BaseSearchComponent extends BaseControlComponent {
  public categories = [];
  protected actions;
  protected pageRenderingService: PageRenderingService;

  constructor() {
    super();
  }
  public setServices(pageRenderingService?: PageRenderingService) {
    this.pageRenderingService = pageRenderingService;
  }



  protected async loadCategories() {
    let categories = [];
    const action = this.actions['onSearch'];
    if (action.type === ESearchType[ESearchType.changeProperty]) {
      const controls = this.stateSnapshotService.getPage().controls;
      _.forEach(controls, (control: IControl) => {
        if (action.targets[control.id]) {
          const fieldName = _.find(action.targets[control.id], (internalTarget) => {
            return internalTarget.property === 'parameters.filter.category';
          }).xpath;

          categories = [...categories, ..._.map(control.parameters.data.fixed, (collectionItem) => {
            return collectionItem[fieldName];
          })];
        }
      });
    } else {
      const targets = action.targets;
      const site = this.stateSnapshotService.getSite();
      const controlXpaths = {};
      _.forEach(targets, (value, key) => {
        const fieldName = _.find(value, (internalTarget) => {
          return internalTarget.property === 'parameters.filter.category';
        }).xpath;
        controlXpaths[key] = fieldName;
      });
      categories = await this.pageRenderingService.getControlCategories(site.url,
        site.references[action.pageId], controlXpaths);
    }
    this.categories = _.uniq(_.filter(_.map(categories, (category: string) => {
      return this._currentLanguageResources[category]?.trim();
    }), (filter) => !!filter));

  }

}
