import {PageDataActions, pageDataActions, SetPageData} from './page-data.actions';

export function pageDataReducer(
  state: IPage = null,
  action: PageDataActions
): IPage {
  switch (action.type) {
   case pageDataActions.SET_PAGE_DATA: {
      return {
        ...state,
       ...(action as SetPageData).payload
      };
    }


    default:
      return state;
  }
}
