import {Component, Input, Output, EventEmitter, ElementRef} from '@angular/core';
import { Global } from 'app/common/misc/global';

@Component({
  selector: 'bs-my-cases-control-card',
  templateUrl: './my-cases-control-card.component.html',
  styleUrls: ['./my-cases-control-card.component.scss']
})
export class MyCasesControlCardComponent {
  @Output() showCaseEvent: EventEmitter<any> = new EventEmitter();
  @Input() data;
  @Input() dataNoShow;
  @Input() stylesComponent;
  public isRenderMode = Global.isOnRenderMode();

  constructor(private _element: ElementRef) { }

  ngAfterViewInit(): void {
    if (Global.isFirefox()) {
      $(this._element.nativeElement).
        parents('.bs-my-cases-body').css('max-height', 'calc(100% - 100px)');
    }
  }

  public getTitleFontSize() {
    const fontSize: string = this.stylesComponent['contentFontSize'];
    const size = +fontSize.substring(0, fontSize.indexOf('px'))
    return (size + 2) + 'px';
  }

  public getKey(values, index) {
    return Object.keys(values)[index];
  }

  public getEntries(data) {
    const result = Object.entries(data);
    return result;
  }

  public isAllowValue(text) {
    return text !== 'processName' || text !== 'caseNumber';
  }

  public getDisplayName(headers: any[], key: string) {
    return headers.find(header => header.key === key).displayName;
  }

  public selectCard(process) {
    this.showCaseEvent.emit(process);
  }

}
