import { ModalWebPartsComponent } from 'app/common/components/modal-webparts/modal-webparts.component';
import { Component, ElementRef, Input, ViewChild } from '@angular/core';
import { ThemeService } from 'app/common/services/sites/theme.service';
import { OAuthClientHelper } from '../../common/services/oauth/oauth-client.helper';
import { BizagiDataService } from '../services/bizagi-data.service';
import { select, Store } from '@ngrx/store';
import { getSiteLanguage } from '../store/multilanguage/multilanguage.selector';
import { Subscription, Subject, timer, Observable } from 'rxjs';
import { ConfirmDialogModalComponent } from 'app/common/components/confirm-dialog-modal/confirm-dialog-modal.component';
import { BzgModalService, BzgModalRef } from 'bz-genome-angular';
import { UpdateLastBPMLanguage } from '../store/multilanguage/multilanguage.actions';
import { TranslateService } from '@ngx-translate/core';
import { SessionHandlerService } from 'app/common/services/oauth/session-handler.service';
import { UpdateLoading } from '../store/loading/loading.actions';
import { SelectOrganizationModalComponent } from './select-organization-modal/select-organization-modal.component';
import { getSiteUser } from '../store/user/user.selector';
import { debounceTime } from 'rxjs/operators';

enum FormType {
  New,
  Existing,
}


@Component({
  selector: 'bs-bizagi-forms',
  templateUrl: './bizagi-forms.component.html',
  styleUrls: ['./bizagi-forms.component.scss'],
})

class BizagiFormsComponent {
  @Input() connection: string;
  @Input() modalTitle: string;
  @Input() type: FormType;
  @Input() parameters: any;
  @Input() site: ISite;
  @Input() currentLanguage: ILanguageResource;
  @Input() currentPage: IPage;

  public CASE_CLOSED = 'Workitem is Closed';
  public CASE_CANCELED = 'Workitem is Canceled';
  public NO_PENDING_ACTIVITIES = 'No pending activities';
  public showGoDown = false;
  public showIframe: boolean;
  public formTitle = {process: '', activityName: '', caseNumber: ''};
  public currentWorkitem: { id: string; taskName: string; index: number } = { id: '', taskName: '', index: -1 };
  public workItems: { id: string; taskName: string, dueDate: string }[] = [];
  public infoMessage = '';
  public errorMessage = '';
  public showLoading = false;
  public tabSelected = 0;
  public isLastActionNext = false;
  public showLoadFormButton = false;
  public globalFormLoaded = false;
  public isModalOn = false;
  public closing = false;
  public showWorkItems = false;
  public showActivitiesSelector = false;
  public organizationSelected;
  public multipleOrganization = false;
  public iframeType = '';

  private _summaryLoaded = false;
  private _loadformCase = false;
  private _idCase: any;
  private _onCloseFunction: Function;
  private iframeReceiveFunction: any;
  private iframeFakeReceiveFunction: any;
  private targetOrigin: string;
  private webpartTheme: any;
  private _defaultFonts: Blob[] = [];
  private _currentLanguageSubscription: Subscription;
  private _currentUserSubscription: Subscription;
  @ViewChild('modal', { static: true })
  private modal: ModalWebPartsComponent;
  private _modalRef: BzgModalRef;
  private _modalRefAuthorizationError: BzgModalRef;
  private _iframeFakeLoaded = false;
  private _iframeResponse;
  private _languageChanged = false;
  private _user;
  private _fakeIframe;
  private _actualProcess: number = null;
  private iframfeInitialized: Subject<string> = new Subject<string>();
  private _loadingIframe = false;
  private _pendingCase = false;
  private _initialLoading = true;
  private _languageError = false;

  constructor(
    private _elementReference: ElementRef,
    private _themeServices: ThemeService,
    private _oAuthClient: OAuthClientHelper,
    private _bizagiDataService: BizagiDataService,
    private _modalService: BzgModalService,
    private _store: Store<IRenderState>,
    private _translateService: TranslateService,
    private _sessionHandlerService: SessionHandlerService
  ) {
    this.iframfeInitialized.asObservable().pipe(
      debounceTime(100))
      .subscribe(data => {
        if(this._pendingCase){
          this.openIFrame(this.iframeType);
      }
    });
  }

  ngOnInit() {
    this._store.dispatch(new UpdateLoading({ loadingWebparts: true }));
    this._currentLanguageSubscription = this._store.pipe(select(getSiteLanguage)).subscribe((siteLanguage: ILanguageResource) => {
      if (siteLanguage) {
        if(!this._initialLoading) {
          this._store.dispatch(new UpdateLoading({ loadingWebparts: true }));
        }
        this.currentLanguage = siteLanguage;
        this.targetOrigin = this.targetOrigin || document.location.origin;
        if (this.targetOrigin || this.connection) {
          const iframe: any = this.getIframeFake();
          this.sendMessageToIframe(iframe, 'set-custom-language', this._getLanguageName(siteLanguage.name));
          this._store.dispatch(new UpdateLoading({ loadingChangeBPMLanguage: true }));
        }
      }
    });
    this._initialLoading = false;
    this._currentUserSubscription = this._store.pipe(select(getSiteUser)).subscribe((user) => {
      if (user) {
        this._user = user;
        this._sessionHandlerService.startSessionSubject.next();
      }
      this.setIframeFakeLanguage();
    });

    if (this.currentPage?.isPrivate || this._user) {
      this._sessionHandlerService.startSessionSubject.next();
    }
  }


  ngOnDestroy() {
    this.destroyIFrameCommunication();
    this.destroyIFrameFakeCommunication();
    this._currentUserSubscription?.unsubscribe();
    this._currentLanguageSubscription?.unsubscribe();
  }

  public show(fonts: Blob[], process?: number) {
    this._actualProcess = process;
    this._defaultFonts = fonts;
    this._initialize();
    this.currentWorkitem = { id: '', taskName: '', index: -1 };
    this.modalTitle = this.currentLanguage.resources[this.modalTitle] || this.modalTitle;
    this.formTitle = {process: '', activityName: '', caseNumber: ''};
    const titleInput = this.modalTitle.split('/');
    if (titleInput.length === 2) {
      this.formTitle.process = titleInput[0];
      this.formTitle.caseNumber = titleInput[1];
    }

    this.selectedTab(0, true, true);
    this.showInFoMessage();
  }

  private _getLanguageName(languageName) {
    if (languageName.split('-').length > 2) {
      languageName = languageName.split('-')[0] + '-' + languageName.split('-')[2];
    }
    return languageName;
  }

  private _initialize() {
    this.multipleOrganization = false;
    this.organizationSelected = 0;
    this.isLastActionNext = false;
    this.showGoDown = false;
    this.infoMessage = '';
    this.errorMessage = '';
    this.closing = false;
    this._loadformCase = false;
    this.showActivitiesSelector = false;
    this.globalFormLoaded = false;
    this._iframeResponse = null;
  }

  public showInFoMessage() {
    if (this.parameters.control === 'my-cases') {
      if (this.parameters.caseState === 'Completed') {
        this.infoMessage = this.CASE_CLOSED;
        return true;
      }
      if (this.parameters.caseState === 'Aborted') {
        this.infoMessage = this.CASE_CANCELED;
        return true;
      }
      if (this.parameters.caseState === 'Running' && !this.parameters.pendingActivities && !this.currentWorkitem?.id) {
        this.infoMessage = this.NO_PENDING_ACTIVITIES;
        return true;
      }
    }
    return false;
  }

  public registerCloseFunction(close: Function) {
    this._onCloseFunction = close;
  }

  public onCloseForm() {
    if (this.tabSelected === 0 && this.showIframe === true && this.showLoading === false && !this.globalFormLoaded &&
      (this.formTitle.process || this.formTitle.activityName !== '' || this.formTitle.caseNumber !== '') &&
      (!this.parameters.caseState || (this.parameters.caseState && this.parameters.caseState !== 'Completed'))) {
        this.sendMessageToIframe(this.getIframe(), 'check-pending-changes');
        this.closing = true;
    } else {
      this.closeForm();
    }
  }

  private closeForm() {
    this.sendMessageToIframe(this.getIframe(), 'dispose-form', null);
    this.modal.closeModal();
    this._onCloseFunction(this.isLastActionNext);
    $('.bs-render').attr('style', 'overflow-y: auto');
    this.destroyIFrameCommunication();
    this._sessionHandlerService.unsubscribeTimerSessionBPM();
  }

  public goDown() {
    const iframe = this.getIframe();
    this.sendMessageToIframe(iframe, 'go-down', 'go-down');
  }

  public onChangeCaseActivity(button: string, itemIndex = null) {
    const index = this.workItems.findIndex(item => {
      return item.id === this.currentWorkitem.id;
    });
    if(itemIndex !== null) {
      this.currentWorkitem = this._getCurrentWorkitem(itemIndex);
    } else {
      let newIndex: number;
      switch (button) {
        case 'next':
          newIndex = index + 1;
          this.currentWorkitem = this._getCurrentWorkitem(newIndex);
          break;
        case 'previous':
          newIndex = index - 1;
          this.currentWorkitem = this._getCurrentWorkitem(newIndex);
          break;
      }
    }
    const iframe = this.getIframe();
    const message = {};
    if(this._iframeResponse?.workItemsResponse.length) {
      // when there is a subprocess, the idCase is taken from it,
      // so is necessary to change it for the parent-process taking it from pending workitems
      message['idCase'] = this._iframeResponse?.workItemsResponse[0].idCase;
    }
    if(! message['idCase']) {
      message['idCase'] = this.parameters['case']? this.parameters['case'] : this._idCase;
    }
    message['idWorkItem'] = this.currentWorkitem.id;

    if (this.parameters.control === 'my-cases') {
      this.parameters['idTask'] = this.currentWorkitem.id;
    }
    this._loadformCase = true;
    this.showLoading = true;
    this.sendMessageToIframe(iframe, 'load-form-case', message);
  }

  private getIframe() {
    const element = this._elementReference.nativeElement;
    return $('#bs-bizagi-process', element);
  }

  private getIframeFake() {
    const element = this._elementReference.nativeElement;
    return $('#bs-bizagi-process-fake', element);
  }

  private getSummaryIframe() {
    const element = this._elementReference.nativeElement;
    return $('#bs-bizagi-process-summary', element);
  }

  public selectedTab(selectedTab, clicked = false, firstTime = false) {
    this._summaryLoaded = false;
    this.closing = false;
    if (!firstTime && selectedTab === 1 && this.parameters.caseState !== 'Completed'
      && this.showIframe === true && this.showLoading === false && !this.globalFormLoaded) {
      const iframe = this.getIframe();
      this.sendMessageToIframe(iframe, 'check-pending-changes');
    } else {
      this.chnageTabOperation(selectedTab, clicked, firstTime);
    }
  }

  private async chnageTabOperation(selectedTab, clicked, firstTime) {
    const type = this.getWebPartType();
    this.showLoading = false;
    this.showLoadFormButton = false;
    this.destroyIFrameCommunication();
    this.infoMessage = '';
    if (firstTime && this.showInFoMessage()) {
      selectedTab = 1;
    }
    this.tabSelected = selectedTab;

    switch (selectedTab) {
      case 0:
        if (this._user?.isMultiOrganization && this._user.sOrganizations?.length > 1 && this.type === FormType.New ) {
          this.multipleOrganization = true;
          this.openSelectOrganizationModal(this._user.sOrganizations, type, clicked);
        } else {
          this.openIFrame(type);
          this.showInFoMessage();
        }
        break;
      case 1:
        this.openIFrame('casesummary'); // summary form
        break;
    }
  }


  private openSelectOrganizationModal(organizations, type, clicked) {
    this.isModalOn = true;
    _.each(this._modalService.openModals, (modal) => {
      modal.close();
    });
    this._modalRef = this._modalService.open(SelectOrganizationModalComponent, {
      backdropClass: 'cdk-overlay-dark-backdrop-custom',
      maxWidth: '700px',
      closeOnBackdropClick: false,
      closeOnEscape: false,
      data: {
        organizations
      }
    });
    this._modalRef.afterClosed().subscribe(result => {

      if (result?.type === 'confirm' && result.params.organizationId) {
        this.organizationSelected = result.params.organizationId;
        this.openIFrame(type);
        this.showInFoMessage();
      }
      this.isModalOn = false;
    });
  }


  private openVerifyModal() {
    this.isModalOn = true;
    _.each(this._modalService.openModals, (modal) => {
      modal.close();
    });
    this._modalRef = this._modalService.open(ConfirmDialogModalComponent, {
      backdropClass: 'cdk-overlay-dark-backdrop-custom',
      maxWidth: '500px',
      closeOnBackdropClick: false,
      closeOnEscape: false,
      data: {
        title: 'siteeditor.formsettings.TITLE_SAVE_FORM',
        message: 'siteeditor.formsettings.MESSAGE_SAVE_FORM',
        cancelButtonText: 'siteeditor.global.CANCEL',
        confirmButtonText: 'siteeditor.global.SAVE',
        optionalButtonText: 'siteeditor.formsettings.DONT_SAVE_FORM_BUTTON',
        cancelButton: true,
        optionalButton: true,
        icon: 'icon-warning',
        iconColor: 'warning'
      }
    });
    this._modalRef.afterClosed().subscribe(result => {
      const iframe = this.getIframe();

      if (result?.type) {
        switch (result.type) {
          case 'confirm':
            this.sendMessageToIframe(iframe, 'save-form');
            break;
          case 'optional':
            this.sendMessageToIframe(iframe, 'without-pending-changes');
            break;
        }
      } else {
        this.tabSelected = this.tabSelected === 1  && !this.closing ? 0 : this.tabSelected;
      }

      this.isModalOn = false;
    });
  }

  private _loadInitialIframes() {
    this._loadingIframe = true;
    const sumaryIframe =  this.getSummaryIframe();
    const formIframe = this.getIframe();
    if (this.currentPage?.isPrivate || this._user) {
      sumaryIframe.attr('src', `api/sites/private/webparts?type=casesummary&remoteServer=${document.location.origin}&application=NEWWORKPORTAL&mashup=false&sitesContext=true`);
      formIframe.attr('src', `api/sites/private/webparts?type=activityform&remoteServer=${document.location.origin}&application=NEWWORKPORTAL&mashup=false&sitesContext=true&idCase=-1`);
    } else {
      formIframe.attr('src', `api/sites/webparts?type=activityform&remoteServer=${document.location.origin}&application=NEWWORKPORTAL&mashup=false&sitesContext=true&idCase=-1`);
      sumaryIframe.attr('src', `api/sites/webparts?type=casesummary&remoteServer=${document.location.origin}&application=NEWWORKPORTAL&mashup=false&sitesContext=true`);
    }
  }


  private async openIFrame(type: string) {
    const iframe: any = type === 'casesummary' ? this.getSummaryIframe() : this.getIframe();
    this.iframeType = type;
    if (this._loadingIframe) {
      this._pendingCase = true;
      this.showLoading = true;
      this.modal.open();
      return;
    } else {
      this._pendingCase = false;
    }
    this.showIframe = false;
    this.showGoDown = false;
    this.showLoading = true;
    this._themeServices.refreshThemeVariables();
    this.webpartTheme = this._themeServices.getWebpartThemeVariables();
    let connection = this.connection;
    if (!connection.endsWith('/')) {
      connection += '/';
    }
    // Create target origin
    const a = document.createElement('a');
    a.href = this.connection;
    this.targetOrigin = document.location.origin;
    this._sessionHandlerService.initializeIframe(this.showIframe, this.targetOrigin || this.connection,
      this._elementReference, this._iframeFakeLoaded);
    if (this._iframeFakeLoaded && this.currentPage?.isPrivate) {
        const newToken = await this._sessionHandlerService.refreshToken();
        this._sessionHandlerService.unsubscribeTimer();
        this._sessionHandlerService.startTimerSession(newToken.expiration);
    }
    const hasURL = iframe.attr('src')?.trim()?.length > 0;
    if (!hasURL) {
      if (this.currentPage?.isPrivate || this._user) {
        iframe.attr('src', `api/sites/private/webparts?type=${type}&remoteServer=${document.location.origin}&application=NEWWORKPORTAL&mashup=false&sitesContext=true&${(await this.getWebPartParameters())}`);
      } else {
        iframe.attr('src', `api/sites/webparts?type=${type}&remoteServer=${document.location.origin}&application=NEWWORKPORTAL&mashup=false&sitesContext=true&${(await this.getWebPartParameters())}`);
      }
    } else {
      this.sendPostMessagesToIframe(iframe);
      this.sendMessageToIframe(iframe, 'theme-initialize', this.webpartTheme);
      if (type === 'activityform') {
        let iframeCase = iframe.attr('src').substr(iframe.attr('src').indexOf('idCase'))?.split('=');
        iframeCase = iframeCase.length > 0 ? iframeCase[1] : '';
        const currentCase = this.parameters['case'];
        if (currentCase.toString() !== iframeCase?.toString() || this.parameters.control === 'my-cases') {
          const curCase = this.parameters['case'];
          this.openNewProcess(curCase);
        }
      }
      if (type === 'casesummary') {
        this.sendMessageToIframe(iframe, 'reload-sumary', {idCase: this.parameters['case']});
      }
    }

    this.modal.open();
    // for IExplorer scroll
    $('.bs-render').attr('style', 'overflow-y: hidden');

    // Receive message from iframe to finish the load
    this.receiveMessageFromIframe(async data => {
      if (data && !data.event) {
        try {
          data = JSON.parse(data);
        } catch (e) { }
        data.event = data.postMessageEventName ? data.postMessageEventName.split('.')[0] : '';
      }
      if (data.event && data.event !== 'ajax-call') {
        console.log('------- MESSAGE IN -------', data.event, data.message);
      };
      switch (data.event) {
        case 'session-timeout':{
          if(data?.message?.sessionTimeout){
            this._sessionHandlerService.startTimerBPMSession(data?.message.sessionTimeout);
          }
          break;
        }
        case 'load-complete':
          this.sendMessageToIframe(iframe, 'remove-bizagi-themes', {});
          this.showIframe = true;
          this._sessionHandlerService.showIframe = true;
          if (type === 'casesummary' && this._summaryLoaded) {
            setTimeout(() => {
              this.showLoading = false;
              this.showIframe = true;
              this._sessionHandlerService.showIframe = true;
            }, 500);
          }
          if (type === 'activityform') {
            const currentCase = this.parameters['case'];
            let iframeCase = iframe.attr('src').substr(iframe.attr('src').indexOf('idCase'))?.split('=');
            iframeCase = iframeCase.length > 0 ? iframeCase[1] : '';
            if (currentCase.toString() !== iframeCase.toString()) {
              this.openNewProcess(currentCase);
            }

          }
          break;
        case 'sumary-rendered':
          this._summaryLoaded = true;
          this.showLoading = false;
          this.showIframe = true;
          break;

        case 'executing-rendering':
          this.showLoading = true;
          break;
        case 'next-applied':
          this.formTitle.activityName = '';
          this.checkActivitiesLoadCase(data, iframe, true);
          this.showLoadGlobalform(data);
          break;
        case 'form-rendered':
          this._idCase = data.message?.summaryResponse?.idCase;
          this._iframeResponse = data.message;
          this._actualProcess = data.message?.summaryResponse?.idWorkFlow || null;
          if (type !== 'casesummary' && !data.message?.activityName) {
            this.checkActivitiesLoadCase(data, iframe);
            this.showLoadGlobalform(data);
          } else {
            this.showActivitiesSelector = false;
          }
          this.sendMessageToIframe(iframe, 'remove-bizagi-themes', {});
          this._loadWebpartDefaultFont();
          this._updateFormTitle(data);

          // this avoid the form load with default bizagi theme
          if (data.message?.activityName || type === 'casesummary' || data.message?.summaryResponse?.isClosed === 'true' ||
           !data.message?.summaryResponse?.hasPendingActivities || this.showActivitiesSelector) {
            setTimeout(() => {
              this.showLoading = false;
              this.showIframe = true;
              this._sessionHandlerService.showIframe = true;
            }, 500);
          }
          break;
        case 'routing-performed':
          this._iframeResponse = data.message;
          this._idCase = data.message?.summaryResponse?.idCase;
          this.checkActivitiesLoadCase(data, iframe, true);
          this.showLoadGlobalform(data);
          break;
        case 'renderComplete':
          if (!!this.parameters['processId'] && !!this.parameters['case']) {
            this.showIframe = false;
            this._sessionHandlerService.showIframe = false;
            this.sendPostMessagesToIframe(iframe);
          }
          break;
        case 'authentication-error':
          this._oAuthClient.logoutToUrl(this.currentPage?.isPrivate);
          break;
        case 'awaiting-theme':
          // this.sendMessageToIframe(iframe, 'theme-initialize', this.webpartTheme);
          if (type === 'casesummary') {
            this.sendMessageToIframe(iframe, 'remove-bizagi-themes', {});
            this._loadWebpartDefaultFont();
          }
          break;
        case 'show-finish-message':
          this.showIframe = false;
          this._sessionHandlerService.showIframe = false;
          this.modal.close();
          break;
        case 'bz-webpart-size':
          this.showGoDown = Number(data.message?.height) > Number($('#bs-bizagi-process').height());
          break;
        case 'form-rendered-error':
          if (data.message?.message === 'Actividad cerrada') {
            this.infoMessage = this.CASE_CLOSED;
          } else {
            this.showIframe = false;
            this._sessionHandlerService.showIframe = false;
            this.errorMessage = data.message.message;
          }
          this.showLoading = false;
          break;
        case 'form-has-changes':
          this.openVerifyModal();
          break;

        case 'authorization-internal-error':
          this.showAlertAuthorizationError();
          break;
        case 'form-without-changes':
        case 'refresh-finished':
        case 'form-saved':
          if (this.closing) {
            this.closeForm();
            this.closing = false;
          } else {
            this.chnageTabOperation(this.tabSelected, true, false);
          }
          break;
      }
    });
  }

  public loadGlobalForm(data) {
    const iframe = this.getIframe();
    const message = {};
    message['idCase'] = this.parameters['case']? this.parameters['case'] : this._idCase;
    if (!message['idCase']) {
      message['idCase'] = data.summaryResponse?.idCase;
    }
    this.sendMessageToIframe(iframe, 'load-global-form', message);
    this.showIframe = true;
    this._sessionHandlerService.showIframe = true;
    this.infoMessage = '';
    this.showLoading = true;
    this.globalFormLoaded = true;
  }

  private showAlertAuthorizationError(){
    this._modalRefAuthorizationError = this._modalService.open(ConfirmDialogModalComponent, {
      backdropClass: 'cdk-overlay-dark-backdrop-custom',
      maxWidth: '500px',
      closeOnBackdropClick: false,
      data: {
         message: 'messages.error.ERROR_USER_UNAUTHORIZED_FORM',
         title: 'messages.error.ERROR_OPEN_FORM_TITLE',
         confirmButtonText: 'siteeditor.global.OK',
         cancelButton: false,
         icon: 'icon-warning',
         iconColor: 'warning'
        }
    });

    this._modalRefAuthorizationError.afterClosed().subscribe(async result => {
      if (result && result.type === 'confirm') {
        this.closeForm();
        this._modalRefAuthorizationError.close();
      } else {
        this.closeForm();
        this._modalRefAuthorizationError.close();
      }
    });
  }

  private showLoadGlobalform(data) {
    if (data?.message?.summaryResponse?.showGlobalForm  && (data.message.summaryResponse.isClosed === 'true'
      || data.message.summaryResponse.hasPendingActivities === false)) {
      this.showLoadFormButton = true;
      this.loadGlobalForm(data.message);
    } else {
      this.showLoadFormButton = false;
    }
  }


  private checkActivitiesLoadCase(data, iframe, next = false) {
    if (data.message && data.message.workItemsResponse &&
      data.message.workItemsResponse.length > 0) {
      const newWorkItems = data.message.workItemsResponse.map(item => {
        return {
          id: item.idWorkItem,
          taskName: item.state || item.displayName,
          dueDate: item.estimatedSolutionDate
        };
      });
      this.workItems = newWorkItems;
      this._idCase = data.message.summaryResponse.idCase;
      if (newWorkItems.length > 1 && !data.message.activityName && (!this.parameters['idTask']
        || this.parameters['control'] === 'my-cases')) {
        this.showActivitiesSelector = true;
        if (next) {
          this.showLoading = false;
        }
      } else {
        this.showActivitiesSelector = false;
        const currentActivity = _.find(newWorkItems, item => item.taskName === data.message?.activityName) ||
        _.find(newWorkItems, item => item.id.toString() === this.parameters['idTask']?.toString());
        this.sendPostMessagesToIframe(iframe, data.message.summaryResponse.idCase, newWorkItems,
          data.message.fromLoadFormCase, currentActivity);
      }
    } else {
      this._sessionHandlerService.showIframe = true;
      this.isLastActionNext = data.message && !_.isEmpty(data.message.summaryResponse) ?
        data.message.summaryResponse.hasPendingActivities === false : false;
      if (this.isLastActionNext) {
        this.showIframe = false;
        this._sessionHandlerService.showIframe = false;
        this.infoMessage = this.NO_PENDING_ACTIVITIES;
        if (data.message.summaryResponse.isClosed === 'true') {
          this.infoMessage = this.CASE_CLOSED;
        }
        if (data.message.summaryResponse.isAborted.toString() === 'true') {
          this.infoMessage = this.CASE_CANCELED;
        }
      }

    }

  }

  private async sendPostMessagesToIframe(iframe, idCase?: number, workItems?: { id: string, taskName: string, dueDate: string }[],
    fromLoadFormCase?: boolean, workitem = null) {
    const message: any = {};
    const isNewCase = !!idCase && !!workItems;
    this.workItems = workItems ? workItems : await this._getWorkitems();
    if (!fromLoadFormCase) {
      this.currentWorkitem = workitem? workitem : (this._getCurrentWorkitem(null, this.workItems));
      this.showIframe = false;
      this._sessionHandlerService.showIframe = false;
      if (this.type === FormType.New && !isNewCase) {
        // TODO: When we add more navigation levels, we'll need to keep track of this context, instead of creating it here;
        const accumulatedContext = [];
        if (this.parameters['bizagiEntity'] && this.parameters['key']) {
          accumulatedContext.push({
            entityGuid: this.parameters['bizagiEntity'],
            surrogateKey: [this.parameters['key']]
          });
        }
        if (this.parameters['process']) {
          message.idWfClass = typeof (this.parameters['process']) === 'string' ? this.parameters['process'].trim() : this.parameters['process'];
        }
        if (this.multipleOrganization) {
          message.idOrganization = this.organizationSelected;
        }
        if (this.parameters['action']) {
          message.guidAction = this.parameters['action'].trim();
        }
        if (accumulatedContext.length) {
          message.accumulatedContext = JSON.stringify(accumulatedContext);
        }
        if (this.parameters['key']) {
          message.surrogateKey = this.parameters['key'].trim();
        }

        this.sendMessageToIframe(iframe, 'create-case', message);
      } else if (this.type === FormType.Existing || isNewCase) {
        this.parameters['case'] = idCase || this.parameters['case'];
        message.idCase = this.parameters['case'];
        if (this.parameters['idTask'] && !this.workItems.length) {
          message.idWorkItem = this.parameters['idTask'];
        } else {
          if (this.workItems.length > 0) {
            message.idWorkItem = this.currentWorkitem.id;
          }
        }
        if (message.idWorkItem) {
          this._loadformCase = false;
          this.sendMessageToIframe(iframe, 'load-form-case', message);
        }
      }
    } else {
      this.showIframe = true;
      this._sessionHandlerService.showIframe = true;
    }

  }

  private async _updateFormTitle(data) {
    if (data.message) {
      if (data.message?.processName && data.message.processName !== this.formTitle.process) {
        this.formTitle.process = data.message.processName;
      }
      if (!this.formTitle.process && data.message?.summaryResponse) {
        this.formTitle.process = data.message.summaryResponse.process;
      }
      if (data.message?.activityName && data.message.activityName !== this.formTitle.activityName) {
        this.formTitle.activityName = data.message.activityName;
      }
      if (data.message?.summaryResponse?.caseNumber && data.message.summaryResponse.caseNumber !== this.formTitle.caseNumber) {
        this.formTitle.caseNumber = await this._translateService.get('sitemanager.global.VIEW_CASE_TITLE',
        { case: data.message.summaryResponse.caseNumber || data.message.summaryResponse.caseNumber }).toPromise();
        ;
      }
    }
  }


  public openNewProcess(processId) {
    const iframe = this.getIframe();
    const message = {};
    message['idCase'] = processId;
    if (this.parameters['idTask']) {
      message['idTask']=this.parameters['idTask']
      message['idWorkitem'] = this.parameters['idTask'];
    } else if (this.currentWorkitem?.id) {
      message['idWorkitem'] = this.currentWorkitem.id;
    }
    this.sendMessageToIframe(iframe, 'load-activity-form', message);
  }


  private getWebPartType() {
    if (this.type === FormType.New) {
      return 'newcase';
    }
    return 'activityform';
  }
  private receiveMessageFromIframe(callback, iframeType?: string) {
    if (this.iframeReceiveFunction) {
      this.destroyIFrameCommunication();
    }

    // This is to make it work in any browser
    const eventMethod = window.addEventListener ? 'addEventListener' : 'attachEvent';
    const eventer = window[eventMethod];
    const messageEvent = eventMethod === 'attachEvent' ? 'onmessage' : 'message';

    // Listen to message from child window
    if (iframeType === 'fake') {
      this.iframeFakeReceiveFunction = function (e) {
        callback(e.data);
      };
      eventer(messageEvent, this.iframeFakeReceiveFunction, false);
    } else {
      this.iframeReceiveFunction = function (e) {
        callback(e.data);
      };
      eventer(messageEvent, this.iframeReceiveFunction, false);
    }

  }

  private destroyIFrameCommunication() {
    // This is to make it work in any browser
    const eventMethod = window.addEventListener ? 'removeEventListener' : 'detachEvent';
    const eventer = window[eventMethod];
    const messageEvent = eventMethod === 'detachEvent' ? 'onmessage' : 'message';

    // Remove handler
    eventer(messageEvent, this.iframeReceiveFunction, false);
    this.iframeReceiveFunction = null;
  }

  private destroyIFrameFakeCommunication() {
    // This is to make it work in any browser
    const eventMethod = window.addEventListener ? 'removeEventListener' : 'detachEvent';
    const eventer = window[eventMethod];
    const messageEvent = eventMethod === 'detachEvent' ? 'onmessage' : 'message';

    // Remove handler
    eventer(messageEvent, this.iframeFakeReceiveFunction, false);
    this.iframeFakeReceiveFunction = null;
  }

  private sendMessageToIframe(iframe, eventType, message = null) {
    const data = {
      event: eventType,
      message
    };
    console.log('------- MESSAGE OUT -------', data.event, data.message);
    iframe[0]?.contentWindow?.postMessage(data, this.targetOrigin || this.connection);
  }

  private async getWebPartParameters() {
    // Empty if not defined yet for activity form
    let _return = '';
    this.workItems = await this._getWorkitems();
    if (this.workItems.length === 1 || this.parameters.control === 'my-cases') {
      this.currentWorkitem = this._getCurrentWorkitem(null, this.workItems);
    }
    if (this.type === FormType.New) {
      // TODO: When we add more navigation levels, we'll need to keep track of this context, instead of creating it here;
      const accumulatedContext = [];
      if (this.parameters['bizagiEntity'] && this.parameters['key']) {
        accumulatedContext.push({
          entityGuid: this.parameters['bizagiEntity'],
          surrogateKey: [this.parameters['key']],
        });
      }
      _return =
        'idWfClass=' +
        (typeof this.parameters['process'] === 'string' ? this.parameters['process'].trim() : this.parameters['process']) +
        (this.parameters['action'] ? '&guidAction=' + this.parameters['action'] : '') +
        (accumulatedContext.length ? '&accumulatedContext=' + JSON.stringify(accumulatedContext) : '') +
        (this.parameters['key'] ? '&surrogateKey=' + this.parameters['key'] : '');

        if (this.multipleOrganization) {
          _return += '&idOrganization=' + this.organizationSelected;
        }
    }

    if (this.type === FormType.Existing) {
      if (this.parameters['idTask'] && !this.globalFormLoaded) {
        _return = 'idCase=' + this.parameters['case'] + `&idTask=${this.parameters['idTask']}&idWorkitem=${this.parameters['idTask']}`;
      } else {
        if (this.currentWorkitem?.id && !this.globalFormLoaded) {
          _return = 'idCase=' + this.parameters['case'] + `&idWorkitem=${this.currentWorkitem.id}`;
        } else {
          _return = 'idCase=' + this.parameters['case'];
        }

      }
    }
    return _return;
  }

  private async _getWorkitems() {
    if (this.parameters['processId'] && this.parameters['case']) {
      const url = (_.values(this.site.environments[this.site.defaultEnvironment].projects) as Array<IBizagiProject>)[0].url;
      const workitemsResponse = await this._bizagiDataService.getWorkitems(url, this.parameters['processId'], this.parameters['case']);
      if (workitemsResponse) {
        const arrayWorkitems = workitemsResponse.workitems.map(workitem => {
          return { id: workitem.id, taskName: workitem.taskName, dueDate: workitem.estimatedSolutionDate };
        });
        if (arrayWorkitems.length === 0) {
          this.showIframe = false;
          this._sessionHandlerService.showIframe = false;
        }
        return arrayWorkitems;
      }
    }
    return [];
  }


  private _getCurrentWorkitem(
    index: number,
    workitems?: { id: string; taskName: string }[]
  ): { id: string; taskName: string; index: number } {
    if (!workitems && !index && !this.workItems) {
      return undefined;
    }

    if (this.workItems && !workitems) {
      workitems = this.workItems.map(workitem => {
        return { ...workitem };
      });
    }

    if (workitems[index]) {
      return { ...workitems[index], index };
    }

    if (workitems.length === 1) {
      return { ...workitems[0], index: 0 };
    }

    const workitemIdx = workitems.findIndex(item => {
      return item.taskName.indexOf('Cancel') !== 0;
    });
    return { ...this.workItems[workitemIdx], index: workitemIdx };
  }

  private _loadWebpartDefaultFont() {
      const iframe: any = this.getIframe();
      const dataToSend: any = {};
      dataToSend.event = 'set-custom-font';
      dataToSend.message = this._defaultFonts && this._defaultFonts.length ?
        this._defaultFonts : (this.site.globalSettings.defaultFontFamily || 'Open Sans');
      iframe[0].contentWindow.postMessage(dataToSend, this.targetOrigin || this.connection);
  }

  private setIframeFakeLanguage() {
    this._fakeIframe = this.getIframeFake();
    if (this._fakeIframe) {
      const hasURL = this._fakeIframe.attr('src')?.trim()?.length > 0;
      if (!hasURL) {
        if (this.currentPage?.isPrivate ||this._user) {
          this._fakeIframe.attr('src', `api/sites/private/webparts?type=newcaselist&remoteServer=${document.location.origin}&application=NEWWORKPORTAL&mashup=false}`);
        } else {
          this._fakeIframe.attr('src', `api/sites/webparts?type=newcaselist&remoteServer=${document.location.origin}&application=NEWWORKPORTAL&mashup=false}`);
        }
      }
      if (!this.targetOrigin) {
        this.targetOrigin = document.location.origin;
      }
    }
    this.receiveMessageFromIframe(async data => {
      if (data && !data.event) {
        try {
          data = JSON.parse(data);
        } catch (e) { }
        data.event = data.postMessageEventName ? data.postMessageEventName.split('.')[0] : '';
      }
      if (data.event && data.event !== 'ajax-call') {
        console.log('------- MESSAGE IN -------', data.event, data.message);
      };
      switch (data.event) {
        case 'language-loaded':
          this._languageChanged = true;
          this._languageError = false;
          this._store.dispatch(new UpdateLastBPMLanguage(data.message));
          this._loadInitialIframes();
          break;
        case 'language-loaded-error':
          this._languageError = true;
          this._loadInitialIframes();
          break;
        case 'load-complete':
          this._iframeFakeLoaded = true;
          this._sessionHandlerService.iframeFakeLoaded = true;
          if (!this._languageChanged && !this._languageError) {
            this.sendMessageToIframe(this._fakeIframe, 'set-custom-language', this._getLanguageName(this.currentLanguage.name));
          }
          break;
          case 'form-rendered':
          case 'routing-performed':
          case 'form-rendered-error':
            case 'sumary-rendered':
            if (this._loadingIframe) {
              this._loadingIframe = false;
              this._store.dispatch(new UpdateLoading({ loadingWebparts: false }));
              this.iframfeInitialized.next();
            }
          break;
      }
    }, 'fake');
  }


}


export { FormType, BizagiFormsComponent };
