import {Injectable} from '@angular/core';
import {Subject} from 'rxjs';

@Injectable()
export class NotificationService {
private _notificationEvent = new Subject<INotificationEvent>();
  constructor() { }

  public subscribe(callback: Function) {
    this._notificationEvent.subscribe((notificationEvent: INotificationEvent) => {
      callback(notificationEvent);
    });
  }

  public notify(notificationEvent: INotificationEvent) {
    this._notificationEvent.next(notificationEvent);
  }

}
