import {Action} from '@ngrx/store';

export const pagesActions = {
  LOAD_PAGES: '[Pages Actions] Load pages',
  LOAD_PAGE_SUCCESS: '[Pages Actions] Load pages success',
  LOAD_PAGE_FAIL: '[Pages Actions] Load pages fail'
};

export class LoadPages implements Action {
  readonly type = pagesActions.LOAD_PAGES;
  constructor(public payload: string) {}
}

export class LoadPagesSuccess implements Action {
  readonly type = pagesActions.LOAD_PAGE_SUCCESS;
  constructor(public payload: Array<IBasicPage>) {}
}

export class LoadPagesFail implements Action {
  readonly type = pagesActions.LOAD_PAGE_FAIL;
  constructor(public payload: any) {}
}

export type PagesActions = LoadPages
  | LoadPagesSuccess
  | LoadPagesFail;

