import {Component} from '@angular/core';
import {ControlDefinition} from 'app/common/decorators/control-definition';
import { CATEGORY, PROPERTY_SECTIONS, SUBCATEGORY_MY_RELEVANTS } from './../controls-name-definition';
import {BaseProcessControlComponent} from 'app/common/controls/base-process.component';
import {DefaultValues} from 'app/common/misc/default-values';
import {TranslateService} from '@ngx-translate/core';

@Component({
  selector: 'bs-my-relevants-control',
  templateUrl: './my-relevants-control.component.html',
  styleUrls: ['./my-relevants-control.component.scss']
})
@ControlDefinition({
  type: 'my-relevants',
  displayName: 'MY_RELEVANTS_CONTROL',
  category: CATEGORY.EXPERIENCE_ELEMENTS.displayName,
  subcategory: SUBCATEGORY_MY_RELEVANTS.displayName,
  icon: SUBCATEGORY_MY_RELEVANTS.controls.MyRelevants.icon,
  styles: {
    layout: {
      type: 'layout',
      defaultValue: 'free',
      category: 'hidden',
      layouts: {
        free: {
          icon: 'free-menu',
          text: 'FREE_MENU',
          columns: 3
        },
        small: {
          icon: 'small-menu',
          text: 'SMALL_MENU',
          columns: 1
        },
        medium: {
          icon: 'medium-menu',
          text: 'MEDIUM_MENU',
          min: 50,
          columns: 2
        },
        large: {
          icon: 'large-menu',
          text: 'LARGE_MENU',
          min: 80,
          columns: 3
        }
      }
    },
    align: {
      section: PROPERTY_SECTIONS.style.key,
      type: 'text-align',
      defaultValue: 'left'
    },
    fontFamily: {
      section: PROPERTY_SECTIONS.fonts.key,
      type: 'font-family',
      defaultValue: 'inherit'
    },
    titleTextColor: {
      section: PROPERTY_SECTIONS.fonts.key,
      type: 'color',
      defaultValue: '#FFFFFF'
    },
    titleBackColor: {
      section: PROPERTY_SECTIONS.background.key,
      type: 'color',
      defaultValue: '#364855'
    },
    categoryFillContent: {
      section: PROPERTY_SECTIONS.background.key,
      type: 'fill-content',
      defaultValue: 'filled'
    },
    categoryFontColor: {
      section: PROPERTY_SECTIONS.fonts.key,
      type: 'color',
      defaultValue: '#FFFFFF'
    },
    categoryBackColor: {
      section: PROPERTY_SECTIONS.background.key,
      type: 'color',
      defaultValue: '#364855'
    },
    itemFillContent: {
      section: PROPERTY_SECTIONS.background.key,
      type: 'fill-content',
      defaultValue: 'filled'
    },
    itemFontColor: {
      section: PROPERTY_SECTIONS.fonts.key,
      type: 'color',
      defaultValue: '#FFFFFF'
    },
    itemBackColor: {
      section: PROPERTY_SECTIONS.background.key,
      type: 'color',
      defaultValue: '#364855'
    },
    showItemIcon: {
      section: PROPERTY_SECTIONS.icon.key,
      type: 'boolean',
      defaultValue: true
    }
  },
  parameters: {
    menuName: {
      type: 'title'
    }
  },
  defaultSize: {
    width: 300,
    height: 530
  },
})

export class MyRelevantsControlComponent extends BaseProcessControlComponent {
  public expanded = {};

  constructor(private _translateParser: TranslateService) {
    super();
  }

  async ngOnInit() {
    if (this.isRenderingMode()) {
      this.data =  await this.loadRelevants();
    } else {
      this.data = await DefaultValues.getDefaultProcesess(this._translateParser);
    }
  }

  public toggle(relevantCategory: string) {
    this.expanded[relevantCategory] = this.expanded[relevantCategory] ? false : true;
  }
}
