import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'bsObjectList'
})
export class ObjectListPipe implements PipeTransform {

  transform(value: any, args?: any[]): Object[] {
    const returnArray = [];
    const keys: Array<string> = Object.keys(value);

    keys.forEach((key: string) => {
      returnArray.push({
        key,
        val: value[key]
      });
    });
    return returnArray;
  }

}
