import { Component, OnInit, Input } from '@angular/core';
import {Global} from '../../misc/global';

@Component({
  selector: 'bs-collection-field-control',
  templateUrl: './collection-field-control.component.html',
  styleUrls: ['./collection-field-control.component.scss']
})

export class CollectionFieldControlComponent {
  @Input() field: IControlField;
  @Input() data: any;
  @Input() className: string;
  @Input() labelClasses: string;
  @Input() textClasses: string;
  @Input() type: string;
  @Input() isOnRenderMode = false;
  public openedTooltipViewMore = false;
  public openedTooltipFiles = false;


  public downloadFile(url, fileName:string) {
    window.open(Global.getBizagiFileUrl(url, fileName));
  }

  public getBoolean(value: boolean | string): boolean {
    return Global.getBooleanValue(value);
  }

}
