import {Actions, Effect, ofType} from '@ngrx/effects';
import {Injectable} from '@angular/core';
import {switchMap} from 'rxjs/operators';
import {LoadPage, LoadPageSuccess, pageActions} from './page.actions';
import {Store} from '@ngrx/store';
import {Router} from '@angular/router';
import {PublishedSiteService} from '../../services/published-site.service';
import {Observable} from 'rxjs';

@Injectable()
export class PageEffects {
  constructor(private actions$: Actions,
              private _publishedSiteServices: PublishedSiteService,
              private _store: Store<IRenderState>, private _router: Router
  ) {
  }


  @Effect()
  loadPage = this.actions$.pipe(ofType(pageActions.LOAD_PAGE),
    switchMap(async (action: LoadPage) => {
      return new LoadPageSuccess(await this._publishedSiteServices.getPage(action.payload.siteName, action.payload.pageName));
    }),
  );

}
