import { sessionActions, SessionActions, SetSession } from './session.actions';


export function sessionReducer(
  state: ISession = null,
  action: SessionActions
): ISession {
  switch (action.type) {
    case sessionActions.SET_SESSION: {
      return {
        ...state,
       ...(action as SetSession).payload
      };
    }
    default:
      return state;
  }
}
