import { map } from 'rxjs/operators';
import { Injectable } from '@angular/core';
import { Lock } from '../../common/decorators/lock';
import { Cache } from '../../common/decorators/cache';
import 'rxjs/add/operator/toPromise';
import { RestApiSevice } from 'app/common/services/oauth/rest-api.service';
import { GlobalCompatibility } from '../../common/misc/global-compatibility';
import { Observable } from 'rxjs';

@Injectable()
export class PublishedSiteService implements IPublishedSiteService {
  private endpoints: any = {
    sites: 'api/publishedSites',
    site: 'api/publishedSites/{publishedSiteName}',
    page: 'api/publishedSites/{publishedSiteName}/pages/{pageName}',
    pages: 'api/publishedSites/{publishedSiteName}/pages',
    categories: 'api/publishedSites/{publishedSiteName}/pages/{pageName}/searchCategories',
    resourcesAll: 'api/publishedSites/resources/all/{siteId}',
    getResource: 'api/publishedSites/resources/{resourceId}',
    isPrivatePage: 'api/publishedSites/{publishedSiteName}/pages/{pageName}/isPrivate',
  };

  private CACHE_SITE_PREFIX = 'SITE_';
  private CACHE_SITEPAGES_PREFIX = 'SITEPAGES_';
  private CACHE_PAGE_PREFIX = 'PAGE_';

  constructor(protected _restApiService: RestApiSevice) {}

  public async getSites(): Promise<ISite[]> {
    const response = await this._restApiService
      .get({
        url: this.endpoints.sites,
      })
      .toPromise();
    return response.publishedSites as ISite[];
  }

  @Lock()
  @Cache()
  public async getSite(siteName: string): Promise<ISite> {
    const response = await this._restApiService
      .get({
        url: this.endpoints.site,
        parameters: {
          publishedSiteName: siteName,
        },
      })
      .toPromise();
    return response.site as ISite;
  }

  @Lock()
  @Cache()
  public async getPage(siteName: string, pageName: string): Promise<IPage> {
    const response = await this._restApiService
      .get({
        url: this.endpoints.page,
        parameters: {
          publishedSiteName: siteName,
          pageName,
        },
      })
      .pipe(
        map(responseService => {
          return GlobalCompatibility.supportPageChanges(responseService.page);
        })
      )
      .toPromise();
    return response as IPage;
  }

  @Cache()
  public async getPages(siteName: string): Promise<Array<IBasicPage>> {
    const response = await this._restApiService
      .get({
        url: this.endpoints.pages,
        parameters: {
          publishedSiteName: siteName,
        },
      })
      .pipe(
        map(responseService => {
          return responseService.pages;
        })
      )
      .toPromise();
    return response;
  }

  public getResources(siteId: string): Promise<ISiteResource[]> {
    const response = this._restApiService.get({
      url: this.endpoints.resourcesAll,
      parameters: {
        siteId,
      },
    });
    return response.toPromise();
  }

  public getIsPrivatePage(publishedSiteName: string, pageName: string): Promise<boolean> {
    const response = this._restApiService.get({
      url: this.endpoints.isPrivatePage,
      parameters: {
        publishedSiteName,
        pageName
      },
    });
    return response.toPromise();
  }

  @Cache()
  public getResource(resourceId: string): Promise<ILanguageResource> {
    const response = this._restApiService.get({
      url: this.endpoints.getResource,
      parameters: {
        resourceId,
      },
    });
    return response.toPromise();
  }

  @Cache()
  public async getControlCategories(siteName: string, pageName: string, controlXpaths: {[key: string]: string}): Promise<Array<string>> {
      const response = await this._restApiService.post({
        url: this.endpoints.categories,
        parameters: {
          publishedSiteName: siteName,
          pageName
        },
        body: controlXpaths
      }).pipe(map((responseService) => {
        return responseService.categories as Array<string>;
      })).toPromise();
      return response;
  }

}
