import * as GridActions from './grid.actions';

export function gridReducer(state = null, action: GridActions.UpdateGridSize) {
  switch (action.type) {
    case GridActions.UPDATE_GRID_SIZE: {
      const screenSize = action.payload;
      const grid: IGrid = {
        size: screenSize,
        boundaries: {
          left: 0,
          right: 0,
          width: 0,
        },
        layoutSize: 960,
      };
      let boundariesSize = 0;
      if (screenSize > 1200) {
        boundariesSize = screenSize - 1200;
        grid.layoutSize = 1200;
      } else if (screenSize > 960) {
        boundariesSize = screenSize - 960;
      }

      grid.boundaries.width = boundariesSize / 2;
      grid.boundaries.left = boundariesSize / 2;
      grid.boundaries.right = screenSize - boundariesSize / 2;
      grid.prev = { ...state };
      delete grid.prev.prev;
      return {
        ...state,
        ...grid
      };
    }
    default:
      return state;
  }
}
