import { PaginatorLabelsService } from 'bz-genome-angular';
import { TranslateService } from '@ngx-translate/core';
import { select } from '@ngrx/store';
import { getSiteLanguage } from 'app/render/store/multilanguage/multilanguage.selector';
import { getCurrentSiteLanguage } from 'app/editor/site-editor/store/multilanguage/multilanguage.selector';
import { Global } from 'app/common/misc/global';

export class CustomPaginatorLabels extends PaginatorLabelsService {
    itemsPerPageOf = 'of';
    constructor(private store, private _translateService: TranslateService) {
        super();
        if (Global.isOnRenderMode()) {
            this.store?.pipe(select(getSiteLanguage)).subscribe((lang) => {
                this.handleServiceTranslate();
            });
        } else {
            this.store?.pipe(select(getCurrentSiteLanguage)).subscribe((lang) => {
                this.handleServiceTranslate();
            });
        }
    }

    /** Label for the range of items */
    getRangeLabel = (page: number, pageSize: number, length: number) => {
        if (length === 0 || pageSize === 0) { return `0 ${this.itemsPerPageOf.toLowerCase()} ${length}`; }

        const startIndex = page * pageSize;
        const endIndex = startIndex < length ? Math.min(startIndex + pageSize, length) : startIndex + pageSize;

        return `${startIndex + 1} - ${endIndex} ${this.itemsPerPageOf.toLowerCase()} ${length}`;
    }

    handleServiceTranslate() {
        this._translateService.get('sitemanager.global.ITEMS_PER_PAGE').subscribe((msg) => {
            this.pageSizeSelectorLabel = msg;
        });
        this._translateService.get('sitemanager.global.ITEMS_PER_PAGE_OF').subscribe((msg) => {
            this.itemsPerPageOf = msg;
        });
    }
}