
import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Global } from 'app/common/misc/global';

@Injectable()
export class BizagiOAuthManager {
    private readonly BIZAGI_OAUTH_INFO_URL = 'api/oauth/bizagi/info';
    private readonly REFRESH_TOKEN_URL = 'api/oauth/bizagi/refreshToken';
    private readonly USE_CODE_URL = 'api/oauth/bizagi/useCode';
    private profile: any;
    private expiration: any;
    private _authInfo: { urls: any, type: any; server: any; client_id: any; scope: any; sso: any; id?: any; saveToken?: any; };

    get authInfo() {
        return this._authInfo;
    }
    set authInfo(value) {
        this._authInfo = value;
    }

    public getProfile() {
        return this.profile;
    }

    public setProfile(profile) {
        this.profile = profile;
    }

    public setExpiration(expiration) {
        this.expiration = expiration;
    }

    public getExpiration() {
        return this.expiration;
    }

    constructor(private _httpClient: HttpClient) {
    }

    public async initAndLogin(loginParams) {
        loginParams = loginParams || {};
        const identityServer = loginParams.identityServer;
        const url = this.BIZAGI_OAUTH_INFO_URL + '?identityServer=' + encodeURI(identityServer);;
        const initData: any = await this._httpClient.get(url).toPromise();
        this.setParams({
            type: loginParams.type,
            server: identityServer,
            scope: initData.scope,
            client_id: initData.client_id,
            urls: initData.urls,
            saveToken: true,
        });
        return this.login(loginParams);
    }

    public async login(loginParams) {
        loginParams = loginParams || {};
        const queryParams = loginParams.queryParams;
        const qs = queryParams || Global.getQueryString();
        if (qs['code']) {
            await this.continueWithAuthorizationCode(qs);
            return true;
        };
        this.redirectToAuthorizePage(qs);
        return false;
    }

    public logout(bUseFullUrlWithHashes?, logoutUrl?) {
        let postLogoutUrl = (logoutUrl ? logoutUrl : this.getRedirectUri(bUseFullUrlWithHashes));
        postLogoutUrl = postLogoutUrl.endsWith('editor.html') ? postLogoutUrl + '%23%2Fdashboard' : postLogoutUrl;
        const userIdentity = sessionStorage.getItem('userIdentity');
        location.href = 'api/oauth/bizagi/logout?userIdentity=' + (userIdentity || '') + '&postLogoutUrl=' + postLogoutUrl;
    }

    public async refreshToken() {
        return this._httpClient.post(this.REFRESH_TOKEN_URL, {}).toPromise()
            .then((response: any) => {
                return response;
            })
            .catch(() => {
                this.logout(true);
            });
    }

    private setParams(params) {
        if (!params) { return; }
        this.authInfo = {
            type: 'bizagi',
            server: params.server,
            id: params.id || 'oauth',
            scope: params.scope,
            client_id: params.client_id,
            sso: true,
            urls: {
                authorize: params.urls.authorize
            },
        };
    }

    private redirectToAuthorizePage(queryString) {
        const nonce = this.createNonce();
        const url = this.getAuthorizeUrl(nonce, queryString);
        location.href = url;
    }

    private async continueWithAuthorizationCode(qs) {
        const code = qs['code'];
        const url = this.USE_CODE_URL + '?identityServer=' + encodeURI(this.authInfo.server);
        const body = { code, redirect_uri: encodeURIComponent(this.getRedirectUri()) };

        await this._httpClient.post(url, body).toPromise()
            .then((response: any) => {
                this.profile = response.profile;
                this.expiration = response.expiration;
                history.pushState(null, '', location.href.split('?')[0]);
            })
            .catch(() => {
                this.logout();
            });
    }

    private getAuthorizeUrl(nonce, queryString) {
        let state: any = {};
        state['nonce'] = nonce;
        if (queryString['redirectRoute']) { state['redirectRoute'] = queryString['redirectRoute']; }
        state = btoa(unescape(encodeURIComponent(JSON.stringify(state))));
        const url = this.authInfo.urls.authorize + '?response_type=' + 'code' // Change this when using OPEN ID
            + '&client_id=' + encodeURIComponent(this.authInfo.client_id)
            + '&state=' + encodeURIComponent(state)
            + '&redirect_uri=' + encodeURIComponent(this.getRedirectUri())
            + '&scope=' + encodeURIComponent(this.authInfo.scope)
            + (this.authInfo.sso ? '&sso=true' : '');
        return url;
    }

    private getRedirectUri(bUseFullUrlWithHashes?) {
        if (bUseFullUrlWithHashes) {
            return encodeURIComponent(window.location.href);
        }
        const base = $('base');
        if (base.length > 0 && base.attr('href').toLowerCase().indexOf('http') === 0) {
            return base.attr('href');
        }
        let url = window.location.origin + window.location.pathname;
        url = url.indexOf('#') > 0 ? url.substring(0, url.lastIndexOf('#')) : url;
        if (url.lastIndexOf('/') === url.length - 1) { url = url.substring(0, url.length - 1); }
        return url;
    }

    private createNonce() {
        let text = '';
        const possible = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789';
        for (let i = 0; i < 10; i++) {
            text += possible.charAt(Math.floor(Math.random() * possible.length));
        }
        return text;
    };

}