import { LoadingActions, loadingActions, UpdateLoading } from './loading.actions';

export function loadingReducer(state: {[key: string]: boolean} = null, action: LoadingActions) {
  switch (action.type) {
    case loadingActions.UPDATE_LOADING: {
      const loading = (action as UpdateLoading).payload;
      return {
        ...state,
        ...loading
      };
    }
    default:
      return state;
  }
}
