import {NavigationContextsActions, updateNavigationContexts} from './navigation-contexts.actions';

export function navigationContextsReducer(state = [], action: NavigationContextsActions) {
  switch (action.type) {
    case updateNavigationContexts.UPDATE_NAVIGATION_CONTEXTS: {
      const navigationContexts: Array<INavigationContext> = action.payload;
      return [...state, ...navigationContexts];
    }
    case updateNavigationContexts.CLEAR_NAVIGATION_CONTEXTS: {
      const navigationContexts: Array<INavigationContext> = [];
      return [...state, ...navigationContexts];
    }
    default:
      return state;
  }
}
