import { Component, Input } from '@angular/core';
import { BaseControlComponent } from 'app/common/controls/base.component';
import { ControlDefinition } from 'app/common/decorators/control-definition';
import { DomSanitizer } from '../../../../../node_modules/@angular/platform-browser';
import {
  EMBED_WEBSITE_CONTROLS,
  CATEGORY,
  SUBCATEGORY_EMBED_WEBSITE,
  PROPERTY_SECTIONS,
} from '../controls-name-definition';

@Component({
    selector: 'bs-embedded-code-control',
    templateUrl: './embedded-code-control.component.html',
    styleUrls: ['./embedded-code-control.component.scss']
})
@ControlDefinition({
    type: EMBED_WEBSITE_CONTROLS.EmbedWebsite.type,
    displayName: EMBED_WEBSITE_CONTROLS.EmbedWebsite.displayName,
    category: CATEGORY.EMBEDS.displayName,
    subcategory: SUBCATEGORY_EMBED_WEBSITE.displayName,
    icon: EMBED_WEBSITE_CONTROLS.EmbedWebsite.icon,
    styles: {
        showTopBar: {
          section: PROPERTY_SECTIONS.style.key,
            type: 'boolean',
            defaultValue: false
        },
        border: {
          section: PROPERTY_SECTIONS.border.key,
            type: 'border',
            defaultValue: 'border-none'
        },
        titleTextColor: {
          section: PROPERTY_SECTIONS.fonts.key,
            type: 'color',
            defaultValue: 'color-sixth'
      }
    },
    parameters: {
      title: {
        type: 'title'
      },
      code: {
          type: 'text',
      },
    },
    navigation: [],
    defaultSize: {
        width: 600,
        height: 404
    }
})

export class EmbeddedCodeControlComponent extends BaseControlComponent {

    constructor(private _domSanitizer: DomSanitizer) {
        super();
    }

    public transformToSecure(url) {
        return this._domSanitizer.bypassSecurityTrustResourceUrl(url);
    }
}
