import { Injectable } from '@angular/core';
import { RestApiSevice } from 'app/common/services/oauth/rest-api.service';
import { Cache } from 'app/common/decorators/cache';

@Injectable()
export class UserService {
  private _endpoints = {
    userPreferences: 'api/user',
    updateUser: 'api/user/update',
    createUser: 'api/user/create',
    updateLanguage: 'api/user/language',
    firstAccess: 'api/user/editorAccess',
    userprofile: 'api/bizagi/userprofile',
    updateEditorLanguage: 'api/user/editorLanguage'
  };

  constructor(protected _restApiService: RestApiSevice) {
  }

  public getUserProfile(url: string) {
    const response = this._restApiService.post({
      url: this._endpoints.userprofile,
      body: {
        url
      }
    });
    return response;
  }

  public updateLanguagePreference(username: string, language) {
    this._restApiService.post({
      url: this._endpoints.updateLanguage,
      body: {
        username,
        language
      },
    })
      .toPromise()
      .catch(error => {
        return null;
      });

  }
  @Cache()
  public createUserPreferences(user: IUserProfile): Promise<any> {
    return this._restApiService.post({
      url: this._endpoints.createUser,
      body: user
    }).toPromise();
  }

  public updateUserEditorLanguage(username:string, language:string): Promise<any> {
    return this._restApiService.post({
      url: this._endpoints.updateEditorLanguage,
      body: {
        username,
        language
      }
    }).toPromise();
  }

  public validateFirstAccess(email: string): Promise<boolean> {
    return this._restApiService.post({
      url: `${this._endpoints.firstAccess}`,
      body: email
    }).toPromise();
  }

  public getUserByEmail(email: string): Promise<any> {
    return this._restApiService.get({
      url: `${this._endpoints.userPreferences}/${email}`
    }).toPromise();
  }

  public getUserByUsername(username: string): Promise<any> {
    return this._restApiService.post({
      url: `${this._endpoints.userPreferences}`,
      body: {username}
    }).toPromise();
  }

  public createOrUpdateUserPreferences(user: any): Promise<any> {
    return this._restApiService.post({
      url: `${this._endpoints.updateUser}`,
      body: {
        username: user.userName,
        user
      }
    }).toPromise();
  }
}
