import {Actions, Effect, ofType} from '@ngrx/effects';
import {Injectable} from '@angular/core';
import {switchMap} from 'rxjs/operators';
import {Store} from '@ngrx/store';
import {Router} from '@angular/router';
import {PublishedSiteService} from '../../services/published-site.service';
import {LoadPages, LoadPagesSuccess, pagesActions} from './pages.actions';

@Injectable()
export class PagesEffects {
  constructor(private actions$: Actions,
              private _publishedSiteServices: PublishedSiteService,
              private _store: Store<IRenderState>, private _router: Router
  ) {
  }


  @Effect()
  loadPages = this.actions$.pipe(ofType(pagesActions.LOAD_PAGES),
    switchMap(async (action: LoadPages) => {
      return new LoadPagesSuccess(await this._publishedSiteServices.getPages(action.payload));
    }),
  );

}
