  public static showPanel(controlId: string, cssClass: string, panelElement: any) {
    const referenceElement = $('#' + controlId + ' ' + cssClass);
    panelElement.css('visibility', 'hidden');
    if (referenceElement && referenceElement.offset()) {
      if ((referenceElement.offset().top + 200) < (2 * $(window).height() / 3)) {
        panelElement.offset({
          top: referenceElement.offset().top + referenceElement.height(),
          left: referenceElement.offset().left < ($(window).width() - 300) ? referenceElement.offset().left :
            referenceElement.offset().left - 200
        });
        panelElement.show();
        panelElement.css('visibility', 'visible');
      } else {
        setTimeout(() => {
          panelElement.offset({
            top: referenceElement.offset().top - $(panelElement.children()[0]).height(),
            left: referenceElement.offset().left < ($(window).width() - 300) ? referenceElement.offset().left :
              referenceElement.offset().left - 200
          });
          panelElement.show();
          panelElement.css('visibility', 'visible');
        }, 10);
      }
    }
  }

