  public static getBizagiImageUrl(imageUrl: string) {
    const urlParts = imageUrl.split('/odata/image?');
    const code = urlParts[1].split('code=')[1];
    const url = urlParts[0];
    let basePath = this.getApplicationPath();
    if (!basePath.endsWith('/')) {
      basePath += '/';
    }
    return `${basePath}api/bizagi/image?url=${url}&code=${code}`;
  }

  public static getBizagiFileUrl(imageUrl: string, fileName: string) {
    const urlParts = imageUrl.split('/odata/file?');
    const code = urlParts[1].split('code=')[1];
    const url = urlParts[0];
    let basePath = this.getApplicationPath();
    if (!basePath.endsWith('/')) {
      basePath += '/';
    }
    return `${basePath}api/bizagi/file?url=${url}&code=${code}&fileName=${fileName}`;
  }

  public static isBizagiImage(image) {
    if (image === null) {
      return false;
    }
    return (image.startsWith('http://') || image.startsWith('https://')) && _.includes(image, 'odata/image');
  }

  public static getQueryString() {
    if (window.location.search && window.location.search.length > 0) {
      return this.parseQueryString(window.location.search.substr(1));
    }
    if (window.location.hash.indexOf('#') === 0) {
      const url = window.location.hash;
      if (url.indexOf('?') < 0) {
        return {};
      }
      const queryString = url.substring(url.indexOf('?') + 1);
      return this.parseQueryString(queryString);
    } else {
      return {};
    }
  }

  public static getRedirectRoute(queryString?) {
    queryString = queryString || Global.getQueryString() || {};
    let state = queryString['state'];
    if (!state) { return null; }

    state = decodeURIComponent(escape(atob(state)));
    try {
      return JSON.parse(state).redirectRoute;
    }
    catch (e) {
      return null;
    }
  }

