  public static ellipsisMultiline(selector: string) {
    $(selector).each(function() {
      const element = $(this)[0];
      const wordArray = element.innerHTML.split(' ');
      while(element.scrollHeight > element.offsetHeight) {
        wordArray.pop();
        element.innerHTML = wordArray.join(' ') + '...';
      }
    })
  }

  private static parseQueryString(queryString) {
    const data = {};
    let pairs
    let pair
    let separatorIndex
    let escapedKey
    let escapedValue;
    let key;
    let value;
    if (queryString === null) {
      return data;
    }
    pairs = queryString.split('&');

    for (let i = 0; i < pairs.length; i++) {
      pair = pairs[i];
      separatorIndex = pair.indexOf('=');

      if (separatorIndex === -1) {
        escapedKey = pair;
        escapedValue = null;
      } else {
        escapedKey = pair.substr(0, separatorIndex);
        escapedValue = pair.substr(separatorIndex + 1);
      }

      key = decodeURIComponent(escapedKey);
      value = decodeURIComponent(escapedValue);

      if (key.substr(0, 1) === '/') {
        key = key.substr(1);
      }

      data[key] = value;
    }
    return data;
  }

  public static isVisibleTo(visibleToList: Array<string>, stakeholderContextId: string | Array<string>): boolean {
    if (_.isEmpty(visibleToList)) {
      return false;
    }
    if (JSON.stringify(visibleToList).match('ALL')) {
      return true;
    }
    let canShowControl = false;
    if (stakeholderContextId instanceof Array) {
      _.each(stakeholderContextId, (id) => {
        if (JSON.stringify(visibleToList).match(id)) {
          canShowControl = true;
        }
      });
    } else {
      canShowControl = JSON.stringify(visibleToList).match(stakeholderContextId) !== null;
    }
    return canShowControl;
  }

  public static capitalizeFirstLetter(text: string) {
    const textLower = text.toLowerCase();
    return textLower.toLowerCase().charAt(0).toUpperCase() + textLower.slice(1);
  }

  public static copyToClipBoard(text: string): boolean {
    const id = 'mycustom-clipboard-textarea-hidden-id';
    let existsTextarea: any = document.getElementById(id);

    if (!existsTextarea) {
      const textarea = document.createElement('textarea');
      textarea.id = id;
      // Place in top-left corner of screen regardless of scroll position.
      textarea.style.position = 'fixed';
      textarea.style.top = '0';
      textarea.style.left = '0';
      textarea.style.width = '1px';
      textarea.style.height = '1px';
      textarea.style.padding = '0';
      textarea.style.border = 'none';
      textarea.style.outline = 'none';
      textarea.style.boxShadow = 'none';
      textarea.style.background = 'transparent';
      document.querySelector('body').appendChild(textarea);
      existsTextarea = document.getElementById(id);
    }
    existsTextarea.value = text;
    existsTextarea.select();

    const status = document.execCommand('copy');
    return status;
  }

  public static sortByDefinition(definition, items, sortKey) {
    const result = [];
    definition.forEach(function (key) {
      let found = false;
      items = items.filter(function (item) {
        if (!found && item[sortKey] === key) {
          result.push(item);
          found = true;
          return false;
        } else {
          return true;
        }
      });
    });

    return result;
  }
  // TODO: Grid refactor
  public static adjustGridNodesToLayout(grid: IGrid, controls: Array<IControl>, gridstack?: any) {
    if (grid.layoutSize === grid.prev.layoutSize) {
      const sizeDifferenceSameLayout = (grid.size - grid.prev.size) / 2;
      _.each(controls, (control: IControl) => {
        control.location.x += sizeDifferenceSameLayout;
        if (gridstack) {
          _.each(gridstack.grid.nodes, (node: any) => {
            if (node.el.attr('id') === control.id) {

              node.el.offset({ left: control.location.x });
            }
          });
        }
      });
    } else {
      _.each(controls, (control: IControl) => {
        control.location.width = control.location.width * (grid.layoutSize / grid.prev.layoutSize);
        if (control.location.x < grid.prev.size / 2) {
          control.location.x = grid.size / 2 - ((grid.prev.size / 2 - control.location.x) * (grid.layoutSize / grid.prev.layoutSize));
        } else {
          control.location.x = grid.size / 2 + ((control.location.x - grid.prev.size / 2) * (grid.layoutSize / grid.prev.layoutSize));
        }
        if (gridstack) {
          _.each(gridstack.grid.nodes, (node: any) => {
            if (node.el.attr('id') === control.id) {
              node.x = control.location.x;
              node.width = control.location.width;
              node.el.offset({ left: control.location.x });
              node.el.width(control.location.width);
            }
          });
        }
      });
    }
  }

  public static checkSize(control: IControl, size: number, deviceType: string, grid: IGrid): boolean {
    switch (size) {
      case 25:
        return (control.location.width >= this._getWidthPercentage(25, grid) &&
          control.location.width < this._getWidthPercentage(30, grid)
          && (deviceType === 'desktop' || deviceType === 'largedesktop'));
      case 30:
        return deviceType === 'mobile' ||
          (control.location.width >= this._getWidthPercentage(30, grid) && control.location.width < this._getWidthPercentage(40, grid) &&
            (deviceType === 'desktop' || deviceType === 'largedesktop'));
      case 50:
        return deviceType === 'tablet' ||
          (control.location.width >= this._getWidthPercentage(40, grid) && control.location.width < this._getWidthPercentage(70, grid) &&
            (deviceType === 'desktop' || deviceType === 'largedesktop'));
      case 75:
        return (control.location.width >= this._getWidthPercentage(70, grid) &&
          control.location.width < this._getWidthPercentage(80, grid) &&
          (deviceType === 'desktop' || deviceType === 'largedesktop'));
      case 80:
        return (control.location.width >= this._getWidthPercentage(80, grid) &&
          control.location.width < this._getWidthPercentage(90, grid) && (deviceType === 'desktop' || deviceType === 'largedesktop'));
    }
  }

  public static applyMixins(derivedCtor: any, baseCtors: any[]) {
    baseCtors.forEach(baseCtor => {
      Object.getOwnPropertyNames(baseCtor.prototype).forEach(name => {
        if (name !== 'constructor') {
          derivedCtor.prototype[name] = baseCtor.prototype[name];
        }
      });
    });
  }

  private static _getWidthPercentage(percentage: number, grid: IGrid) {
    return grid.layoutSize * percentage / 100;
  }

  public static transformObjectToArray(value: any): any {
    const returnArray = [];
    const keys: Array<string> = Object.keys(value);

    keys.forEach((key: string) => {
      returnArray.push({
        key,
        val: value[key]
      });
    });
    return returnArray;
  }


  public static jsonToBase64(obj: Object): string {
    const objJsonStr = JSON.stringify(obj);
    return btoa(unescape(encodeURIComponent(objJsonStr)));
  }

  public static base64ToJson(text: string): Object {
    const decodedStr = decodeURIComponent(escape(atob(text)));
    return JSON.parse(decodedStr);
  }

