  public static getBooleanValue(value: string | boolean = ''): boolean {
    if (value != null && (value === true || value.toString().toLowerCase() === 'true')) {
      return true;
    } else if (value != null && (value === false || value.toString().toLowerCase() === 'false')) {
      return false;
    } else {
      return null;
    }
  }



  public static b64toBlob = (b64Data, contentType = '', sliceSize = 512) => {
    const byteCharacters = atob(b64Data);
    const byteArrays = [];

    for (let offset = 0; offset < byteCharacters.length; offset += sliceSize) {
      const slice = byteCharacters.slice(offset, offset + sliceSize);

      const byteNumbers = new Array(slice.length);
      for (let i = 0; i < slice.length; i++) {
        byteNumbers[i] = slice.charCodeAt(i);
      }

      const byteArray = new Uint8Array(byteNumbers);
      byteArrays.push(byteArray);
    }

    const blob = new Blob(byteArrays, { type: contentType });
    return blob;
  }

  public static modifyWidhSelect(selectDropDown, event, selectOverlay, time?) {
    return $(selectDropDown).bind(event, () => {
      if (time !== null) {
        setTimeout(() => {
          this.performIECalculateWidth(selectOverlay);
        }, time);
      } else {
        this.performIECalculateWidth(selectOverlay);
      }
    });
  }

  private static performIECalculateWidth(selectOverlay) {
    if (this.isInternetExplorer()) {
      const overlay = $(selectOverlay).get(0);
      if (overlay && overlay.scrollHeight > overlay.clientHeight) {
        $(overlay).css('min-width', 'calc(100% - 17px)');
      } else {
        $(overlay).css('min-width', '100%');
      }
    }
  }
