    public static loadDefaultData(dataMappings: Array<any>, size: number = 3): any {
        const data = [];
        for (let i = 0; i < size; i++) {
            const row = {};
            _.keys(dataMappings).forEach((mapping: string) => {
                const dataType = dataMappings[mapping].typeData || dataMappings[mapping].type;
                row[mapping] = this.getDefaultValue(dataType);
            });
            data.push(row);
        }
        return {data, dataCount: data.length};
    }

    public static loadDefaultDataMyCases(): any {
        return this._defaultValuesMyCases;
    }

    public static loadDefaultDataFields(dataMappings: any, size: number = 3, controlId?: string) {
        const langResources = {};
        const data = [];
        for (let i = 0; i < size; i++) {
            const row = {};
            _.keys(dataMappings).forEach((mapping: string) => {
                const resourceKey = `${controlId}_field${i}_${mapping}`;
                if (mapping === 'image') {
                    langResources[resourceKey] = this.getRandomImage();
                    row[mapping] = resourceKey;
                } else {
                    switch (mapping) {
                        case 'body_1':
                        case 'body_2':
                        case 'body_3':
                            langResources[resourceKey] = this._defaultValuesFields[mapping] + this.getRandomText();
                            row[mapping] = resourceKey;
                            break;

                        default:
                            langResources[resourceKey] = this._defaultValuesFields[mapping];
                            row[mapping] = resourceKey;
                            break;
                    }
                }
            });
            data.push(row);
        }
        return { data, langResources };
    }

    public static getDefaultValue(type: string, controlType?: string): any {
        switch (type.toLowerCase()) {
            case 'title': {
                if (controlType && controlType.match('subtitle')) {
                    return this.getRandomTitle(6);
                } else {
                    return this.getRandomTitle(2);
                }
            }
            case 'text': {
                if (controlType && controlType.match('EmbedWebsite')) {
                    return '';
                }
                return this.getRandomText(3);
            }
            case 'image':
                return this.getRandomImage();
            case 'number':
                return this.getRandomNumber();
            case 'icon-selector':
                return null;
            case 'coords':
                return { latitude: '', longitude: '' };
            case 'display-search':
                return 'boxed';
            case 'workitem':
                return this._getWorkItems(3);
            case 'bizagi-number':
                return this.getRandomBizagiNumber();
            case 'date':
                return this.getRandomDate(new Date(2012, 0, 1), new Date());
            case 'url':
                return '';
            case 'xpath':
                return true;
            case 'boolean':
                return true;
            default:
                return this.getRandomText();
        }
    }

    public static getDefaultProcesess(translateService: TranslateService) {
        return new Promise((resolve) => {
            translateService.get(['controls.relevants.CATEGORY', 'controls.relevants.PROCESS'])
                .subscribe((resources: Array<string>) => {
                    const defaultData = {};
                    for (let i = 0; i < 5; i++) {
                        const categoryKey = resources['controls.relevants.CATEGORY'] + ' ' + i;
                        defaultData[categoryKey] = [];

                        for (let j = 0; j < 3; j++) {
                            defaultData[categoryKey].push({
                                name: 'CreatePolicy',
                                displayName: resources['controls.relevants.PROCESS'] + ' ' + j,
                                processId: 8
                            });
                        }
                        resolve(defaultData);
                    }
                });
        });
    }
    public static setTemplateImages(images: Array<string>) {
        this._templateImages = images;
    }

    public static getDefaultPicture(): string {
        return 'i/bz-default-image.png';
    }


    public static isDefaultValue(value: string): boolean {
        const defaultControlNames = CATEGORY.BUNDLES;
        return value.match(this.getDefaultPicture()) !== null || value.match('Text ') !== null;
    }

    public static getRandomText(nsentences?: number) {
        return loremIpsum({
            count: nsentences || 1
            , units: 'sentences'
        });
    }

    private static getRandomTitle(nwords?: number) {
        return 'Text ' + loremIpsum({
            count: nwords || 1
            , units: 'words'
        });
    }

    private static getRandomImage() {
        return this._templateImages && this._templateImages.length ? _.sample(this._templateImages) : this.getDefaultPicture();
    }

    private static getRandomNumber() {
        return Math.floor((Math.random() * 10) + 1);
    }

    private static getRandomBizagiNumber() {
        const rnumber = Math.floor((Math.random() * 10) + 1);
        const pad = '000';
        const ans = pad.substring(0, pad.length - rnumber.toString().length) + rnumber.toString();
        return 'CO-' + ans;
    }

    private static getRandomDate(start, end) {
        return new Date(start.getTime() + Math.random() * (end.getTime() - start.getTime())).toDateString();
    }

    private static _getWorkItems(nitems: number): Array<any> {
        const items = [];
        for (let index = 0; index < nitems; index++) {
            items[index] = {
                taskName: 'Task ' + index + 1,
                state: 'red',
                id: index + 1,
                solutionDate: this.getRandomDate(new Date(2012, 0, 1), new Date())
            };
        }

        return items;
    }

