
export class CacheService {
    private static cache = [];

    public static get(method, key) {
        const fullKey = this.getFullKey(method, key);
        return this.cache[fullKey];
    }

    public static add(method, key, value) {
        const fullKey = this.getFullKey(method, key);
        this.cache[fullKey] = value;
    }

    public static delete(method, key) {
        const fullKey = this.getFullKey(method, key);
        delete this.cache[fullKey];
    }

    public static getCacheKey(_arguments) {
        const args = Array.prototype.slice.call(_arguments);
        const key = _.filter(args, a => {
            const type = typeof (a);
            return type === 'number' || type === 'string' || type === 'boolean';
        }).join('_');
        return key;
    }

    private static getFullKey(method, key) {
        return method + '-' + key;
    }
}

// This only work with promises
export function Cache() {
    return (target, method, descriptor) => {

        // Save original
        if (descriptor === undefined) {
          descriptor = Object.getOwnPropertyDescriptor(target, method);
        }
        const _originalMethod = descriptor.value;

        // Override to the function
        descriptor.value = function(): any {
            const key = CacheService.getCacheKey(arguments);

            // First check in cache
            let result = CacheService.get(method, key);
            if (result) {
              return result;
            }

            result = _originalMethod.apply(this, arguments);

            // Check if result is promise or not
            if (result instanceof Promise) {
              (result as Promise<any>).then(resultIt => CacheService.add(method, key, resultIt));
            } else {
              CacheService.add(method, key, result);
            }

            return result;
        };

        // Return modified method
        return descriptor;
    };
}
