import {Component, EventEmitter, Input, Output} from '@angular/core';
import {BaseControlComponent} from 'app/common/controls/base.component';
import {ControlDefinition} from 'app/common/decorators/control-definition';
import { PROPERTY_SECTIONS } from '../controls-name-definition';

@Component({
  selector: 'bs-footer-control',
  templateUrl: './footer-control.component.html',
  styleUrls: ['./footer-control.component.scss']
})
@ControlDefinition({
  type: 'footer',
  displayName: 'FOOTER_CONTROL',
  icon: '',
  styles: {
    backColor: {
      section: PROPERTY_SECTIONS.background.key,
      type: 'color',
      defaultValue: 'rgb(149, 169, 173)'
    },
    showCopyright: {
      section: PROPERTY_SECTIONS.fonts.key,
      type: 'boolean',
      defaultValue: false
    },
    bold: {
      section: PROPERTY_SECTIONS.fonts.key,
      type: 'boolean',
      defaultValue: true
    },
    fontFamily: {
      section: PROPERTY_SECTIONS.fonts.key,
      type: 'font-family',
      defaultValue: 'inherit'
    },
    fontSize: {
      section: PROPERTY_SECTIONS.fonts.key,
      type: 'font-size',
      defaultValue: '14px',
      max: 20
    },
    fontColor: {
      section: PROPERTY_SECTIONS.fonts.key,
      type: 'color'
    },
    align: {
      section: PROPERTY_SECTIONS.fonts.key,
      type: 'text-align',
      defaultValue: 'left'
    },
    showFooter: {
      section: PROPERTY_SECTIONS.style.key,
      type: 'boolean',
      defaultValue: true
    },
    fullWidth: {
      section: PROPERTY_SECTIONS.style.key,
      type: 'boolean',
      defaultValue: true
    },
    height: {
      section: PROPERTY_SECTIONS.style.key,
      type: 'range',
      defaultValue: 100,
      min: 100,
      max: 300
    },
  },
  parameters: {
    copyright: {
      type: 'text',
      defaultValue: 'Copyright  Bizagi | 2020'
    }
  },
  navigation: []
})
export class FooterControlComponent extends BaseControlComponent {

  @Input() control: IFooter;
  @Output() onFooterSelected: EventEmitter<IFooter>;
  @Input() currentLanguage: ILanguageResource;

  protected onComponentInitialized() {
    this.onFooterSelected = new EventEmitter<IFooter>();
  }

  ngOnChanges() {
    if (this.control) {
      // This control is not loaded dinamically, so we need to initialize the control after the input is set
      this.initializeControl();
    }
  }

  selectFooter() {
    this.onFooterSelected.emit(this.control);
  }

}
