import {createSelector} from '@ngrx/store';

const renderSelector = (state: IRenderState) => state;

export const getSiteResources = createSelector(
  renderSelector,
  (state: IRenderState) => _.orderBy(state.multilanguage.resources, r=>r.language.displayName)
);
export const getSiteLanguage = createSelector(
  renderSelector,
  (state: IRenderState) => state.multilanguage.currentLanguage
);
export const getLastBpmLanguage = createSelector(
  renderSelector,
  (state: IRenderState) => state.multilanguage.currentLanguage.lastBPMLanguage
);
export const getResourcesByName = createSelector(
  renderSelector,
  (state: IRenderState) => state.multilanguage.resourcesByName
);

