import {
  LoadSiteSuccess,
  SiteActions,
  siteActions,
} from './site.actions';
import {INITIAL_STATE} from '../initial-state';

export function siteReducer(
  state: ISite = INITIAL_STATE.currentSite,
  action: SiteActions
): ISite {
  switch (action.type) {
    case siteActions.LOAD_SITE: {
      return {
        ...state,
      };
    }
    case siteActions.LOAD_SITE_SUCCESS: {
      return {
        ...state,
        ... (action as LoadSiteSuccess).payload,
      };
    }

    default:
      return state;
  }
}
