import {Action} from '@ngrx/store';

export const pageActions = {
  LOAD_PAGE: '[Page Actions] Load page',
  LOAD_PAGE_SUCCESS: '[Page Actions] Load page success',
  LOAD_PAGE_FAIL: '[Page Actions] Load page fail',
};

export class LoadPage implements Action {
  readonly type = pageActions.LOAD_PAGE;
  constructor(public payload: {siteName: string, pageName: string}) {}
}

export class LoadPageSuccess implements Action {
  readonly type = pageActions.LOAD_PAGE_SUCCESS;
  constructor(public payload: IPage) {}
}

export class LoadPageFail implements Action {
  readonly type = pageActions.LOAD_PAGE_FAIL;
  constructor(public payload: IPage) {}
}


export type PageActions = LoadPage
  | LoadPageSuccess
  | LoadPageFail;

