import { LoadUserSuccess, UserActions, userActions } from './user.actions';


export function userReducer(
  state: IUserSiteProfile = null,
  action: UserActions
): IUserSiteProfile {
  switch (action.type) {
    case userActions.LOAD_USER_SUCCESS: {
      return {
        ...state,
       ...(action as LoadUserSuccess).payload
      };
    }
    case userActions.UPDATE_USER_SUCCESS: {
      return {
        ...state,
       ...(action as LoadUserSuccess).payload
      };
    }
    default:
      return state;
  }
}
