import { Injectable } from '@angular/core';
import { DomSanitizer } from '@angular/platform-browser';
import { Global } from 'app/common/misc/global';
import { RestApiSevice } from '../oauth/rest-api.service';

@Injectable()
export class ThemeService {
  private themes: Array<ITheme>;
  private webpartThemes: any;
  private themeVariables: { [key: string]: string } = {
    'color-first': '#FAFAFA ',
    'color-second': '#9E9E9E ',
    'color-third': '#757575 ',
    'color-fourth': '#FFFFFF',
    font: 'opensans, Helvetica, sans-serif'
  };
  private _endpoints = {
    customThemes: 'api/sites/customThemes',
  };
  constructor(private _restApiService: RestApiSevice, private _sanitizer: DomSanitizer) {
    this.themes = [
      {
        id: '6b72f0d0-c6b8-4bc9-8506-b20982643a00',
        displayName: 'Luxury',
        colors: ['#89711f', '#c0b283', '#dcd0c0', '#272727'],
        fileName: ''
      },
      {
        id: '0748c562-1d16-439e-8795-8f0be0bb5971',
        displayName: 'Sky',
        colors: ['#4484ce', '#f2bf08', '#00142b', '#272727'],
        fileName: ''
      },
      {
        id: '3ed7d38a-af57-429f-b829-e4de141fbc16',
        displayName: 'Modern',
        colors: ['#07889b', '#adc345', '#033840', '#272727'],
        fileName: ''
      },
      {
        id: 'a13d181a-5e75-4055-8569-56f0c92bb2df',
        displayName: 'Beezagi',
        colors: ['#0a1612', '#f7ce3e', '#1a2930', '#272727'],
        fileName: ''
      },
      {
        id: 'bfef2652-eac3-4bf6-b30d-7094de2cad00',
        displayName: 'Clear',
        colors: ['#033840', '#94c2cc', '#caebf2', '#272727'],
        fileName: ''
      },
      {
        id: '4760dc64-e96b-426b-b60c-a04f47f2b4ac',
        displayName: 'Lavender',
        colors: ['#513746', '#bbb5c1', '#846d7a', '#272727'],
        fileName: ''
      }
    ];

    this.webpartThemes = {
      'active-border-radius': 'true',
      'active-box-shadow': 'false',
      'active-smart-contrast': 'false',
      'active-text-shadow': 'false',
      background: '#ffffff',
      button: 'color-second',
      'button-text': 'color-sixth',
      content: '#ffffff',
      'content-border': '#ffffff',
      'content-link': 'color-sixth',
      'font-family': '\'Open Sans\', \'Helvetica Neue\', Arial, Helvetica, sans-serif',
      'form-container': 'color-fifth',
      'form-grid': 'color-third',
      'form-grid-border': 'color-second',
      'form-group': 'color-fifth',
      'form-group-border': 'color-second',
      'form-input': '#ffffff',
      'form-input-border': 'color-second',
      'form-tab': 'color-third',
      'form-tab-border': 'color-second',
      'form-text': 'color-sixth',
      'logo-background': '#ffffff',
      'minimum-line-height': 'inactive',
      modal: 'color-fifth',
      'modal-border': 'color-second',
      navigation: 'color-second',
      'navigation-border': 'color-fifth',
      'navigation-link': 'color-second',
      'navigation-submenu-background': 'color-fifth',
      'navigation-submenu-text': 'color-sixth',
      'normal-line-height': 'active',
      pane: 'color-third',
      'sidebar-image-theme': 'url(\'jquery/workportal/css/desktop/images/bg-landscape.png\')',
      text: 'color-sixth',
      title: 'color-first',
      tooltip: 'color-sixth',
      'color-second': 'color-second',
      'color-third': 'color-third',
      'color-fourth': 'color-fourth',
      sites: 'true'
    };
  }

  public getThemes() {
    return this.themes;
  }

  public getCustomThemes() {
    return this._restApiService.get({ url: this._endpoints.customThemes });
  }

  public async loadSiteTheme(theme: string) {
    this.removePrevThemeLinks();
    const head = document.getElementsByTagName('head')[0];
    const link = document.createElement('link');
    link.rel = 'stylesheet';
    link.type = 'text/css';
    let url = 'assets/themes/' + Global.deleteRelativeBackUrl(theme) + '.min.css';
    if (theme.indexOf('custom_') >= 0) {
      const themeFile = await this._restApiService.get({ url: 'api/sites/customThemes/' + theme.replace('custom_', '') }).toPromise();
      if (themeFile) {
        const blob = Global.b64toBlob(themeFile, 'text/css');
        url = window.URL.createObjectURL(blob);
        const themeUrl = {};
        themeUrl[theme.replace('custom_', '')] = url;
        localStorage.setItem('themeUrlByName', JSON.stringify(themeUrl));
      }
    }
    link.href = url;
    link.media = 'all';
    head.appendChild(link);
  }

  public removePrevThemeLinks() {
    // Remove previous themes
    const prevThemes = document.getElementsByTagName('link');
    for (let i = 0; i < prevThemes.length; i++) {
      if ($(prevThemes[i]).attr('href').indexOf('assets/themes') !== -1 || $(prevThemes[i]).attr('href').indexOf('blob') !== -1) {
        $(prevThemes[i]).remove();
        localStorage.removeItem('themeUrlByName');
      }
    }
  }

  public removePrevThemeLinkByName(themeName: string) {
    // Remove previous themes
    const prevThemes = document.getElementsByTagName('link');
    const urlByThemeName = JSON.parse(localStorage.getItem('themeUrlByName'));
    for (let i = 0; i < prevThemes.length; i++) {
      if (($(prevThemes[i]).attr('href').indexOf('assets/themes') !== -1 && $(prevThemes[i]).attr('href').indexOf(themeName) !== -1)
        ||($(prevThemes[i]).attr('href').indexOf('blob') !== -1 && $(prevThemes[i]).attr('href').indexOf(urlByThemeName[themeName]) !== -1)
        ) {
        $(prevThemes[i]).remove();
        localStorage.removeItem('themeUrlByName');
      }
    }
  }

  public refreshThemeVariables() {
    const regexp = /#bs-form-theme-([\w-]+)__([\w-]+)/g;

    // First, iterate through all stylesheets
    for (let i = 0; i < document.styleSheets.length; i++) {
      try {
        const mysheet = (document.styleSheets[i]) as any;
        const myrules = mysheet.cssRules ? mysheet.cssRules : mysheet.rules || [];
        let primary_color = '';
        let secondary_color = '';
        let third_color = '';
        let fourth_color = '';

        // Second, iterate through all rules in the given stylesheet
        for (let j = 0; j < myrules.length; j++) {
          if (myrules[j].selectorText === 'html' && myrules[j].cssText.indexOf('--primary-color') >= 0) {
            primary_color = (myrules[j].cssText.match(/--primary-color: *([^;]+);/i) || ['', ''])[1];
            secondary_color = (myrules[j].cssText.match(/--secondary-color: *([^;]+);/i) || ['', ''])[1];
            third_color = (myrules[j].cssText.match(/--third-color: *([^;]+);/i) || ['', ''])[1];
            fourth_color = (myrules[j].cssText.match(/--fourth-color: *([^;]+);/i) || ['', ''])[1];
          }
          const selector = myrules[j].selectorText;
          if (selector) {
            // Try to match, in order to find the different generated rules for form themes
            let match = regexp.exec(selector);
            while (match != null) {
              const variable = match[1];
              const cssProp = match[2];
              this.themeVariables[variable] = myrules[j].style[cssProp]?.replace('var(--primary-color)', primary_color)
                .replace('var(--secondary-color)', secondary_color)
                .replace('var(--third-color)', third_color)
                .replace('var(--fourth-color)', fourth_color);

              // Get next match
              match = regexp.exec(selector);
            }
          }
        }
      } catch (error) {
        console.log('error to load themes', error);
      }
    }
  }
  public getThemeVariables() {
    return this.themeVariables;
  }
  public getWebpartThemeVariables(): any {
    const webpartMappedTheme: { [key: string]: string } = { ...this.webpartThemes };
    for (const key of Object.keys(this.webpartThemes)) {
      if (this.themeVariables[this.webpartThemes[key]]) {
        webpartMappedTheme['@' + key] = this.themeVariables[this.webpartThemes[key]];
      } else {
        webpartMappedTheme['@' + key] = this.webpartThemes[key];
      }
      delete webpartMappedTheme[key];
    }
    return webpartMappedTheme;
  }

  public createCustomTheme(data, replace = false) {
    return this._restApiService.post({
      url: `${this._endpoints.customThemes}/${replace}`,
      body: data
    });
  }

  public removeCustomTheme(data) {
    return this._restApiService.delete({
      url: `${this._endpoints.customThemes}/${data}`,
      body: data
    });
  }

}
