import { Action } from '@ngrx/store';

export const multilanguageActions = {
  LOAD_CURRENT_LANGUAGE: '[Multilanguage Actions] Load Current Language',
  LOAD_CURRENT_LANGUAGE_SUCCESS: '[Multilanguage Actions] Load Current Language success',
  LOAD_SITE_RESOURCES_SUCCESS: '[Multilanguage Actions] Load Site Resources success',
  UPDATE_CURRENT_LANGUAGE: '[Multilanguage Actions] Update Current Language',
  UPDATE_CURRENT_LANGUAGE_SUCCESS: '[Multilanguage Actions] Update Current Language Success',
  UPDATE_LAST_BPM_LANGUAGE: '[Multilanguage Actions] Update Last BPM Language',
  UPDATE_LAST_BPM_LANGUAGE_SUCCESS: '[Multilanguage Actions] Update Last BPM Language Success',
  LOAD_SITE_RESOURCES_BY_NAME: '[Multilanguage Actions] Load Site Resources by name',
};

export class LoadCurrentLanguage implements Action {
  readonly type = multilanguageActions.LOAD_CURRENT_LANGUAGE;
  constructor(public payload: string) {}
}

export class LoadCurrentLanguageSuccess implements Action {
  readonly type = multilanguageActions.LOAD_CURRENT_LANGUAGE_SUCCESS;
  constructor(public payload: ILanguageResource) {}
}

export class LoadSiteResourcesSuccess implements Action {
  readonly type = multilanguageActions.LOAD_SITE_RESOURCES_SUCCESS;
  constructor(public payload: Array<ISiteResource>) {}
}

export class LoadSiteResourcesByName implements Action {
  readonly type = multilanguageActions.LOAD_SITE_RESOURCES_BY_NAME;
  constructor(public payload: any) {}
}

export class UpdateCurrentLanguage implements Action {
  readonly type = multilanguageActions.UPDATE_CURRENT_LANGUAGE;
  constructor(public payload: string) {}
}

export class UpdateCurrentLanguageSuccess implements Action {
  readonly type = multilanguageActions.UPDATE_CURRENT_LANGUAGE_SUCCESS;
  constructor(public payload: ILanguageResource) {}
}

export class UpdateLastBPMLanguage implements Action {
  readonly type = multilanguageActions.UPDATE_LAST_BPM_LANGUAGE;
  constructor(public payload: string) {}
}
export class UpdateLastBPMLanguageSuccess implements Action {
  readonly type = multilanguageActions.UPDATE_LAST_BPM_LANGUAGE_SUCCESS;
  constructor(public payload: string) {}
}

export type MultilanguageActions = LoadCurrentLanguage
  | LoadCurrentLanguageSuccess
  | LoadSiteResourcesSuccess
  | UpdateCurrentLanguageSuccess
  | UpdateLastBPMLanguage
  | UpdateLastBPMLanguageSuccess
  | UpdateCurrentLanguage;

