import {Mutex} from 'async-mutex';
import 'rxjs/add/observable/fromPromise';

export function Lock() {
    return (target, method, descriptor) => {

        // Save original
        if (descriptor === undefined) {
          descriptor = Object.getOwnPropertyDescriptor(target, method);
        }
        const _originalMethod = descriptor.value;
        const _mutex = new Mutex();

        // Override to the function
        descriptor.value = function(): any {
            const that = this
            const methodArguments = arguments;

            // Mutex returns a promise, so we need to convert it to observable
            const promise = _mutex.runExclusive(() => new Promise( (resolve, reject) => {
                const result = _originalMethod.apply(that, methodArguments);

                // Check if result is observable
                if (result instanceof Promise) {
                    (result as Promise<any>).then(resultIt => resolve(resultIt))
                                          .catch(err => reject(err));
                } else {
                  resolve(result);
                }
            }));

            return promise;
        };

        // Return modified method
        return descriptor;
    };
}
