import {Component, EventEmitter, Input, Output} from '@angular/core';

@Component({
  selector: 'bs-pagination-control-element',
  templateUrl: './pagination-control-element.component.html',
  styleUrls: ['./pagination-control-element.component.scss']
})
export class PaginationControlElementComponent {

  @Input() isOnRenderMode = false;
  @Input() pagination: IPagination;
  @Input() showTotalCases = false;
  @Output() onChangePage = new EventEmitter<IPagination>();

  public prev() {
    if (this.pagination.currentPage > 0) {
      this.pagination.currentPage --;
      this.loadCurrentPage();
    }
  }

  public next() {
    if (this.pagination.currentPage < this.pagination.totalPages - 1) {
      this.pagination.currentPage ++;
    }
    this.loadCurrentPage();
  }

  public goToPage(page: number) {
    this.pagination.currentPage = page;
    this.loadCurrentPage();
  }

  public filterPages() {
    if (this.pagination.currentPage < 2 || this.pagination.currentPage > this.pagination.totalPages - 3) {
      return [];
    } else {
      return [this.pagination.currentPage - 1, this.pagination.currentPage, this.pagination.currentPage + 1];
    }
  }

  private loadCurrentPage() {
    this.pagination.from = this.pagination.currentPage * this.pagination.rows;
    this.pagination.currentPaginationSet = _.floor(this.pagination.currentPage / this.pagination.intervalSize);
    this.onChangePage.emit(this.pagination);
  }
}
