import { from as observableFrom, Observable, of, Subject, Subscription } from 'rxjs';

import { map } from 'rxjs/operators';
import { Injectable } from '@angular/core';
import { RestApiSevice } from 'app/common/services/oauth/rest-api.service';
import * as moment from 'moment';
import { Global } from 'app/common/misc/global';
import { UserService } from 'app/common/services/sites/user.service';
import { OAuthClientHelper } from 'app/common/services/oauth/oauth-client.helper';

@Injectable()
export class InternalResourcesService {

    private _endpoints =
        {
            laguages: 'api/sites/availableLanguages',
            internalResources: 'api/sites/availableLanguagesResources',
            internalResourcesDescription: 'assets/mockups/render-resources.json',
            languageResources: 'api/sites/languagesResources/'
        };


    constructor(protected _restApiService: RestApiSevice,
        private _userService: UserService,
        private _oAuthClientHelper: OAuthClientHelper) {
       
    }

   
    ngOnDestroy() {
    }

    
    public getAvailableLanguages() {
        return this._restApiService.get({ url: this._endpoints.laguages });
    }

    public getLanguageInternalResources() {
        return this._restApiService.get({ url: this._endpoints.internalResources });
    }

    public getFormatOptionsResourcesDescription() {
        return this._restApiService.get({ url: this._endpoints.internalResourcesDescription });
    }

   public setNewResources(data): Promise<any> {
    return this._restApiService.post({
      url: this._endpoints.languageResources,
      body: data
    }).toPromise();
  }


}
