import {Component} from '@angular/core';
import {ControlDefinition} from 'app/common/decorators/control-definition';
import {BaseDataControlComponent} from 'app/common/controls/base-data.component';
import { CATEGORY, SUBCATEGORY_LIST, LIST_CONTROLS, PROPERTY_SECTIONS } from './../controls-name-definition';
import { DefaultValues } from '../../misc/default-values';

@Component({
  selector: 'bs-card-link-control',
  templateUrl: './card-link-control.component.html',
  styleUrls: ['./card-link-control.component.scss']
})
@ControlDefinition(
  {
    type: LIST_CONTROLS.cardLink.type,
    category: CATEGORY.BUNDLES.displayName,
    subcategory: SUBCATEGORY_LIST.displayName,
    displayName: LIST_CONTROLS.cardLink.displayName,
    icon: LIST_CONTROLS.cardLink.icon,
    styles: {
      designDisplay: {
        type: 'design',
        defaultValue: 'basic-right',
        category: 'hidden',
        displays: {
          'basic-right': {
            icon: 'card-link-basic-right',
            text: 'siteeditor.layouts.DESIGN_DISPLAY_RIGHT',
          },
          'basic-left': {
            icon: 'card-link-basic-left',
            text: 'siteeditor.layouts.DESIGN_DISPLAY_LEFT',
          },
          'title-top-left': {
            icon: 'card-link-title-top-left',
            text: 'siteeditor.layouts.DESIGN_TITLE_TOP_LEFT',
          },
          'title-top-right': {
            icon: 'card-link-title-top-right',
            text: 'siteeditor.layouts.DESIGN_TITLE_TOP_RIGHT',
          }
        }
      },
      layout: {
        type: 'layout',
        defaultValue: 'three',
        category: 'hidden',
       layouts: {
            one: {
              icon: 'one-column-thin',
              text: 'ONE_COLUMN',
              columns: 1
            },
            two: {
              icon: 'two-columns-thin',
              text: 'TWO_COLUMNS',
              min: 50,
              columns: 2
            },
            three: {
              icon: 'three-columns-thin',
              text: 'THREE_COLUMNS',
              min: 75,
              columns: 3
            }
          }
      },
      multipageItemView: {
        type: 'radio',
        defaultValue: 'Pagination',
        category: 'layout',
        values: ['Pagination']
      },
      verticalItemSpacing: {
        type: 'range',
        defaultValue: 12,
        category: 'layout',
        step: 4,
        min: 4,
        max: 40,
        unitValue: 'px',
        headerName: 'Rows'
      },
      displayedItems: {
        type: 'range',
        defaultValue: 3,
        category: 'layout',
        min: 1,
        max: 10
      },
      backColor: {
        section: PROPERTY_SECTIONS.background.key,
        type: 'color',
        defaultValue: '#FFF'
      },
      boxShadow: {
        section: PROPERTY_SECTIONS.border.key,
        type: 'text',
        defaultValue: '0 1px 1px 0 rgba(0,0,0,.5), 0 1px 6px 0 rgba(0,0,0,.02)'
      },
      border: {
        section: PROPERTY_SECTIONS.border.key,
        type: 'border',
        defaultValue: '1px solid #e4e8ea'
      },
      cornerRadius: {
        section: PROPERTY_SECTIONS.border.key,
        type: 'range',
        defaultValue: 0,
        min: 6,
        max: 50
      },
      showActions: {
        section: PROPERTY_SECTIONS.style.key,
        type: 'boolean',
        defaultValue: 'true'
      }
    },
    parameters: {
      data: {
        type: 'data',
        size: 10
      },
      filter: {
        type: 'text',
        defaultValue: '',
        isFilter: true
      }
    },
    inputs: ['parameters.filter'],
    defaultSize: {
      width: 900,
      height: 182,
      minWidth: 300,
      minHeight: 124,
    dynamic: true
    },
    bizagiConnections: {
      cases: true,
      collections: true,
      searches: true,
      config: {
        image: 'card-link-config.png',
        coords: {
          image: {
            w: 77,
            h: 82,
            x: 6,
            y: 6
          },
          title: {
            w: 166,
            h: 21,
            x: 97,
            y: 17
          },
          text: {
            w: 103,
            h: 21,
            x: 97,
            y: 43
          }
        }
      }
    },
    fields: {
      image: {
        type: 'image',
        displayName: 'siteeditor.properties.IMAGE',
        styles: {
          imagesSize: {
            type: 'image-size',
            defaultValue: 'scale'
          }, opacity: {
            type: 'range',
            defaultValue: 100,
            min: 0,
            max: 100
          },
          background: {
            type: 'color',
            defaultValue: 'rgba(0, 0, 0, 0)'
          },
          border: {
            type: 'border',
            defaultValue: 'border-none'
          }
        }
      },
      title: {
        type: 'title',
        displayName: 'siteeditor.properties.TITLE',
        styles: {
          fieldLabel: {
            type: 'label-field',
            defaultValue: {
              isActive: false,
              text: 'Label:'
            }
          },
          fontFamily: {
            type: 'font-family',
            defaultValue: 'inherit'
          },
          fontSize: {
            type: 'font-size',
            defaultValue: '16px',
            max: 48
          },
          letterSpacing: {
            type: 'range',
            defaultValue: 0,
            min: 0,
            max: 60
          },
          align: {
            type: 'text-align',
            defaultValue: 'left'
          },
          fontColor: {
            type: 'color',
            defaultValue: 'color-first'
          },
          backColor: {
            type: 'color',
            defaultValue: 'rgba(0, 0, 0, 0)'
          },
          fontWeight: {
            type: 'font-weight',
            defaultValue: 'normal'
          }
        }
      },
      text: {
        type: 'text',
        displayName: 'siteeditor.properties.TEXT',
        styles: {
          fieldLabel: {
            type: 'label-field',
            defaultValue: {
              isActive: false,
              text: 'Label:'
            }
          },
          fontFamily: {
            type: 'font-family',
            defaultValue: 'inherit'
          },
          fontSize: {
            type: 'font-size',
            defaultValue: '12px',
            max: 48
          },
          letterSpacing: {
            type: 'range',
            defaultValue: 0,
            min: 0,
            max: 60
          },
          align: {
            type: 'text-align',
            defaultValue: 'left'
          },
          fontColor: {
            type: 'color',
            defaultValue: 'color-sixth'
          },
          backColor: {
            type: 'color',
            defaultValue: 'rgba(0, 0, 0, 0)'
          },
          fontWeight: {
            type: 'font-weight',
            defaultValue: 'normal'
          }
        }
      }
    },
    hiddenOptions: {
      modifyFields: true,
      designOption: false
    }
  }
)

export class CardLinkControlComponent extends BaseDataControlComponent {
  public defaultPicture = DefaultValues.getDefaultPicture();
  public isOnRenderMode: boolean;
}
