import { Component } from '@angular/core';
import { BaseProcessControlComponent } from '../base-process.component';
import { ControlDefinition } from '../../decorators/control-definition';
import { CATEGORY, BOOLEAN_CONTROLS, SUBCATEGORY_BOOLEAN, PROPERTY_SECTIONS } from '../controls-name-definition';
import {Global} from '../../misc/global';

@Component({
  selector: 'bs-boolean-control',
  templateUrl: './boolean-control.component.html',
  styleUrls: ['./boolean-control.component.scss']
})
@ControlDefinition(
  {
    type: BOOLEAN_CONTROLS.boolean.type,
    category: CATEGORY.BUTTONS.displayName,
    subcategory: SUBCATEGORY_BOOLEAN.displayName,
    displayName: BOOLEAN_CONTROLS.boolean.displayName,
    icon: BOOLEAN_CONTROLS.boolean.icon,
    styles: {
      withBooleanIcon: {
        section: PROPERTY_SECTIONS.icon.key,
        type: 'boolean',
        defaultValue: 'true'
      },
      withBooleanText: {
        section: PROPERTY_SECTIONS.style.key,
        type: 'boolean',
        defaultValue: 'true'
      },
      fontFamily: {
        section: PROPERTY_SECTIONS.style.key,
        type: 'font-family',
        defaultValue: 'inherit'
      },
      fontSize: {
        section: PROPERTY_SECTIONS.fonts.key,
        type: 'font-size',
        defaultValue: '18px',
        max: 72
      },
      fontColor: {
        section: PROPERTY_SECTIONS.fonts.key,
        type: 'color',
        defaultValue: 'color-second'
      }
    },
    parameters: {
      booleanlabeltext: {
        type: 'textShort',
        defaultValue: 'Label text here'
      },
      selectanattribute: {
        type: 'xpath',
        category: 'boolean',
        defaultValue: false
      }
    },
    defaultSize: {
      width: 270,
      height: 64,
      minHeight: 64
    },
    onlyContextualizedMode : true
  }
)
export class BooleanControlComponent extends BaseProcessControlComponent {

  public isOnRenderMode = Global.isOnRenderMode();

  getBoolean(value: boolean | string): boolean {
   return Global.getBooleanValue(value);
  }

}
