import {Action} from '@ngrx/store';

export const userActions = {
  LOAD_USER: '[User Actions] Load user',
  LOAD_USER_SUCCESS: '[User Actions] Load user success',
  UPDATE_USER: '[User Actions] Update user',
  UPDATE_USER_SUCCESS: '[User Actions] Update user success',
  LOAD_USER_FAIL: '[User Actions] Load user fail',
};

export class LoadUser implements Action {
  readonly type = userActions.LOAD_USER;
  constructor(public payload: string) {}
}

export class LoadUserSuccess implements Action {
  readonly type = userActions.LOAD_USER_SUCCESS;
  constructor(public payload: IUserProfile) {}
}

export class UpdateUser implements Action {
  readonly type = userActions.UPDATE_USER;
  constructor(public payload: IUserProfile) {}
}
export class UpdateUserSuccess implements Action {
  readonly type = userActions.UPDATE_USER_SUCCESS;
  constructor(public payload: IUserProfile) {}
}


export type UserActions = LoadUser
  | UpdateUser
  | UpdateUserSuccess
| LoadUserSuccess;

