import { Component, OnInit } from '@angular/core';
import {BaseProcessControlComponent} from 'app/common/controls/base-process.component';
import {ControlDefinition} from 'app/common/decorators/control-definition';
import { CATEGORY, SUBCATEGORY_IMAGE, IMAGE_CONTROLS, PROPERTY_SECTIONS } from './../controls-name-definition';
import { DefaultValues } from '../../misc/default-values';

@Component({
  selector: 'bs-image-control',
  templateUrl: './image-control.component.html',
  styleUrls: ['./image-control.component.scss']
})
@ControlDefinition(
  {
    type: IMAGE_CONTROLS.image.type,
    category: CATEGORY.MEDIA.displayName,
    subcategory: SUBCATEGORY_IMAGE.displayName,
    displayName: IMAGE_CONTROLS.image.displayName,
    icon: IMAGE_CONTROLS.image.icon,
    styles: {
      imagesSize: {
        section: PROPERTY_SECTIONS.style.key,
        type: 'image-size',
        defaultValue: 'scale'
      },
      opacity: {
        section: PROPERTY_SECTIONS.background.key,
        type: 'range',
        defaultValue: 100,
        min: 0,
        max: 100
      },
      background: {
        section: PROPERTY_SECTIONS.background.key,
        type: 'color',
        defaultValue: 'rgba(0, 0, 0, 0)'
      },
      border: {
        section: PROPERTY_SECTIONS.border.key,
        type: 'border',
        defaultValue: '0px solid #fff'
      }
    },
    parameters: {
      image: {
        type: 'image',
        defaultValue: DefaultValues.getDefaultPicture()
      }
    },
    links: {
      staticPages: true,
      internalPages: true,
      processes: true
    },
    defaultSize: {
      width: 120,
      height: 120
    }
  }
)

export class ImageControlComponent extends BaseProcessControlComponent {

  public defaultPicture = DefaultValues.getDefaultPicture();

}
