import {createSelector} from '@ngrx/store';

const renderSelector = (state: IRenderState) => state;

export const getCurrentPage = createSelector(
  renderSelector,
  (state: IRenderState) => state.currentPage
);

export const getPageContext = createSelector(
  renderSelector,
  (state: IRenderState) => state.pageContext
);

export const getRenderHero = createSelector(
  renderSelector,
  (state: IRenderState) => state.currentPage?.hero
);