import { Component, Input, Output, EventEmitter } from '@angular/core';

@Component({
  selector: 'bs-actions-control-element',
  templateUrl: './actions-control-element.component.html',
  styleUrls: ['./actions-control-element.component.scss']
})
export class ActionsControlElementComponent {
  @Input() showList = 'left';
  @Input() item: any;
  @Input() canShowCaseLink: boolean;
  @Input() position: string;
  @Input() isRender = true;
  @Output() onShowCase = new EventEmitter<any>();
  @Output() onCreateCase = new EventEmitter<any>();

  public itemActions: any;

  ngOnInit() {
    // *ngIf="item.actions?.length || canShowCaseLink"
    this.itemActions = !_.isUndefined(this.item.actions) ? this.item.actions : [];
    if (!this.isRender) {
      this.itemActions = [
        {
          id: 1,
          processId: 2,
          displayName: 'action 1'
        },
        {
          id: 2,
          processId: 3,
          displayName: 'action 2'
        }
      ];
    }
  }

  public createCase(caseObj: object) {
    this.onCreateCase.emit(caseObj);
  }

  public showCase(idCase: number, processName: string) {
    this.onShowCase.emit({idCase, processName});
  }

}
