import { Component } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { OAuthClientHelper } from 'app/common/services/oauth/oauth-client.helper';
import { RestApiSevice } from 'app/common/services/oauth/rest-api.service';
import { UserService } from 'app/common/services/sites/user.service';
import {HttpClient, HttpXsrfTokenExtractor} from '@angular/common/http';
import { Global } from 'app/common/misc/global';
import { FontsServiceCommon } from 'app/common/services/sites/fonts.service';
import { Store } from '@ngrx/store';
import { LoadFonts } from '../../../editor/site-manager/store/fonts/fonts.actions';
import { LoadCustomThemes } from 'app/editor/site-manager/store/themes/themes.actions';
import { SessionHandlerService } from 'app/common/services/oauth/session-handler.service';
import {Cookie} from 'ng2-cookies/ng2-cookies';

@Component({
  selector: 'bs-site-login',
  template: ''
})
export class LoginComponent {

  private queryParams: { [key: string]: any };

  constructor(private oAuthClient: OAuthClientHelper, protected api: RestApiSevice,
    private router: Router, private route: ActivatedRoute,
    private _http: HttpClient,
    protected _store: Store<IEditorState>,
    private _userService: UserService,
    private _sessionHandlerService: SessionHandlerService,
    private tokenExtractor: HttpXsrfTokenExtractor
  ) {
    this.route.queryParams.subscribe(async (params) => {
      this.queryParams = params;
      await this.login();
    });

  }

  async login() {
    const XSRFtoken = this.tokenExtractor.getToken() as string;
    $.ajaxSetup({
      beforeSend(xhr) {
        xhr.setRequestHeader('X-XSRF-TOKEN', XSRFtoken);
      }
    });
    const environment = await this.getExecutionEnvironment();
    const type = environment.executionEnvironment === 'ONPREMISES' ? 'bizagi' : 'accounts';
    Global.bizagiEnvironment = environment;
    console.log('Environment type: ', type);
    console.log('Global.bizagiEnvironment: ', Global.bizagiEnvironment);
    const result = await this.oAuthClient.login({
      queryParams: this.queryParams,
      identityServer: this.queryParams.identityServer || null
    }, type);
    if (result) {
      if (Global.isOnRenderMode()) {
        sessionStorage.setItem('userLoggedOn', JSON.stringify(true));
        if(Global.isInternetExplorer()) {
          Cookie.set('USERLOGGEDON', JSON.stringify(true));
        }
      }
      this.userPreferences();
      this.redirect();
      this._store.dispatch(new LoadFonts());
      this._store.dispatch(new LoadCustomThemes());
      const expiration = this.oAuthClient.getExpiration();
      this._sessionHandlerService.unsubscribeTimer();
      this._sessionHandlerService.startTimerSession(expiration);
    }

  }

  redirect() {
    const redirectRoute = this.queryParams.redirectRoute || Global.getRedirectRoute(this.queryParams) || '';
    const newParams = _.clone(this.queryParams);
    // Delete internal params
    delete newParams.identityServer;
    delete newParams.redirectRoute;
    delete newParams.code;
    delete newParams.state;
    // Redirect
    this.router.navigateByUrl(redirectRoute + '?' + $.param(newParams), { preserveQueryParams: true });
  }

  public async userPreferences() {
    let userProfile = this.oAuthClient.getProfile();
    if (!userProfile) {
      await this.oAuthClient.refreshToken();
      userProfile = this.oAuthClient.getProfile();
    }
    await this._userService.createUserPreferences(userProfile);
  }

  async getExecutionEnvironment(): Promise<any> {
    const response = await this._http.get('api/sites/executionEnvironment').toPromise();
    return response;
  }
}
