import { createSelector } from '@ngrx/store';

const siteEditorSelector = (state: IEditorState) => state.siteEditor;

export const getSiteVersion = createSelector(
  siteEditorSelector,
  (state: ISiteEditorState) => state.siteVersion
);

export const getCurrentSite = createSelector(
  siteEditorSelector,
  (state: ISiteEditorState) => state.currentSite
);

export const getCurrentHeader = createSelector(
  siteEditorSelector,
  (state: ISiteEditorState) => state.currentSite ? state.currentSite.header: null
);


export const getCurrentFooter = createSelector(
  siteEditorSelector,
  (state: ISiteEditorState) => state.currentSite ? state.currentSite.footer: null
);

export const getSiteNavigationPages = createSelector(
  siteEditorSelector,
  (state: ISiteEditorState) => state.currentSite ? state.currentSite.header.navigation: null
);

export const getSavingSite = createSelector(
  siteEditorSelector,
  (state: ISiteEditorState) => state.loading.savingSite
);

export const getSiteDefaultValues = createSelector(
  siteEditorSelector,
  (state: ISiteEditorState) => state.currentSite.defaultValues
);

export const getRandomPendingChanges = createSelector(
  siteEditorSelector,
  (state: ISiteEditorState) => state.randomPendingChanges
);

export const getCurrentSiteSubscription = createSelector(
  siteEditorSelector,
  (state: ISiteEditorState) => state.subscriptionId
);

export const getDefaultFontSite = createSelector(
  siteEditorSelector,
  (state: ISiteEditorState) => state.currentSite?.globalSettings.defaultFontFamily
);

export const getColorLibrary = createSelector(
  siteEditorSelector,
  (state: ISiteEditorState) => state.colorLibrary
);


export const getBizagiProjectUrl = createSelector(
  siteEditorSelector,
  (state: ISiteEditorState,
   props: {bizagiProjectId: string}) => {
    return state.currentSite.environments[state.currentSite.defaultEnvironment]?.projects[
      props.bizagiProjectId
      ]?.url;
  });

  export const getInProgressActions = createSelector(
    siteEditorSelector,
    (state: ISiteEditorState) => state.inProgressActions
  );
  
