import { ChangeDetectorRef } from '@angular/core';
import { BaseControlComponent } from './base.component';
import { PageRenderingService } from 'app/common/services/sites/page-rendering.service';
import { FormType } from 'app/render/bizagi-forms/bizagi-forms.component';
import { Global } from '../misc/global';

export abstract class BaseProcessControlComponent extends BaseControlComponent {

  protected pageRenderingService: PageRenderingService;
  protected bizagiDataServices: IBizagiDataServices;
  
  public isProcessLoading = false;
  public link: any = {};
  public data = {};

  public setServices(pageRenderingService: PageRenderingService,
    bizagiDataServices: IBizagiDataServices) {
    this.pageRenderingService = pageRenderingService;
    this.bizagiDataServices = bizagiDataServices;
  }

  public setControl(control: IControl, changeDetectorRef: ChangeDetectorRef) {
    super.setControl(control, changeDetectorRef);
    this.setControlLink();
    
  }

  public startProcess() {
    const link : IControlLink = typeof (this.control.link) === 'string' 
    ? JSON.parse(this._currentLanguageResources[this.control.link]) : this.control.link;
    
    if (this.isRenderingMode() && (link as IControlLinkRelevants).projectId) {
      this._startProcess(Global.getBizagiProjectUrl(this.stateSnapshotService.getSite(),
        (link as IControlLinkRelevants).projectId),
        (link as IControlLinkRelevants).processId);
    }
  }

  public startProcessByProcessId(processId: string, title: string) {
    const site = this.stateSnapshotService.getSite();
    this._startProcess((_.first(_.values(site.environments[site.defaultEnvironment].projects)) as any).url, processId, title);
  }

  public async goToPage() {
    if (this.isRenderingMode()) {
      this.setControlLink();
      const controlLink = (this.link as IControlLinkExternalPage);
      const target: string = controlLink.blank ? '_blank' : '_self';
      if (controlLink.type === 'staticPages') {
        const link = !/^(http:|https:)/i.test(controlLink.url) ?
          '//' + controlLink.url : controlLink.url;
        window.open(link, target);
      } else {
        if (controlLink.url) {
          window.open(controlLink.url, target);
        }
      }
    }
  }

  private _startProcess(url: string, processId: string, title?: string) {
    this.pageRenderingService.showForm({
      connection: url,
      title: title || (this.control.parameters && this.control.parameters.text ? this.control.parameters.text : ''),
      type: FormType.New,
      parameters: {
        process: processId
      },
      refresh: this.refresh.bind(this)
    });
  }

  private setControlLink(){
    if (typeof (this.control.link) === 'string') {
      this.control.link = this.control.link || `${this.control.id}_link`;
      this.link = JSON.parse(this._currentLanguageResources[this.control.link]);
    } else {
      this.control.link = this.control.link || {
        type: ''
      };
      this.link = this.control.link;
    }

    if (this.isRenderingMode()) {
      if (this.link.type === 'internalPages' && (this.link as IControlLinkInternalPage).id) {
        const site = this.stateSnapshotService.getSite();
        const pageName = site.references[(this.link as IControlLinkInternalPage).id];
        if (!pageName) {
          return;
        }
        // Set new url
        (this.link as IControlLinkInternalPage).url = '#/' + site.url + '/' + pageName;
      }
    }
  }

  protected async loadRelevants() {
    const site = this.stateSnapshotService.getSite();
    const url = (_.first(_.values(site.environments[site.defaultEnvironment].projects)) as any).url;
    const response = await this.bizagiDataServices.getRelevants(url);
    if (response) {
      return _.groupBy(response.instances, 'categoryName') as any;
    }
    return {};
  }
}
