import { Component } from '@angular/core';
import { BaseProcessControlComponent } from 'app/common/controls/base-process.component';
import { ControlDefinition } from 'app/common/decorators/control-definition';
import {
  CATEGORY,
  SUBCATEGORY_BUTTON_ROUND,
  BUTTON_ROUND_CONTROLS,
  PROPERTY_SECTIONS,
} from './../controls-name-definition';

@Component({
  selector: 'bs-button-round-control',
  templateUrl: './button-round-control.component.html',
  styleUrls: ['./button-round-control.component.scss']
})

@ControlDefinition(
  {
    type: BUTTON_ROUND_CONTROLS.button_round.type,
    category: CATEGORY.BUTTONS.displayName,
    subcategory: SUBCATEGORY_BUTTON_ROUND.displayName,
    displayName: BUTTON_ROUND_CONTROLS.button_round.displayName,
    icon: BUTTON_ROUND_CONTROLS.button_round.icon,
    styles: {
      iconColor: {
        section: PROPERTY_SECTIONS.icon.key,
        type: 'color',
      },
      backColor: {
        section: PROPERTY_SECTIONS.background.key,
        type: 'color',
      },
      buttonSize: {
        section: PROPERTY_SECTIONS.style.key,
        type: 'size',
        defaultValue: 'md'
      },
      align: {
        section: PROPERTY_SECTIONS.style.key,
        type: 'text-align',
        defaultValue: 'center'
      },
      border: {
        section: PROPERTY_SECTIONS.border.key,
        type: 'border',
      }
    },
    parameters: {
      iconSelector: {
        type: 'icon-selector',
        defaultValue: 'plus'
      },
      showIcon: {
        type: 'boolean',
        defaultValue: true
      }
    },
    defaultSize: {
      width: 42,
      height: 42
    },
    links: {
      staticPages: true,
      internalPages: true,
      processes: true
    }
  }
)
export class ButtonRoundControlComponent extends BaseProcessControlComponent {
}



