import {ChangeDetectorRef} from '@angular/core';
import {EControlMode} from 'app/editor/models/EControlMode';
import { Subject, Subscription, timer } from 'rxjs';
import {ThemeService} from 'app/common/services/sites/theme.service';
import {Global} from 'app/common/misc/global';
import {EDeviceMode} from 'app/editor/models/EDeviceMode';
import {CultureSettingsService} from '../services/sites/culture-settings.service';
import {CATEGORY} from './controls-name-definition';
import {Store} from '@ngrx/store';
import {StateSnapshotService} from '../services/sites/state-snapshots.service';
import { debounce, debounceTime } from 'rxjs/operators';

declare let browser_detect: any;

abstract class BaseControlComponent {
  private _changed: Subject<any> = new Subject<any>();
  private _parameters: {[key: string]: any} = {};
  private _parametersTranslation: {[key: string]: any} = {};
  protected _currentLanguageResources: {[key: string]: string};
  protected _defaultPreferences: IUserPreferences;

  protected control: IControl;
  protected onResize: Subject<{width: number; height: number}>;
  protected cdr: any;
  protected controlDefinition: IControlDefinition; // Dinamically set by control definition decorator
  protected state: EControlMode = EControlMode.Build;
  protected stateSnapshotService: StateSnapshotService;
  protected height;
  protected width;
  protected themeServices: ThemeService;
  protected cultureService: CultureSettingsService;
  protected baseStore: Store<IRenderState>;
  protected refreshSubject: Subject<any> = new Subject<any>();
  private _refreshSubscription: Subscription;


  public styles: {[key: string]: any} = {};
  public location: {[key: string]: any} = {};
  public deviceMode: EDeviceMode;
  public isOnRenderMode = Global.isOnRenderMode();

  private _boundParameters: {[key: string]: {xpaths: any[], url?: string}} = {};

  constructor() {
    this.controlDefinition = this.constructor['controlDefinition'];
    this.onComponentInitialized();
    this.onResize = new Subject<any>();
    this._refreshSubscription?.unsubscribe();
    this._refreshSubscription = this.refreshSubject
    .pipe(debounceTime(600),
      debounce(loading => !loading || (loading && !loading.loadingUser &&  !loading.loadingChangeBPMLanguage)
      ? timer(0) : timer(3000)))
    .subscribe(() => {
      this.refresh();
    })
  }

  public get parameters() {
    return this._parametersTranslation;
  }

  public setBaseServices(
    stateSnapshotsService: StateSnapshotService,
    themeServices: ThemeService,
    cultureService: CultureSettingsService
  ) {
    this.stateSnapshotService = stateSnapshotsService;
    this.themeServices = themeServices;
    this.cultureService = cultureService;
  }

  public setCurrentLanguage(currentLanguage: ILanguageResource) {
    this._currentLanguageResources = currentLanguage.resources;
    this._defaultPreferences = currentLanguage.culture;
  }

  public setControl(control: IControl, changeDetectorRef: ChangeDetectorRef) {
    this.control = control;
    this.initializeControl();
    this.cdr = changeDetectorRef;
  }

  public enableRenderingMode() {
    this.state = EControlMode.Render;
  }

  protected isRenderingMode() {
    return this.state === EControlMode.Render;
  }

  public getControl() {
    return this.control;
  }

  protected initializeControl() {
    // Subscribe to control changed subject, Note: This subject is only set at building mode
    // TODO: Store refactor
    // if (this.control.changed) {
    //   this.control.changed.asObservable().subscribe((updatedControl: IControl) => this.onChanged(updatedControl));
    // }
    if (!this.deviceMode) {
      this._checkDeviceMode();
    }
    // Load options
    this.location = this.control.location;
    this.loadStyles(this.control.styles);
  }

  private _checkDeviceMode() {
    if (browser_detect.mobilecheck() || Global.getScreenSize() === 'mobile') {
      this.deviceMode = EDeviceMode.Mobile;
    } else if (
      browser_detect.mobileAndTabletcheck() ||
      Global.getScreenSize() === 'tablet'
    ) {
      this.deviceMode = EDeviceMode.Tablet;
    } else {
      this.deviceMode = EDeviceMode.Desktop;
    }
  }

  public initialized() {
    this.onComponentInitialized();
  }

  /*
   *   EVENT TEMPLATE METHODS
   */
  protected onComponentInitialized() {
    // This method is called after all the controls in the page are fully initialized, and the page mappings are applied
  }

  protected onChanged(updatedControl: IControl) {
    this.control = updatedControl;
    this.loadStyles(this.control.styles);
    this.loadParameters(
      this.control.parameters,
      this.control.parametersBinding
    );
  }

  /*
   *   LOAD STYLE / PARAMETER METHODS
   */
 public loadStyles(styles) {
    for (const key in this.controlDefinition.styles) {
      if (!_.isNil(styles[key])) {
        if (this.themeServices && this.themeServices.getThemeVariables()[styles[key]]) {
          this.themeServices.refreshThemeVariables();
          this.styles[key] = this.themeServices.getThemeVariables()[styles[key]];
        } else {
          // Property defined in the control
          this.styles[key] = styles[key];
        }
      } else {
        // Set default value
        if (this.themeServices && this.themeServices.getThemeVariables()[this.controlDefinition.styles[key].defaultValue]) {
          this.themeServices.refreshThemeVariables();
          this.styles[key] = this.themeServices.getThemeVariables()[this.controlDefinition.styles[key].defaultValue];
        } else {
          this.styles[key] = this.controlDefinition.styles[key].defaultValue;
        }
      }
    }
  }

  public loadParameters(parameters, parametersBinding) {
    if (!this._currentLanguageResources) {
      return;
    }
    for (const key in this.controlDefinition.parameters) {
      if (this.controlDefinition.parameters[key]) {
        const translation =
          this._currentLanguageResources[parameters[key]] !== null &&
          this._currentLanguageResources[parameters[key]] !== undefined
            ? this._currentLanguageResources[parameters[key]]
            : parameters[key];
        if (parametersBinding && parametersBinding[key]) {
          // Bound values
          if (this.isRenderingMode()) {
            this._setParametersOnRender(translation, key);
          } else {
            if (
              this.controlDefinition.category !== CATEGORY.EMBEDS.displayName
            ) {
              this._parametersTranslation[key] = `<${translation}>`;
            } else {
              this._parametersTranslation[key] = translation;
            }
          }
        } else if (parameters[key] !== undefined) {
          // Property defined in the control
          switch (this.controlDefinition.parameters[key].type) {
            case 'coords':
              this._parametersTranslation[key] = translation && this._isJSON(translation) ? JSON.parse(translation) : {};
              break;

            case 'range':
              this._parametersTranslation[key] = parseInt(translation, 10);
              break;

            default:
              this._parametersTranslation[key] = translation;
              break;
          }
        } else {
          // Set default value
          this._parametersTranslation[key] = null;
        }
      }
    }
  }

  public setBoundParameters(data) {
   _.forEach(this._boundParameters, ({url, xpaths}, key) => {

      _.forEach(xpaths, xpath => {
        if (!this._parametersTranslation[key]) {
          this._parametersTranslation[key] =
            this.controlDefinition.category !== CATEGORY.EMBEDS.displayName
              ? data[xpath]
              : url.replace(`$(${xpath})$`, data[xpath]);
        } else {
          this._parametersTranslation[key] =
            this.controlDefinition.category !== CATEGORY.EMBEDS.displayName
              ? data[xpath]
              : this._parametersTranslation[key].replace(
                  `$(${xpath})$`,
                  data[xpath]
                );
        }

        if (
          data[xpath] &&
          data[xpath].match &&
          data[xpath].match('/odata/image')
        ) {
          this._parametersTranslation[key] = Global.getBizagiImageUrl(
            this._parametersTranslation[key]
          );
        }
      });
      if (
        this.control.parameters.type &&
        this.controlDefinition.category !== CATEGORY.EMBEDS.displayName &&  this.controlDefinition.category !== CATEGORY.MEDIA.displayName
      ) {
        switch (this.control.parameters.type.toLowerCase()) {
          case 'datetime':
              this._parametersTranslation[
                key
              ] = this.cultureService.dateTimeFormat(
                this._parametersTranslation[key]
              );
            break;

          case 'money':
            this._parametersTranslation[
              key
            ] = this.cultureService.currencyFormat(
              this._parametersTranslation[key]
            );
            break;
          default:
            break;
        }
      }
      if ((this.control.type === 'simplemap' || this.control.type === 'map')  && this.control.parametersBinding[key]) {
        if (key === 'coords') {
          this._getGoogleMapsDirection(key, this._parametersTranslation[key]);
        }
      }

    });
}

  /*
   *   MISC HELPER METHODS
   */
  public navigateTo(url: string) {
    window.location.href = url;
  }

  protected isResizable() {
    return this.controlDefinition.defaultSize.dynamic;
  }

  public getQueryStringByName(name): string {
    const url = window.location.href;
    name = name.replace(/[\[\]]/g, '\\$&');
    const regex = new RegExp('[?&]' + name + '(=([^&#]*)|&|#|$)')
    const results = regex.exec(url);
    if (!results) {
      return null;
    }
    if (!results[2]) {
      return '';
    }
    return decodeURIComponent(results[2].replace(/\+/g, ' '));
  }

  private async _getGoogleMapsDirection(key: string, value: string) {
    if (value) {
      const geocoder = new google.maps.Geocoder;
      geocoder.geocode({address: value}, data => {
        const latLng = data[0]?.geometry?.location?.toJSON();
        if (data && latLng) {
          this._parametersTranslation[
            key
          ] = {
            place: data[0]?.formatted_address,
            latitude: latLng.lat,
            longitude: latLng.lng
          }
        }
      })
    } else {
      this._parametersTranslation[
        key
      ] = {
        place: '',
        latitude: null,
        longitude: null
      }
    }
  }

  /*
   *   ACTION METHODS
   */
  public whenChanged(callback: Function) {
    this._changed.subscribe(params => {
      callback(params);
    });
  }

  protected notifyChange(notifyParams: IControlActionNotification) {
    this._changed.next(notifyParams);
  }

  public updateProperty(key: string, value: any) {
    // Makes a set into this class, the parameter here could be "parameters.filter"
    // so .. this line will do the following: this._parameters["filter"] = value.
    // But sometimes the key could be "styles.fontSize".
    _.set(this, key, value);
    _.set(this.control, key, value);
    this.refreshSubject.next();
    this.cdr.detectChanges();
  }

  public refresh() {}

  private _setParametersOnRender(translation, key) {
    const xpaths = [];

    if (this.controlDefinition.category === CATEGORY.EMBEDS.displayName) {
      const indexesInit = [];
      const indexesEnd = [];
      let translationClone = _.cloneDeep(translation);
      let counter = 0;
      while (translationClone.indexOf('$(') >= 0) {
        indexesInit.push(translationClone.indexOf('$(') + counter);
        indexesEnd.push(translationClone.indexOf(')$') + counter);
        translationClone = translationClone.replace('$(', '').replace(')$', '');
        counter = counter + 4;
      }

      for (let index = 0; index < indexesInit.length; index++) {
        const xpathFind = translation.substring(
          indexesInit[index] + 2,
          indexesEnd[index]
        );
        xpaths.push(xpathFind);
      }
    } else {
      xpaths.push(translation);
    }

    this._boundParameters[key] = {xpaths, url: translation};
    // this.setBoundParameter(key, xpaths, translation);
  }

  public setStore(store: Store<IRenderState>) {
    this.baseStore = store;
  }

  private _isJSON(value) {
    try {
      return (JSON.parse(value) && !!value);
    } catch (e) {
        return false;
    }
  }
}

export {BaseControlComponent};
