import { UpdateLoading } from './../loading/loading.actions';
import { Actions, Effect, ofType } from '@ngrx/effects';
import { Injectable } from '@angular/core';
import { UserService } from '../../../common/services/sites/user.service';
import { LoadUser, LoadUserSuccess, UpdateUser, UpdateUserSuccess, userActions } from './user.actions';
import { switchMap } from 'rxjs/operators';
import { Store } from '@ngrx/store';

@Injectable()
export class UserEffects {
  constructor(private actions$: Actions,
              private _store: Store<IRenderState>,
              private _userService: UserService,
  ) {
  }

  @Effect()
  loadUser = this.actions$.pipe(ofType(userActions.LOAD_USER),
    switchMap(async (action: LoadUser) => {
      this._store.dispatch(new UpdateLoading({ loadingUser: true }));
      const siteUser = await this._userService.getUserByUsername(action.payload);
      return new LoadUserSuccess(siteUser);
    }),
  );

  @Effect()
  updateUser = this.actions$.pipe(ofType(userActions.UPDATE_USER),
    switchMap(async (action: UpdateUser) => {
      return new UpdateUserSuccess(await this._userService.createOrUpdateUserPreferences(action.payload));
    }),
  );
}
