import {debounceTime} from 'rxjs/operators';
import {ControlsService} from './../../../common/services/sites/controls.service';
import {ChangeDetectorRef, Component, ElementRef, Input, NgZone, QueryList, ViewChildren} from '@angular/core';
import {BaseControlComponent} from 'app/common/controls/base.component';
import {Observable, Subject, Subscription} from 'rxjs';
import {ActivatedRoute, NavigationExtras, Router} from '@angular/router';
import {BaseDataControlComponent} from 'app/common/controls/base-data.component';
import {GridCellRenderComponent} from 'app/render/page-render/grid-render/grid-cell-render.component';
import {Global} from 'app/common/misc/global';
import {select, Store} from '@ngrx/store';
import {GridService} from '../../../common/services/sites/grid.service';
import {UpdateGridSize} from '../../store/grid/grid.actions';
import {getCurrentSite} from '../../store/site/site.selector';
import {getCurrentPage} from '../../store/page/page.selector';
import {getGrid} from '../../store/grid/grid.selector';
import {getSiteLanguage} from '../../store/multilanguage/multilanguage.selector';

@Component({
  selector: 'bs-grid-render',
  templateUrl: './grid-render.component.html',
  styleUrls: ['./grid-render.component.scss'],
})
export class GridRenderComponent {
  @Input() controls: Array<IControl>;
  @ViewChildren(GridCellRenderComponent) children: QueryList<GridCellRenderComponent>;

  public renders: { [key: string]: BaseControlComponent } = {};
  public deviceType = Global.getScreenSize();
  public gridLayoutWidth = 0;
  public showGrid = false;
  public isLoading = false;

  public site: ISite;
  private page: IPage;
  private projectId: string;
  private element: any;
  private pageParams: { [key: string]: string };
  private _paramsSubscription: Subscription;
  private gridSubscription: Subscription;
  private _onGridChanges = new Subject();
  private _whenChangeSubject = new Subject();
  public currentLanguage: ILanguageResource;
  private _currentLanguageSubscription: Subscription;
  private _grid: any;
  private _container: any;
  private _currentSiteSubscription: Subscription;
  private _currentPageSubscription: Subscription;

  constructor(
    private _elementReference: ElementRef,
    private _changeDetectorRef: ChangeDetectorRef,
    private _route: ActivatedRoute,
    private _router: Router,
    private _ngZone: NgZone,
    private _controlsServices: ControlsService,
    private _gridService: GridService,
    private _store: Store<IRenderState>
  ) {
  }

  ngOnInit() {
    this._currentSiteSubscription = this._store.pipe(select(getCurrentSite)).subscribe(async (site: ISite) => {
      if (site) {
        this.site = site;
        this.projectId = site.projectId;
      }
    });
    this._currentPageSubscription = this._store.pipe(select(getCurrentPage)).subscribe(async (page: IPage) => {
      if (page) {
        this.page = page;
      }
    });

    const pageSize = this.page.grid.size;
    this._store.dispatch(new UpdateGridSize(pageSize));
    this._paramsSubscription = this._route.queryParams.subscribe(params => {
      this.pageParams = params;
     this._currentLanguageSubscription = this._store.pipe(select(getSiteLanguage)).subscribe((currentLanguage: ILanguageResource) => {
        if (currentLanguage) {
          this.currentLanguage = currentLanguage;
          setTimeout(() => {
            this.initializeGridAndControls();
          }, 300);
        }
      });
    });
    this._onGridChanges.pipe(debounceTime(400)).subscribe((params: any) => {
      const screenSize = (params.target as any).innerWidth;
      if (screenSize) {
        this._store.dispatch(new UpdateGridSize(pageSize));
      }
    });
    this._whenChangeSubject.pipe(debounceTime(200)).subscribe((value:{instance, params})=>{
      this.onControlChanged(value.instance, value.params);
    });
    this.gridSubscription = this._store.pipe(select(getGrid)).subscribe((grid: IGrid) => {
      if (grid && !_.isEmpty(grid)) {
        this._grid = grid;
        this.gridLayoutWidth = grid.layoutSize;
        if (grid.size && grid.prev.size) {
          if (grid.size !== grid.prev.size) {
            this.isLoading = true;
            // Global.adjustGridNodesToLayout(grid, this.controls, this._gridstack);
            this.isLoading = false;
          }
        }
      }
    });
    window.onresize = params => {
      this._onGridChanges.next(params);
      if (this._container) {
        this.updateContainer();
      }
    };
    $(window).resize();
  }

  public ngAfterViewInit() {
    setTimeout(() => {
      this._container = ($('#bs-grid-container') as any);
      this.updateContainer();
    }, 0);
  }

  ngOnDestroy() {
    this._onGridChanges.unsubscribe();
    this._currentLanguageSubscription.unsubscribe();
    this.gridSubscription.unsubscribe();
    this._paramsSubscription.unsubscribe();
    this._currentSiteSubscription.unsubscribe();
    this._currentPageSubscription.unsubscribe();
  }

  private async initializeGridAndControls() {
    // Retrieve native DOM instance
    this.element = $('#grid-stack-render', this._elementReference.nativeElement);
    if (this.children !== null) {
      // Iterate over children to build the list of current renders
      this.children.toArray().forEach((child: GridCellRenderComponent) => {
        // Add to render hash
        const instance = child.currentComponent;
        const control = instance.getControl();
        this.renders[control.id] = instance;

        // Bind control change to this receptor
        instance.whenChanged(params => {
          this._whenChangeSubject.next({instance, params});
        });

        // if the component has a dynamic size, bind the resize handler
        const definition = instance.controlDefinition;
        if (definition.defaultSize.dynamic) {
          instance.onResize.subscribe(size => {
            // this.resize(child.currentComponent.control, size.width, size.height);
          });
        }
      });

      // If We have pageParams we need to fetch the mappings
      if (_.keys(this.pageParams).length > 0) {
        _.each(this.pageParams, (value: any, key: string) => {
          const urlKeyyParts = key.split(':');
          if (urlKeyyParts.length === 4) {
            const targetControlId = urlKeyyParts[0];
            const output = urlKeyyParts[1];
            const property = urlKeyyParts[2];
            const xpath = urlKeyyParts[3];
            if (this.renders[targetControlId]) {
              (this.renders[targetControlId] as BaseDataControlComponent).setXpathFilter(output, xpath);
              this.renders[targetControlId].updateProperty(property, value);
              this._changeDetectorRef.detectChanges();
            }
          }
        });
      }
    }
  }

  private async onControlChanged(source: BaseControlComponent, params: IControlActionNotification) {
    // Use the params to fetch the action from the control
    const control = source.getControl();
    if (control.actions === null) {
      return;
    }
    const action = control.actions[params.action];
    if (action === null) {
      return;
    }

    // Change property events
    if (action.type === 'changeProperty') {
      // Find the target, and set the property based on the mapped param
      const targets = (action as IControlActionChangeProperty).targets;
      for (const targetId in targets) {
        if (targets[targetId]) {
          const targetRender = this.renders[targetId];
          // Each entry from this hash is a component, but inside it could have multiple properties set,
          // so we"ve defined an array of property targets
          targets[targetId].forEach(target => {
            const property = target.property;
            const value = params.value[target.output];
            if(!!targetRender){
              (targetRender as BaseDataControlComponent).setXpathFilter(target.output, target.xpath);
              targetRender.updateProperty(property, value);

              if (target.output === 'category') {
                (targetRender as BaseDataControlComponent).setTypeSearch(target.typeFilter);
              }
            }
          });
        }
      }
    } else if (action.type === 'navigate') {
      const targets = (action as IControlActionNavigation).targets;

      const targetUrl = {};
      _.forEach(targets, (outputs: Array<IControlActionProperty>, controlId: string) => {
        outputs.forEach((output: IControlActionProperty) => {
          targetUrl[controlId + ':' + output.output + ':' + output.property + ':' + output.xpath]  = params.value[output.output];
        });
      });
     const navigationExtras: NavigationExtras = {
        queryParams: targetUrl,
      };
      this._router.navigate([this.site.url, this.site.references[(action as IControlActionNavigation).pageId]], navigationExtras);
    }
  }

  public trackByFn(index, item) {
    return item.id;
  }

  public checkSize(control: IControl, size: number): boolean {
    if (this._grid && this.gridLayoutWidth) {
      return Global.checkSize(control, size, this.deviceType, this._grid);
    }
  }

  public updateContainer() {
    this._ngZone.runOutsideAngular(() => {
      this._gridService.adjustContainerResolution(this._container);
    });
  }
}
