import { Component, Input } from '@angular/core';
import { PublishedSiteService } from '../services/published-site.service';
import {select, Store} from '@ngrx/store';
import {UpdateCurrentLanguage} from '../store/multilanguage/multilanguage.actions';
import {getCurrentSite} from '../store/site/site.selector';
import {Subscription} from 'rxjs';
import {getSiteLanguage, getSiteResources} from '../store/multilanguage/multilanguage.selector';

@Component({
  selector: 'bs-languages-menu-render',
  templateUrl: './languages-menu-render.component.html',
  styleUrls: ['./languages-menu-render.component.scss'],
})
export class LanguagesMenuRenderComponent {
  @Input() public currentLanguage;
  @Input() styles;
  public resources: ISiteResource[];
  public isOpenedLangList = false;
  public site: ISite;
  private _currentSiteSubscription: Subscription;
  private _siteResourcesSubscription: Subscription;
  private _currentLanguageSubscription: Subscription;

  constructor(
    private _store: Store<IRenderState>
  ) {}

  async ngOnInit() {
    this._currentSiteSubscription = this._store.pipe(select(getCurrentSite)).subscribe( (site: ISite) => {
      if (site) {
        this.site = site;
      }
    });
    this._siteResourcesSubscription = this._store.pipe(select(getSiteResources)).subscribe( (siteResources: Array<ISiteResource>) => {
      if (siteResources) {
        this.resources = siteResources;
      }
    });
    this._currentLanguageSubscription = this._store.pipe(select(getSiteLanguage)).subscribe( (siteLanguage: ILanguageResource) => {
      if (siteLanguage) {
        this.currentLanguage = siteLanguage;
      }
    });
  }

  ngOnDestroy() {
    this._currentSiteSubscription?.unsubscribe();
    this._currentLanguageSubscription?.unsubscribe();
    this._siteResourcesSubscription?.unsubscribe();
  }

  async changeSelectedResource(resource: ISiteResource) {
    localStorage.setItem(this.site.id + 'languageId', resource.id);
    this._store.dispatch(new UpdateCurrentLanguage(resource.id));
    this.site.globalSettings.currentResource = resource.id;
    this.isOpenedLangList = false;
  }

  public setMenuVisibility(show) {
    if (show) {
      $('.bs-languages-menu-render-container').addClass('bs-languages-menu-render-container--visible');
    } else {
      $('.bs-languages-menu-render-container--visible').removeClass('bs-languages-menu-render-container--visible');
    }
  }
}
