import { Injectable } from '@angular/core';
import { ActivatedRoute, ActivatedRouteSnapshot, CanActivate, Router, RouterStateSnapshot } from '@angular/router';
import { OAuthClientHelper } from 'app/common/services/oauth/oauth-client.helper';
import { HttpClient } from '@angular/common/http';
import { Global } from '../../misc/global';

@Injectable()
export class ProtectedRouteActivator implements CanActivate {
    public _environment: any;

    constructor(private _oAuthClient: OAuthClientHelper, private _router: Router,
        private _route: ActivatedRoute, private _http: HttpClient) {
    }

    async canActivate(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): Promise<boolean> {
        if (!this._environment || !Global.bizagiEnvironment) {
            this._environment = await this.getExecutionEnvironment();
            Global.bizagiEnvironment = this._environment;
            console.log('Environment from canActivate login.service: ', this._environment);
        }

        // Get url parameters
        const queryString = Global.getQueryString() || {};
        if (await this._oAuthClient.isLoggedOn()) {
            if (!this._oAuthClient.getProfile()) {
                await this._oAuthClient.refreshToken();
            }
            if (this._environment.executionEnvironment === 'ONPREMISES' && route.data.disabledOnPremises) {
                this._router.navigate(['dashboard']);
            }
            if (this._environment.executionEnvironment === 'ONPREMISES' && !this._oAuthClient.hasEditorRole()) {
                this._router.navigate(['unauthorized']);
            }
            return true;
        } else {
            const redirectRoute = _.map(route.url, function (part) { return part.path; }).join('/');
            queryString['redirectRoute'] = redirectRoute;
            const queryParams = route.queryParams;
            this.goToLogin(redirectRoute, queryParams);
            return false;
        }
    }

    async getExecutionEnvironment(): Promise<any> {
        const response = await this._http.get('api/sites/executionEnvironment').toPromise();
        return response;
    }

    goToLogin(redirectRoute: string, queryParams: any) {
        // Get url parameters
        let queryString = Global.getQueryString();
        if (Object.keys(queryParams).length > 0) {
            queryString = JSON.parse(JSON.stringify(queryParams));
        }
        queryString['redirectRoute'] = redirectRoute;

        if (this._environment.executionEnvironment === 'ONPREMISES') {
            queryString['identityServer'] = this._environment.projectUrl;
        }

        // First step call the login page
        this._router.navigate(['login'], {
            queryParams: queryString,
            skipLocationChange: true
        });
    }
}

@Injectable()
export class PublicRouteActivator implements CanActivate {

    constructor(private _oAuthClient: OAuthClientHelper, private _router: Router, private _route: ActivatedRoute) {
    }

    canActivate(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): boolean {
        // Redirect to route if found in state
        const redirectRoute = Global.getRedirectRoute();
        if (redirectRoute) {
            this._router.navigateByUrl(redirectRoute + window.location.search, { preserveQueryParams: true });
            return false;
        }

        return true;
    }
}
