import { Injectable } from '@angular/core';
import { ActivatedRoute, ActivatedRouteSnapshot, CanActivate, Router, RouterStateSnapshot } from '@angular/router';
import { PublishedSiteService } from 'app/render/services/published-site.service';
import { OAuthClientHelper } from 'app/common/services/oauth/oauth-client.helper';
import { HttpClient } from '@angular/common/http';
import { Global } from 'app/common/misc/global';
import {Cookie} from 'ng2-cookies/ng2-cookies';

@Injectable()
export class RouteActivatorRender implements CanActivate {
    public _environment: any;
    constructor(private _oAuthClient: OAuthClientHelper,
                private _router: Router,
                private _route: ActivatedRoute,
                private _publishedSiteServices: PublishedSiteService, private _http: HttpClient) {
    }

    async canActivate(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): Promise<boolean> {
        if (!this._environment) {
            this._environment = await this.getExecutionEnvironment();
            Global.bizagiEnvironment = this._environment;
        }

        // Go to site
        if (route.params.siteName) {
            const siteName = route.params.siteName;
            const redirectRoute = _.map(route.url, function(part) { return part.path; }).join('/');
            const queryParams = route.queryParams;
            const site = await this.loadSite(siteName);
            let pageName = null;

            try {
              pageName = decodeURIComponent(escape(route.params.pageName? route.params.pageName : site.homePageName));
            }
            catch(e) {
              pageName = route.params.pageName? route.params.pageName : site.homePageName;
            }
            const isPrivate = await this._publishedSiteServices.getIsPrivatePage(route.params.siteName, pageName);
            const userLoggedOn = await sessionStorage.getItem('userLoggedOn');
            const userLoggedOnCookie = Cookie.get('USERLOGGEDON');

            if(Global.isInternetExplorer() && userLoggedOnCookie && !userLoggedOn) {
                /* After redirect to external site in explorer, go to login to get user loggedOn   */
                const identityServer = site.identityServer;
                this.goToLogin(identityServer, redirectRoute, queryParams);
                return;
            }
            // If authenticated, and route contains siteName and pageName, go ahead
            if (((!isPrivate && !route.queryParams.code && !userLoggedOn) || await this._oAuthClient.isLoggedOn()) &&
                route.params.pageName != null) {
                if (route.params.pageName && route.params.pageName !== pageName) {
                  this._router.navigate([site.url, pageName]);
                }
                return true;
            }

            if (!isPrivate && !route.queryParams.code && !userLoggedOn || (!isPrivate && !route.params.pageName)) {
                this._router.navigate([site.url, pageName]);
                return true;
            }
            // Validate if is logged on
            if (!(await this._oAuthClient.isLoggedOn())) {
                const identityServer = site.identityServer;
                this.goToLogin(identityServer, redirectRoute, queryParams);
                return;
            }

            // If we have don't a page name, we can redirect to site root
            if (!route.params.pageName) {
                this.redirectToSite(site);
                return;
            }
            return false;
        }

        return true;
    }

    async getExecutionEnvironment(): Promise<any> {
        const response = await this._http.get('api/sites/executionEnvironment').toPromise();
        return response;
    }

    async loadSite(siteName: string) {
        try {
            return await this._publishedSiteServices.getSite(siteName);
        } catch (err) {
            this._router.navigate(['404']);
        }
    }

    redirectToSite(site: ISite) {
        this._router.navigate([site.url, site.homePageName]);
    }

    goToLogin(identityServer: string, redirectRoute: string, queryParams: any) {
        // Get url parameters
        let queryString :any = Global.getQueryString();
        if (Object.keys(queryParams).length > 0) {
            queryString = JSON.parse(JSON.stringify(queryParams));
        }
        queryString.identityServer = identityServer;
        queryString.redirectRoute = redirectRoute;

        // First step call the login page
        this._router.navigate(['login'], {
            queryParams: queryString,
            skipLocationChange: true
        });
    }
}
