import {Action} from '@ngrx/store';

export const themeActions = {
  LOAD_THEMES: '[Themes Actions] Load themes',
  LOAD_THEMES_SUCCESS: '[Themes Actions] Load themes success',
  LOAD_CUSTOM_THEMES: '[Themes Actions] Load custom themes',
  LOAD_CUSTOM_THEMES_SUCCESS: '[Themes Actions] Load custom themes success',
  SAVE_THEME: '[Themes Actions] Save theme',
  SAVE_THEME_SUCCESS: '[Themes Actions] Save theme success',
  REMOVE_THEME: '[Themes Actions] Remove theme',
  REMOVE_THEME_SUCCESS: '[Themes Actions] Remove theme success',
  EDIT_THEME: '[Themes Actions] Edit theme',
  EDIT_THEME_SUCCESS: '[Themes Actions] Edit theme success',
};

export class LoadThemes implements Action {
  readonly type = themeActions.LOAD_THEMES;
  constructor(public payload?: Array<ITheme>) { }
}

export class LoadThemesSuccess implements Action {
  readonly type = themeActions.LOAD_THEMES_SUCCESS;
  constructor(public payload?: Array<ITheme>) { }
}

export class LoadCustomThemes implements Action {
  readonly type = themeActions.LOAD_CUSTOM_THEMES;
  constructor(public payload?: string) { }
}

export class LoadCustomThemesSuccess implements Action {
  readonly type = themeActions.LOAD_CUSTOM_THEMES_SUCCESS;
  constructor(public payload: Array<ITheme>) { }
}


export class SaveTheme implements Action {
  readonly type = themeActions.SAVE_THEME;
  constructor(public payload: {data: any, replace:boolean, undo?:boolean, duplicate?: boolean}) { }
}

export class SaveThemeSuccess implements Action {
  readonly type = themeActions.SAVE_THEME_SUCCESS;
  constructor(public payload?: any) { }
}

export class RemoveTheme implements Action {
  readonly type = themeActions.REMOVE_THEME;
  constructor(public payload: ITheme) { }
}

export class RemoveThemeSuccess implements Action {
  readonly type = themeActions.REMOVE_THEME_SUCCESS;
  constructor(public payload?: any) { }
}

export class EditTheme implements Action {
  readonly type = themeActions.EDIT_THEME
  constructor(public payload: {theme: any, oldThemeName: string, replace: boolean}) { }
}

export class EditThemeSuccess implements Action {
  readonly type = themeActions.EDIT_THEME_SUCCESS
  constructor(public payload?: any) { }
}

export type ThemeActions = LoadThemes
  | LoadThemesSuccess
  |LoadCustomThemes  
  | LoadCustomThemesSuccess
  | SaveTheme
  | SaveThemeSuccess
  | RemoveTheme
  | RemoveThemeSuccess
  | EditTheme
  | EditThemeSuccess
  ;
