import { Component } from '@angular/core';
import { BaseControlComponent } from 'app/common/controls/base.component';
import { ControlDefinition } from 'app/common/decorators/control-definition';
import { CATEGORY, SUBCATEGORY_MAP, MAP_CONTROLS, PROPERTY_SECTIONS } from './../controls-name-definition';

@Component({
  selector: 'bs-map-control',
  templateUrl: './map-control.component.html',
  styleUrls: ['./map-control.component.scss']
})
@ControlDefinition(
  {
    type: MAP_CONTROLS.map.type,
    category: CATEGORY.MEDIA.displayName,
    subcategory: SUBCATEGORY_MAP.displayName,
    displayName: MAP_CONTROLS.map.displayName,
    icon: MAP_CONTROLS.map.icon,
    isDisabled: true,
    styles: {
      marker: {
        section: PROPERTY_SECTIONS.style.key,
        type: 'pin-marker'
      },
      zoom: {
        section: PROPERTY_SECTIONS.style.key,
        type: 'range',
        defaultValue: 24,
        min: 12,
        max: 20
      },
      textColor: {
        section: PROPERTY_SECTIONS.fonts.key,
        type: 'color',
        defaultValue: 'color-third'
      },
      backgroundColor: {
        section: PROPERTY_SECTIONS.background.key,
        type: 'color',
        defaultValue: 'color-first'
      },
      border: {
        section: PROPERTY_SECTIONS.border.key,
        type: 'border',
        defaultValue: '1px solid #000'
      },
      contentFontColor: {
        section: PROPERTY_SECTIONS.fonts.key,
        type: 'color',
        defaultValue: 'color-third'
      },
    },
    parameters: {
      title: {
        type: 'title'
      },
      coords: {
        type: 'coords'
      },
      text: {
        type: 'text'
      }
    },
    defaultSize: {
      width: 561,
      height: 226,
      minWidth: 267,
      minHeight: 126,
      dynamic: true
    }
  }
)

export class MapControlComponent extends BaseControlComponent {

  ngAfterViewInit(): void {
    this.setCurrentPosition();
  }

  private setCurrentPosition() {
    if (navigator.geolocation  && this.parameters && typeof(this.parameters.coords) === 'object') {
      navigator.geolocation.getCurrentPosition((position) => {
        this.parameters.coords.latitude = this.parameters.coords.latitude || position.coords.latitude;
        this.parameters.coords.longitude = this.parameters.coords.longitude || position.coords.longitude;
      });
    }
  }
}
