import { Component, EventEmitter, Input, Output, ViewChild, Inject } from '@angular/core';
import { UserPreferencesComponent } from 'app/common/components/user-preferences/user-preferences.component';
import { BzgModalRef, BZG_MODAL_DATA } from 'bz-genome-angular';

@Component({
  selector: 'bs-user-preferences-render-modal',
  templateUrl: './user-preferences-render-modal.component.html',
  styleUrls: ['./user-preferences-render-modal.component.scss'],
})
export class UserPreferencesRenderModalComponent {
  public buttons = [];
  public currentUserPreferences: IUserPreferences;
  public formatOptions: ICultureFormatOptions;
  public currentLanguage: IBasicLanguageResource;
  public resources: ISiteResource[];
  @Output() onUserUpdate = new EventEmitter();
  @ViewChild('userPreferences', { static: false }) private _userPreferencesComponent: UserPreferencesComponent;

  constructor(
    @Inject(BZG_MODAL_DATA) public data: { currentLanguage, currentUserPreferences, resources },
    private modalRef: BzgModalRef
  ) {}

  ngOnInit() {
    this.currentLanguage = this.data.currentLanguage;
    this.currentUserPreferences = this.data.currentUserPreferences;
    this.resources = this.data.resources;
  }

  public async onModalButtonClicked(button: string) {
    if (button === 'save') {
      this.modalRef.close({
        type: 'save', userPreferences: await this._userPreferencesComponent.getUserPreferences(),
        currentUserPreferences: this.currentUserPreferences,
    });
    } else {
      this.modalRef.close();
    }
  }

}
