import {LoadPagesSuccess, PagesActions, pagesActions} from './pages.actions';


export function pagesReducer(
  state: Array<IBasicPage> = [],
  action: PagesActions
): Array<IBasicPage> {
  switch (action.type) {
    case pagesActions.LOAD_PAGES: {
      return  state;
    }
    case pagesActions.LOAD_PAGE_SUCCESS: {
      return {
        ...state,
       ...(action as LoadPagesSuccess).payload
      };
    }
   default:
      return state;
  }
}
