import {Component} from '@angular/core';
import {BaseControlComponent} from 'app/common/controls/base.component';
import {ControlDefinition} from 'app/common/decorators/control-definition';
import { CATEGORY, PARAGRAPH_CONTROLS, PROPERTY_SECTIONS, SUBCATEGORY_PARAGRAPH } from './../controls-name-definition';

@Component({
  selector: 'bs-paragraph12-control',
  templateUrl: './paragraph12-control.component.html',
  styleUrls: ['./paragraph12-control.component.scss']
})
@ControlDefinition(
  {
    type: PARAGRAPH_CONTROLS.paragraph12.type,
    category: CATEGORY.TEXT.displayName,
    subcategory: SUBCATEGORY_PARAGRAPH.displayName,
    displayName: PARAGRAPH_CONTROLS.paragraph12.displayName,
    icon: PARAGRAPH_CONTROLS.paragraph12.icon,
    styles: {
      fontFamily: {
        section: PROPERTY_SECTIONS.fonts.key,
        type: 'font-family',
        defaultValue: 'inherit'
      },
      fontSize: {
        section: PROPERTY_SECTIONS.fonts.key,
        type: 'font-size',
        defaultValue: '12px',
        max: 72
      },
      letterSpacing: {
        section: PROPERTY_SECTIONS.fonts.key,
        type: 'range',
        defaultValue: 0,
        min: 0,
        max: 60
      },
      align: {
        section: PROPERTY_SECTIONS.style.key,
        type: 'text-align',
        defaultValue: 'left'
      },
      fontColor: {
        section: PROPERTY_SECTIONS.fonts.key,
        type: 'color',
      },
      backColor: {
        section: PROPERTY_SECTIONS.background.key,
        type: 'color',
        defaultValue: 'rgba(0, 0, 0, 0)'
      },
      bold: {
        section: PROPERTY_SECTIONS.fonts.key,
        type: 'boolean',
        defaultValue: false
      }

    },
    parameters: {
      text: {
        type: 'text',
        defaultValue: PARAGRAPH_CONTROLS.paragraph12.displayName
      }
    },
    defaultSize: {
      width: 270,
      height: 81
    }
  }
)
export class Paragraph12ControlComponent extends BaseControlComponent {

  fontWeight() {
    const bold = this.styles.bold;
    return bold === true ? '600' : '100';
  }

}
