import {
  LoadCurrentLanguageSuccess,
  LoadSiteResourcesByName,
  LoadSiteResourcesSuccess,
  multilanguageActions,
  MultilanguageActions,
  UpdateCurrentLanguage, UpdateCurrentLanguageSuccess, UpdateLastBPMLanguage
} from './multilanguage.actions';

export function multilanguageReducer(state: {currentLanguage: ILanguageResource, resources: Array<ISiteResource>, 
  resourcesByName: {[key: string]: ISiteResource}}
= null, action: MultilanguageActions) {
  switch (action.type) {
    case multilanguageActions.UPDATE_CURRENT_LANGUAGE_SUCCESS: {
      const currentSiteLanguage = _.cloneDeep((action as UpdateCurrentLanguageSuccess).payload);
      state.currentLanguage = currentSiteLanguage;
      return {
        ...state,
      };
    }
    case multilanguageActions.LOAD_CURRENT_LANGUAGE_SUCCESS: {
      const currentSiteLanguage = _.cloneDeep((action as LoadCurrentLanguageSuccess).payload);
      state.currentLanguage = currentSiteLanguage;
      return {
        ...state,
      };
    }
    case multilanguageActions.LOAD_SITE_RESOURCES_SUCCESS: {
      const resources = _.cloneDeep((action as LoadSiteResourcesSuccess).payload);
      state.resources = resources;
      return {
        ...state,
      };
    }
    case multilanguageActions.LOAD_SITE_RESOURCES_BY_NAME: {
      const resources = _.cloneDeep((action as LoadSiteResourcesByName).payload);
      state.resourcesByName = resources;
      return {
        ...state,
      };
    }
    case multilanguageActions.UPDATE_LAST_BPM_LANGUAGE_SUCCESS: {
      state.currentLanguage.lastBPMLanguage = (action as UpdateLastBPMLanguage).payload.toString();
      return {
        ...state,
      };
    }
    default:
      return state;
  }
}
