import {LoadPageSuccess, pageActions, PageActions} from './page.actions';


export function pageReducer(
  state: IPage = null,
  action: PageActions
): IPage {
  switch (action.type) {
    case pageActions.LOAD_PAGE: {
      return  state;
    }
    case pageActions.LOAD_PAGE_SUCCESS: {
      return {
        ...state,
       ...(action as LoadPageSuccess).payload
      };
    }


    default:
      return state;
  }
}
