import { Component } from '@angular/core';
import { BaseControlComponent } from 'app/common/controls/base.component';
import { ControlDefinition } from 'app/common/decorators/control-definition';
import { CATEGORY, SUBCATEGORY_SIMPLEMAP, SIMPLEMAP_CONTROLS, PROPERTY_SECTIONS } from './../controls-name-definition';
import { Global } from 'app/common/misc/global';

@Component({
  selector: 'bs-simple-map',
  templateUrl: './simple-map.component.html',
  styleUrls: ['./simple-map.component.scss']
})
@ControlDefinition(
  {
    type: SIMPLEMAP_CONTROLS.simplemap.type,
    category: CATEGORY.MEDIA.displayName,
    subcategory: SUBCATEGORY_SIMPLEMAP.displayName,
    displayName: SIMPLEMAP_CONTROLS.simplemap.displayName,
    icon: SIMPLEMAP_CONTROLS.simplemap.icon,
    isDisabled: true,
    styles: {
      pinInfoColor: {
        section: PROPERTY_SECTIONS.style.key,
        type: 'color',
      },
      pinInfoTextColor: {
        section: PROPERTY_SECTIONS.fonts.key,
        type: 'color',
      },
      marker: {
        section: PROPERTY_SECTIONS.style.key,
        type: 'boolean',
        defaultValue: true
      },
      markerLabel: {
        section: PROPERTY_SECTIONS.fonts.key,
        type: 'boolean',
        defaultValue: true
      },
      zoom: {
        section: PROPERTY_SECTIONS.style.key,
        type: 'range',
        defaultValue: 24,
        min: 12,
        max: 20
      },
    },
    parameters: {
      coords: {
        type: 'coords'
      },
      markerText: {
        type: 'textShort'
      },
    },
    defaultSize: {
      width: 561,
      height: 226,
      minWidth: 267,
      minHeight: 126,
      dynamic: true
    }
  }
)

export class SimpleMapComponent extends BaseControlComponent {
  public iconURL: string;
  public transparentIconURL: string;
  private _pinIconRoute = 'assets/images/point_map.svg';
  private _pinTransparentIconRoute = 'assets/images/point_map_transparent.svg';
  infoVisible = true;

  constructor() {
    super();
    this.iconURL = this._getURLIcon(this._pinIconRoute);
    this.transparentIconURL = this._getURLIcon(this._pinTransparentIconRoute);
  }

  ngOnInit(): void {
    this.infoVisible = this.isActiveLabel();
  }

  ngAfterViewInit(): void {
    this.setCurrentPosition();
  }

  private setCurrentPosition() {
    if (navigator.geolocation && this.parameters && this.parameters.coords) {
      navigator.geolocation.getCurrentPosition((position) => {
        this.parameters.coords.latitude = this.parameters.coords.latitude || position.coords.latitude;
        this.parameters.coords.longitude = this.parameters.coords.longitude || position.coords.longitude;
      });
    }
  }

  onChanged(updCtr) {
    super.onChanged(updCtr);
    this.refreshInfoWindow();
  }

  private isActiveLabel(): boolean {
    return this.control.styles.markerLabel as boolean;
  }

  private _getURLIcon(iconRoute): string {
    if (Global.isOnRenderMode()) {
      return location.origin + location.pathname + '/' + iconRoute;
    } else {
      const url = location.origin + location.pathname;
      const exp = url.substr(url.lastIndexOf('/') + 1) + '$';
      const preUrl = url.replace(new RegExp(exp), '/');
      return preUrl + iconRoute;
    }
  }

  private refreshInfoWindow() {
    this.infoVisible = false;
    this.infoVisible = this.isActiveLabel();
  }
}
