import { Component, ViewChild, ElementRef } from '@angular/core';
import { ControlDefinition } from 'app/common/decorators/control-definition';
import { CATEGORY, SUBCATEGORY_SEARCH, SEARCH_CONTROLS, PROPERTY_SECTIONS } from './../controls-name-definition';
import { ThemeService } from 'app/common/services/sites/theme.service';
import { BaseSearchComponent } from '../base-search.component';
import { Subject, Subscription } from 'rxjs';
import { debounceTime } from 'rxjs/operators';
import { select } from '@ngrx/store';
import { getSiteLanguage } from '../../../render/store/multilanguage/multilanguage.selector';
import { BzgSelectComponent } from 'bz-genome-angular/select/select/select.component';
import { Global } from 'app/common/misc/global';
import { getDefaultFontSite } from 'app/render/store/site/site.selector';
import { DomSanitizer } from '@angular/platform-browser';

@Component({
  selector: 'bs-search-control',
  templateUrl: './search-control.component.html',
  styleUrls: ['./search-control.component.scss']
})
@ControlDefinition(
  {
    type: SEARCH_CONTROLS.search.type,
    category: CATEGORY.BUNDLES.displayName,
    subcategory: SUBCATEGORY_SEARCH.displayName,
    displayName: SEARCH_CONTROLS.search.displayName,
    icon: SEARCH_CONTROLS.search.icon,
    styles: {
      displaySearch: {
        section: PROPERTY_SECTIONS.style.key,
        type: 'display-search',
        defaultValue: 'boxed'
      },
      showCategories: {
        type: 'boolean',
        defaultValue: false,
        category: 'hidden'
      },
      backColor: {
        section: PROPERTY_SECTIONS.background.key,
        type: 'color',
      },
      buttonBackColor: {
        section: PROPERTY_SECTIONS.background.key,
        type: 'color',
      },
      buttonFontColor: {
        section: PROPERTY_SECTIONS.fonts.key,
        type: 'color',
      },
      iconColor: {
        section: PROPERTY_SECTIONS.icon.key,
        type: 'color',
      },
      border: {
        section: PROPERTY_SECTIONS.border.key,
        type: 'border',
        defaultValue: '1px solid #000'
      }
    },
    parameters: {
      searchButtonName: {
        type: 'title',
        defaultValue: 'Search'
      },
      searchPlaceholder: {
        type: 'title',
        defaultValue: 'Type something'
      },
      searchIcon: {
        type: 'icon-selector',
        defaultValue: 'search'
      },
      searchCategoryPlaceholder: {
        type: 'title',
        defaultValue: 'Category'
      },
    },
    defaultSize: {
      width: 800,
      height: 125,
      minWidth: 492
    },
    actions: {
      onSearch: {
        outputs: [
          {
            type: 'text',
            name: 'filter'
          },
          {
            type: 'text-list',
            name: 'category'
          }
        ]
      }
    },
    hiddenOptions: {
      dataManager: true
    }

  }
)
export class SearchControlComponent extends BaseSearchComponent {

  private _searchValueSubject = new Subject<string>();
  public searchValue: string;
  public searchData: ISearchData = {} as ISearchData;
  @ViewChild('TextSearch') searchInput: ElementRef;
  public expandedCategory = false;
  public expandedCategories = false;
  @ViewChild('selectCategories', { static: false }) selectCategories: BzgSelectComponent;
  public themeVariables: Object;
  public isTooltipDisabled = true;
  public globalFont: string;
  public select: JQuery<HTMLElement>;
  private currentLanguage: ILanguageResource;
  private refreshTime = 300;
  private _searchValueSubscription: Subscription;
  private _languageSubscription: Subscription;
  private _defaultFontSubscription: Subscription;

  constructor(private _themeServices: ThemeService,
    private _domSanitizer: DomSanitizer) {
    super();
    this._searchValueSubscription = this._searchValueSubject.pipe(
      debounceTime(this.refreshTime)).subscribe((value) => {
        this.notifyChange({
          action: 'onSearch',
          value: {
            filter: value,
            category: this.searchData.category || ''
          }
        });
      });
  }

  public onSearchHandler(checkNavigate = false) {
    if (!checkNavigate || this.actions['onSearch'].type !== 'navigate')  {
      this._searchValueSubject.next(this.searchValue);
    }
  }

  ngAfterViewInit() {
    this.select = Global.modifyWidhSelect(
      'bzg-select',
      'click',
      '.cdk-overlay-container .bz-select-options', null);
  }

  ngOnDestroy() {
    this._searchValueSubscription?.unsubscribe();
    this._languageSubscription?.unsubscribe();
    this.select?.unbind('click');
  }

  public getIconColorStyle() {
    return this._domSanitizer.bypassSecurityTrustStyle(this.styles['iconColor'] ? 'color: '
    + this.styles['iconColor'] + ' !important' : '');
  }

  ngOnInit() {
    const isActiveCategories = this.styles['showCategories'];
    this._themeServices.refreshThemeVariables();
    this.themeVariables = this._themeServices.getThemeVariables();
    this.actions = this.control.actions;
    if (this.isRenderingMode()) {
      if (isActiveCategories) {
        this.loadCategories();
      }
      this._languageSubscription = this.baseStore.pipe(select(getSiteLanguage)).subscribe((language) => {
        if (this.currentLanguage && this.currentLanguage.language.name !== language.language.name) {
          if (this.actions['onSearch'].type !== 'navigate') {
            if (this.selectCategories?.value) {
              this.selectCategories.value = '';
            }
            this.searchData.category = '';
            this.clearSearch(0);
          }
          if (isActiveCategories) {
            this.loadCategories();
          }
        }

        this.currentLanguage = language;
      })
      this._defaultFontSubscription = this.baseStore.pipe(select(getDefaultFontSite)).subscribe((fontFamily: any) => {
        this.globalFont = fontFamily;
      });
    }
  }

  public getBackgroundColor() {
    if (this.styles['displaySearch'] === 'boxed') {
      return this.themeVariables && this.themeVariables[this.styles['backColor']]
        ? this.themeVariables[this.styles['backColor']] : this.styles['backColor'];
    } else {
      return 'transparent';
    }
  }

  public clearSearch(time = 300) {
    this.refreshTime = time;
    this.searchValue = '';
    if (this.actions['onSearch'].type !== 'navigate') {
      this._searchValueSubject.next(this.searchValue);
    }
  }

  public expandCategories(event) {
    this.expandedCategory = !this.expandedCategory;
  }

  public setCategoryData(value) {
    this.searchData.category = value;
    if (this.actions['onSearch'].type !== 'navigate') {
      this.onSearchHandler();
    }
    const textWidth = Global.getHtmlTextWidth(value,
      '14px',
      this.globalFont);
    this.isTooltipDisabled = textWidth <= 229;
  }
}
