import {Actions, Effect, ofType} from '@ngrx/effects';
import {Injectable} from '@angular/core';
import {switchMap} from 'rxjs/operators';
import {LoadSite, LoadSiteSuccess, siteActions} from './site.actions';
import {Store} from '@ngrx/store';
import {PublishedSiteService} from '../../services/published-site.service';
import {Observable} from 'rxjs';

@Injectable()
export class SiteEffects {
  constructor(
    private actions$: Actions,
    private _publishedSiteServices: PublishedSiteService,
    private _store: Store<IRenderState>,
  ) {
  }


  @Effect()
  loadSite$ = this.actions$.pipe(
    ofType(siteActions.LOAD_SITE),
    switchMap(async (action: LoadSite) => {
      return new LoadSiteSuccess(await this._publishedSiteServices.getSite(action.payload));
    }),

  );

}
