import { from as observableFrom, Observable, of, Subject, Subscription } from 'rxjs';

import { map } from 'rxjs/operators';
import { Injectable } from '@angular/core';
import { RestApiSevice } from 'app/common/services/oauth/rest-api.service';
import * as moment from 'moment';
import { Global } from 'app/common/misc/global';
import { UserService } from 'app/common/services/sites/user.service';
import { OAuthClientHelper } from 'app/common/services/oauth/oauth-client.helper';
import {
    ECultureCurrencySymbolPosition,
    ECultureDateTypes,
    ECultureNegativeNumberFormat,
    EDateFormatStyles
} from 'app/common/models/ECultureFormats';
import { select, Store } from '@ngrx/store';
import { getCurrentSiteLanguage } from '../../../editor/site-editor/store/multilanguage/multilanguage.selector';
import { getSiteLanguage } from '../../../render/store/multilanguage/multilanguage.selector';

@Injectable()
export class CultureSettingsService {

    private _currentLanguage$: Observable<ILanguageResource>;
    private _currentLanguageSubscription: Subscription;
    private _currentLanguage: ILanguageResource;

    public userPreferencesStatus: Subject<any>;
    public userFromSites;

    private _endpoints =
        {
            workportalCulture: 'http://localhost/BizAgiBPM/Rest/Multilanguage/Client?cultureName=en-US',
            formatOptions: 'assets/mockups/cultureFormatOptions.json',
        };

    private _preferences: IUserPreferences;
    private _languagePreferences: IUserPreferences;
    private _formatOptions: ICultureFormatOptions;

    constructor(protected _restApiService: RestApiSevice,
        private _userService: UserService,
        private _oAuthClientHelper: OAuthClientHelper,
        private _store: Store<IEditorState>, private _storeRender: Store<IRenderState>) {
        this.userPreferencesStatus = new Subject();
        if (Global.isOnRenderMode()) {
            this._currentLanguage$ = this._storeRender.pipe(select(getSiteLanguage));
        } else {
            this._currentLanguage$ = this._store.pipe(select(getCurrentSiteLanguage));
        }

        this._currentLanguage$.subscribe((language) => {
            if (language) {
                this._languagePreferences = language.culture;
            }
        });

    }

    public getFormatOptions(): Observable<ICultureFormatOptions> {
        if (this._formatOptions) {
            return of(this._formatOptions);
        }
        return this._restApiService.get({ url: this._endpoints.formatOptions }).pipe(map((response: any) => {
            this._formatOptions = (response as ICultureFormatOptions);
            return this._formatOptions;
        }));
    }

    public async getFixedPreferences(preferences, language?:ILanguageResource): Promise<any> {
        const formatOptions = await this.getFormatOptions().toPromise();
        if (!preferences) {
            const currentLanguage = await this._setDefaultLanguageResource();
            preferences = !!currentLanguage ? currentLanguage.culture
                        : !!language ? language.culture
                        : this.getDefaultUserPreferences();
        }
        if (formatOptions.dateFormat.long.indexOf(preferences.dateFormatLong) < 0) {
            preferences.dateFormatLong = formatOptions.dateFormat.long[1];
        }
        if (formatOptions.dateFormat.short.indexOf(preferences.dateFormatShort) < 0) {
            preferences.dateFormatShort = formatOptions.dateFormat.short[1];
        }
        if (!_.some(formatOptions.timeFormat, (time) => time.format === preferences.timeFormat)) {
            preferences.timeFormat = formatOptions.timeFormat[0].format;
        }

        return preferences;
    }

    public getPreferences(): Observable<IUserPreferences> {
        if (Global.isOnRenderMode()) {
            return observableFrom(this.getPreferencesFromUser()).pipe(map((preferences: IUserPreferences) => {
                this._preferences = preferences;
                return this._preferences;
            }));
        }
        return observableFrom(this.getPreferencesFromDefaultResource()).pipe(map((preferences: IUserPreferences) => {
            this._preferences = preferences;
            return this._preferences;
        }));
    }

    public getUserPreferences(): Observable<IUserSiteProfile> {
        return observableFrom(this.getUser()).pipe(map((user: IUserSiteProfile) => {
            return user;
        }));
    }

    private async getUser(): Promise<IUserSiteProfile> {
        const username = await this._oAuthClientHelper.getUsername();
        this.userFromSites = await this._userService.getUserByUsername(username);
        if (!this.userFromSites.preferences) {
            this.userFromSites.preferences = this.getDefaultUserPreferences();
        }

        return new Promise<IUserSiteProfile>((
            resolve) => {
            resolve(this.userFromSites);
        });
    }

    public async getPreferencesFromUser(): Promise<IUserPreferences> {
        const username = await this._oAuthClientHelper.getUsername();
        const userFromSites = await this._userService.getUserByUsername(username);
        if (userFromSites) {
            return new Promise<IUserPreferences>((
                resolve) => {
                resolve(userFromSites.preferences && userFromSites.preferences.isCustom ? (userFromSites.preferences as IUserPreferences) :
                    this.getPreferencesFromDefaultResource());
            });
        } else {
            return this.getPreferencesFromDefaultResource();
        }
    }

    ngOnDestroy() {
        this._currentLanguageSubscription.unsubscribe();
    }

    public async getPreferencesFromDefaultResource(): Promise<IUserPreferences> {
        this._currentLanguage = await this._setDefaultLanguageResource();
        return new Promise<IUserPreferences>((resolve) => { resolve(this._currentLanguage
            ? this._currentLanguage.culture : this.getDefaultUserPreferences()); });
    }

    public dateTimeFormat(date: string, dataType: ECultureDateTypes = ECultureDateTypes.date, userPreferences?: IUserPreferences): string {
        const currentUserPreferences = userPreferences ? userPreferences : this._preferences;
        if (!moment(date).isValid()) {
            return '...';
        }
        let format: string;
        switch (dataType) {
            case ECultureDateTypes.date:
                format = this._formatToDate(currentUserPreferences);
                break;
            case ECultureDateTypes.time:
                format = currentUserPreferences.timeFormat;
                break;
            case ECultureDateTypes.dateTime:
                format = `${this._formatToDate(currentUserPreferences)} ${currentUserPreferences.timeFormat}`;
                break;
        }

        const validFormat = this.getValidFormat(format);
        return moment(date).format(validFormat);
    }

    public currencyFormat(money: string, userPreferences?: IUserPreferences): string {
        const currentPreferences = userPreferences ? userPreferences : this._preferences;
        const moneyNumber = parseFloat(money);
        if(isNaN(moneyNumber)){
            return '';
        }
        if (currentPreferences.decimalDigits > 0) {
            money = moneyNumber.toFixed(currentPreferences.decimalDigits).replace('.', currentPreferences.decimalSeparator);
        }
        money = !_.isNil(money) ? money.toString() : '';
        money = this.negativeNumberFormatFloat(money, currentPreferences.negativeNumbers);
        money = money.toString().replace(/\B(?=(\d{3})+(?!\d))/g, currentPreferences.thousandsSeparator);
        return currentPreferences.currencySymbolPosition === ECultureCurrencySymbolPosition.before
            ? `${this._languagePreferences.currencySymbol}${money}`
            : `${money}${this._languagePreferences.currencySymbol}`;
    }

    public floatFormat(float: string, userPreferences?: IUserPreferences): string {
        const currentPreferences = userPreferences ? userPreferences : this._preferences;
        const floatNumber = parseFloat(float);
        if(isNaN(floatNumber)) {
            return '';
        }
        if (currentPreferences.decimalDigits > 0) {
            float = floatNumber.toFixed(currentPreferences.decimalDigits).replace('.', currentPreferences.decimalSeparator);
        }
        float = !_.isNil(float) ? float.toString() : '';
        float = this.negativeNumberFormatFloat(float, currentPreferences.negativeNumbers);
        float = float.replace(/\B(?=(\d{3})+(?!\d))/g, currentPreferences.thousandsSeparator);
        return float;
    }

    public negativeNumberFormat(numberToFormat: number, negativeCustomFormat?: ECultureNegativeNumberFormat): string {
        if (negativeCustomFormat > 0 && numberToFormat < 0) {
            return `(${numberToFormat.toString().replace('-', '')})`;
        }
        return `${numberToFormat}`;
    }

    public negativeNumberFormatFloat(numberToFormat: string, negativeCustomFormat?: ECultureNegativeNumberFormat): string {
        if (negativeCustomFormat > 0 && parseFloat(numberToFormat) < 0) {
            return `(${numberToFormat.replace('-', '')})`;
        }
        return `${numberToFormat}`;
    }

    public async openUserPreferences() {
        this.userPreferencesStatus.next({ isOpen: true });
    }

    public closeUserPreferences() {
        this.userPreferencesStatus.next({ isOpen: false });
    }

    public getDefaultUserPreferences(): IUserPreferences {
        return {
            dateFormatStyle: 0,
            dateFormatLong: 'dddd, MMMM DD, YYYY',
            dateFormatShort: 'MM/DD/YYYY',
            dateSeparator: '/',
            timeFormat: 'hh:mm:ss A',
            negativeNumbers: 0,
            decimalDigits: 0,
            thousandsSeparator: ',',
            decimalSeparator: '.',
            currencySymbol: '$',
            currencySymbolPosition: 0,
            toolsBarVisible: true,
        };
    }

    public countDecimals(value) {
        if (Math.floor(value) === value) { return 0; }
        return value.toString().split('.')[1].length || 0;
    }

    private _formatToDate(userPreferences: IUserPreferences): string {
        if (userPreferences.dateFormatStyle === EDateFormatStyles.long) {
            return userPreferences.dateFormatLong;
        } else {
            return userPreferences.dateFormatShort.replace(new RegExp('/', 'g'), userPreferences.dateSeparator);
        }
    }

    private _setDefaultLanguageResource(): Promise<ILanguageResource> {
        return new Promise<ILanguageResource>((resolve, reject) => {
            this._currentLanguageSubscription = this._currentLanguage$.subscribe(async (currentLanguage: ILanguageResource) => {
                if (currentLanguage) {
                    currentLanguage.culture = await this.getFixedPreferences(currentLanguage.culture);
                    resolve(currentLanguage);
                }
                else {
                    resolve(null);
                }
            });
        });
    }

    private getValidFormat(format: string) {
        let newFormat = (format.replace('yyyy', 'YYYY')).replace('yy', 'YY').replace('dddd, MMMM d, YYYY', 'dddd, MMMM DD, YYYY');
        newFormat = (newFormat.replace('tt', ''));
        return newFormat;
    }
}
