import {Component} from '@angular/core';
import {BaseDataControlComponent} from 'app/common/controls/base-data.component';
import {Global} from 'app/common/misc/global';
import {ControlDefinition} from 'app/common/decorators/control-definition';
import {
  CATEGORY,
  SUBCATEGORY_GALLERY_LINK,
  GALERRY_LINK_CONTROLS,
  PROPERTY_SECTIONS,
} from './../controls-name-definition';
import { DefaultValues } from '../../misc/default-values';

@Component({
  selector: 'bs-gallery-link-control',
  templateUrl: './gallery-link-control.component.html',
  styleUrls: ['./gallery-link-control.component.scss']
})
@ControlDefinition(
  {
    type: GALERRY_LINK_CONTROLS.galleryLink.type,
    category: CATEGORY.BUNDLES.displayName,
    subcategory: SUBCATEGORY_GALLERY_LINK.displayName,
    displayName: GALERRY_LINK_CONTROLS.galleryLink.displayName,
    icon: GALERRY_LINK_CONTROLS.galleryLink.icon,
    styles: {
      designDisplay: {
        type: 'design',
        defaultValue: 'title-bottom',
        category: 'hidden',
        displays: {
          'title-bottom': {
            icon: 'image-gallery-display-bottom',
            text: 'siteeditor.layouts.DESIGN_TITLE_BOTTOM',
          },
          'title-top': {
            icon: 'image-gallery-display-top',
            text: 'siteeditor.layouts.DESIGN_TITLE_TOP',
          }
        }
      },
      showActions: {
        section: PROPERTY_SECTIONS.style.key,
        type: 'boolean',
        defaultValue: 'true'
      },
      layout: {
        type: 'layout',
        defaultValue: 'three',
        category: 'hidden',
        layouts: {
            two: {
              icon: 'two-columns-thin',
              text: 'TWO_COLUMNS',
              columns: 2
            },
            three: {
              icon: 'three-columns-thin',
              text: 'THREE_COLUMNS',
              min: 50,
              columns: 3
            },
            four: {
              icon: 'four-columns-thin',
              text: 'FOUR_COLUMNS',
              min: 66,
              columns: 4
            }
          }
      },
      multipageItemView: {
        type: 'radio',
        defaultValue: 'Pagination',
        category: 'layout',
        values: ['Pagination']
      },
      verticalItemSpacing: {
        type: 'range',
        defaultValue: 12,
        category: 'layout',
        step: 4,
        min: 4,
        max: 40,
        unitValue: 'px',
        headerName: 'Rows'
      },
      displayedItems: {
        type: 'range',
        defaultValue: 4,
        category: 'layout',
        min: 1,
        max: 10
      },
      backColor: {
        section: PROPERTY_SECTIONS.background.key,
        type: 'color',
        defaultValue: '#FFF'
      },
      boxShadow: {
        section: PROPERTY_SECTIONS.border.key,
        type: 'text',
        defaultValue: '0 1px 1px 0 rgba(0,0,0,.5), 0 1px 6px 0 rgba(0,0,0,.02)'
      },
      border: {
        section: PROPERTY_SECTIONS.border.key,
        type: 'border',
        defaultValue: '1px solid #e4e8ea'
      },
      cornerRadius: {
        section: PROPERTY_SECTIONS.border.key,
        type: 'range',
        defaultValue: 0,
        min: 6,
        max: 50
      }
      },
    parameters: {
      data: {
        type: 'data',
        size: 10
      }
    },
    inputs: ['parameters.filter'],
    defaultSize: {
      width: 963,
      height: 276,
      minWidth: 300,
      minHeight: 124,
      dynamic: true
    },
    bizagiConnections: {
      cases: true,
      collections: true,
      searches: true,
      config: {
        image: 'image-gallery-config.png',
        coords: {
          image: {
            w: 56,
            h: 61,
            x: 10,
            y: 6
          },
          title: {
            w: 56,
            h: 9,
            x: 10,
            y: 73
          }
        }
      }
    },
    fields: {
      image: {
        type: 'image',
        displayName: 'siteeditor.properties.IMAGE',
        styles: {
          imagesSize: {
            type: 'image-size',
            defaultValue: 'scale'
          }, opacity: {
            type: 'range',
            defaultValue: 100,
            min: 0,
            max: 100
          },
          background: {
            type: 'color',
            defaultValue: 'rgba(0, 0, 0, 0)'
          },
          border: {
            type: 'border',
            defaultValue: 'border-none'
          }
        }
      },
      title: {
        type: 'title',
        displayName: 'siteeditor.properties.TITLE',
        styles: {
          fieldLabel: {
            type: 'label-field',
            defaultValue: {
              isActive: false,
              text: 'Label:'
            }
          },
          fontFamily: {
            type: 'font-family',
            defaultValue: 'inherit'
          },
          fontSize: {
            type: 'font-size',
            defaultValue: '16px',
            max: 48
          },
          letterSpacing: {
            type: 'range',
            defaultValue: 0,
            min: 0,
            max: 60
          },
          align: {
            type: 'text-align',
            defaultValue: 'left'
          },
          fontColor: {
            type: 'color',
            defaultValue: 'color-first'
          },
          backColor: {
            type: 'color',
            defaultValue: 'rgba(0, 0, 0, 0)'
          },
          fontWeight: {
            type: 'font-weight',
            defaultValue: 'normal'
          }
        }
      }
    },
    hiddenOptions: {
      modifyFields: true,
      designOption: false
    }
  }
)

export class GalleryLinkControlComponent extends BaseDataControlComponent {

  public isDesktop = false;
  public isResponsive = false;
  public defaultPicture = DefaultValues.getDefaultPicture();
  public isOnRenderMode: boolean;

}
