import { Injectable } from '@angular/core';
import { RestApiSevice } from '../oauth/rest-api.service';
import { Observable } from 'rxjs';
import { Global } from 'app/common/misc/global';

@Injectable()
export class FontsServiceCommon {
  constructor(private _httpClient: RestApiSevice) {
  }

  public loadAllFonts(defaultFontFamily: string = '') {
    this._httpClient.get({ url: 'api/sites/fonts' }).subscribe((files) => {
      const fileNames: string[] = [];
      _.forEach(files, (value, key) => {
        fileNames.push(key);
        const contentStyle = {
          'font-family': key,
          'src': this.getFilesByFont(value)
        }
        const style = document.createElement('style');
        style.type = 'text/css';
        style.textContent = `@font-face {${this.getCSS(contentStyle)}}`;
        $('head').prepend(style);
      });

      if (defaultFontFamily) {
        $('body').attr('style', 'font-family: ' + this.getFormattedFontName(defaultFontFamily) + ' !important');
        $('style:first').append(`
                  bs-confirm-dialog-modal bzg-modal-footer button {
                    font-family: ${this.getFormattedFontName(defaultFontFamily)} !important;
                  }`.trim());
      }
    });
  }

  public getFonts(): Observable<any> {
    return this._httpClient.get({ url: 'api/sites/fonts' });
  }

  public removeFonts(fonts: string[]): Observable<any> {
    return this._httpClient.put({ url: 'api/sites/fonts', body: fonts, });
  }

  public saveFonts(formData: FormData) {
    return this._httpClient.postFormData({
      url: 'api/sites/fonts',
      body: formData
    });
  }

  public async loadFontsFilesByFamilyName(familyName: string) {
    const fontsBlobs: Blob[] = [];
    if (familyName) {
      const fonts = await this._httpClient.get({ url: 'api/sites/fonts/family/' + familyName }).toPromise();
      _.each(fonts, (font) => {
        const blob = Global.b64toBlob(font, 'application/octet-stream');
        fontsBlobs.push(blob);
      });

      return fontsBlobs;
    }
  }

  public addClassUsingDefaultFont(familyFontName: string) {
    if (familyFontName) {
      _.forEach($('bs-workspace style'), (style: any) => {
        style.remove();
      });
      $('<style>')
        .prop('type', 'text/css')
        .html(`.sites-custom-font {font-family: ${this.getFormattedFontName(familyFontName)};}`)
        .appendTo('bs-workspace');
    }
  }

  private getFormattedFontName(fontName: string) {
    return fontName.indexOf(' ') >= 0 ? `"${fontName}"` : fontName;
  }

  private getCSS(style) {
    return Object.keys(style).reduce((prev, curr) => {
      const escapedParameter = Global.escapeOutput(curr.split(/(?=[A-Z])/).join('-').toLowerCase());
      const escapedStyle = Global.escapeOutput(style[curr]);
      return `${prev += escapedParameter}:
        ${curr === 'font-family' ? this.getFormattedFontName(escapedStyle) : escapedStyle};`
    }, '');
  }

  private getFilesByFont(files): string {
    let strFilesUrls = '';
    _.forEach(files, (file) => {
      strFilesUrls += `url('api/sites/fonts/${file}'), `;
    });
    return strFilesUrls.trim().slice(0, -1);
  }
}
