import { Component, Input, Output, EventEmitter, SimpleChanges, ViewChild, ElementRef } from '@angular/core';
import { Global } from 'app/common/misc/global';
import { TranslateMultiLangPipe } from 'app/common/pipes/translate-multilang.pipe';

interface CollectionWidthColumn {
  [key: string]: {[key: string]: number[]}
}
@Component({
  selector: 'bs-my-cases-control-grid',
  templateUrl: './my-cases-control-grid.component.html',
  styleUrls: ['./my-cases-control-grid.component.scss']
})
export class MyCasesControlGridComponent {
  @Output() showCaseEvent: EventEmitter<any> = new EventEmitter();
  @Output() changeOrderColumn: EventEmitter<any> = new EventEmitter();
  @Input() data;
  @Input() cellSpacingMode;
  @Input() dataNoShow;
  @Input() stylesComponent;
  @Input() controlId;
  @Input() selectedProcess;
  @ViewChild('table') table: ElementRef;
  public elementActual: JQuery<any>;
  public widthColumn = [];
  public widthTable = 0;
  public heightTable = '0px';
  public isRenderMode = Global.isOnRenderMode();

  constructor() { }

  ngOnChanges(changes: SimpleChanges) {
    if (this.data?.headers) {
      setTimeout(() => {
          this.elementActual = $(this.table.nativeElement);
          this.verifyWidthProcess(this.selectedProcess);
          this.updateHeightTable();
        }, 0);
    }
  }

  public selectRow(process) {
    this.showCaseEvent.emit(process);
  }

  public orderColumn(event) {
    this.changeOrderColumn.emit(event);
  }

  public calculeWidth(text: string) {
    return text.length + '%';
  }

  public changeResize(event) {
    this.widthColumn[event.columnIndex-1] = event.sizeInPx + 'px';
    let values = JSON.parse(localStorage.getItem('myCasesColumnsWidth')) as CollectionWidthColumn;
    const control: CollectionWidthColumn = {};
    control[this.controlId] = {
      [this.selectedProcess ? this.selectedProcess : 'all']: this.widthColumn
    }
    values = _.merge(values, control);
    localStorage.setItem('myCasesColumnsWidth', JSON.stringify(values));
  }

  private calculateWidthColumn() {
    for (let index = 0; index < this.data.headers.length; index++) {
      const cells = this.elementActual.find('.bz-table-adv__body .bz-table-adv__row .bz-table-adv__cell:nth-child(' + (index + 2) + ') > div').toArray();
      if (cells.length > 0) {
        const max = cells.reduce((prev, current) => (prev.innerText.length > current.innerText.length) ? prev : current);
        this.widthColumn[index] = max.innerText;
      }
    }
    this.widthColumn = this.widthColumn.map((value,index) => {
      const widthHeader = Global.getHtmlTextWidth(this.data.headers[index] ? this.data.headers[index].displayName : '',
      this.stylesComponent['headerFontSize'], this.stylesComponent['headerFontFamily']);
      const widthCell = Global.getHtmlTextWidth(value,
        this.stylesComponent['contentFontSize'], this.stylesComponent['contentFontFamily'])
      return (widthHeader > widthCell ? widthHeader : widthCell) + 62;
    });
    const widthColumns = this.widthColumn.reduce((acumulated, actual) => acumulated + actual, 0) + 50;
    const widthTable = this.elementActual.width();
    const extraWidth = widthColumns < widthTable ? (widthTable - widthColumns) / this.widthColumn.length : 0;
    this.widthColumn.forEach((value, index) => this.widthColumn[index] = value + extraWidth + 'px');
  }

  private updateHeightTable() {
    let heightTable;
    let heightHeader;
    if (Global.isFirefox()) {
      const parentComponent = this.elementActual.parents('.bs-my-cases');
      heightTable = parentComponent.height() -
        parentComponent.find('.bs-my-cases-header').height() -
        parentComponent.find('.bs-my-cases-body__options').height() -
        parentComponent.find('.bs-my-cases-body__paginator').height();
    } else {
      heightHeader = this.elementActual.find('.bz-table-adv__header').height();
      heightTable = this.elementActual.parent().parent().height() - heightHeader;
    }

    const widthTable = this.elementActual.find('> div').width();
    const rows = this.elementActual.find('.bz-table-adv__row');
    const totalHeightRows = rows.length * rows.height();

    if (Global.isFirefox()) {
      this.heightTable = (heightTable - 80) + 'px';
    } else {
      if (totalHeightRows > heightTable) {
        heightTable = heightHeader + heightTable;
        this.heightTable = this.widthTable >= widthTable ? (heightTable + 21 - 10) + 'px' : (heightTable + 21) + 'px';
      } else {
        this.heightTable = 'auto';
      }
    }
  }

  private verifyWidthProcess(process: string) {
    const values = JSON.parse(localStorage.getItem('myCasesColumnsWidth')) as CollectionWidthColumn;

    if (values) {
      this.widthColumn = values[this.controlId] && values[this.controlId][process || 'all'] ? values[this.controlId][process || 'all'] : [];

      if (!this.widthColumn.length) {
        this.calculateWidthColumn();
      }
    } else {
      this.calculateWidthColumn();
    }
  }
}
