import { Component, Inject, NgZone } from '@angular/core';
import { BzgModalRef, BZG_MODAL_DATA } from 'bz-genome-angular';

@Component({
    selector: 'bs-select-organization-modal',
    templateUrl: './select-organization-modal.component.html',
    styleUrls: ['./select-organization-modal.component.scss']
})
export class SelectOrganizationModalComponent {
    public organizationSelected;
    constructor(
        private _modalComponentRef: BzgModalRef,
        @Inject(BZG_MODAL_DATA) public data: {
            organizations: any,
        }
    ) { }


    public selectOrganization(organization) {
        this.organizationSelected = organization;
    }

    public close() {
     this._modalComponentRef.close();
    }


    public confirm() {
        this._modalComponentRef.close({
            type: 'confirm',
            params: {
                organizationId: this.organizationSelected.id
        }});
    }
}
