import { Component, Inject, NgZone } from '@angular/core';
import { BzgModalRef, BZG_MODAL_DATA } from 'bz-genome-angular';

@Component({
    selector: 'bs-confirm-dialog-modal',
    templateUrl: './confirm-dialog-modal.component.html',
    styleUrls: ['./confirm-dialog-modal.component.scss']
})
export class ConfirmDialogModalComponent {
    public checkBoxValue = false;
    constructor(
        private _modalComponentRef: BzgModalRef,
        private _ngZone: NgZone,
        @Inject(BZG_MODAL_DATA) public data: {
            message: string,
            title: string,
            confirmButtonText?: string,
            buttonColor?: string,
            cancelButton?: boolean,
            cancelButtonText?: string,
            checkBox: boolean,
            checkBoxMessage: string,
            icon?: string,
            iconColor?: string,
            optionalButtonText?: string,
            optionalButton?: string,
        }
    ) { }


    close() {
        this._ngZone.run(() => {
            this._modalComponentRef.close();
        });
    }

    performOptional() {
      this._ngZone.run(() => {
        this._modalComponentRef.close({
            type: 'optional',
            params: {
                checkBoxValue: this.checkBoxValue
        }});
      });
    }

    confirm() {
        this._ngZone.run(() => {
            this._modalComponentRef.close({
                type: 'confirm',
                params: {
                    checkBoxValue: this.checkBoxValue
            }});
        });
    }
}
