import {createSelector} from '@ngrx/store';

const siteEditorSelector = (state: IEditorState) => state.siteEditor;

export const getCurrentPageState = createSelector(
  siteEditorSelector,
  (state: ISiteEditorState) => state.currentPageState
);
export const getSitePages = createSelector(
  siteEditorSelector,
  (state: ISiteEditorState) => _.orderBy(_.filter(state.sitePages, (page) => {
    if(!state.currentSite) {
      return null;
    }
    const nav = _.find(state.currentSite.header.navigation, 
      (navf : any)=> (navf.pageId === page.id || navf.references?.length
        && _.some(navf.references, (ref)=> ref.pageId === page.id)));
    return nav;
  }), 'name')
);
export const getHero = createSelector(
  siteEditorSelector,
  (state: ISiteEditorState) => state.currentPageState?.hero
);
export const getHeroAction = createSelector(
  siteEditorSelector,
  (state: ISiteEditorState) => state.heroAction
);
export const getSitePagesNoContextualized = createSelector(
  siteEditorSelector,
  (state: ISiteEditorState) => 
  _.orderBy(_.filter(state.sitePages, (page)=> !page.isContextualized || page.id === state.currentPageState.id), 'name')
);