
import { map } from 'rxjs/operators';
import { Injectable } from '@angular/core';
import { RestApiSevice } from 'app/common/services/oauth/rest-api.service';
import { DomSanitizer } from '@angular/platform-browser';
import { BzgModalRef, BzgModalService } from 'bz-genome-angular';
import { ConfirmDialogModalComponent } from 'app/common/components/confirm-dialog-modal/confirm-dialog-modal.component';
import { Observable } from 'rxjs/Rx';

@Injectable()
export class ImagesService {

  private endpoints: any = {
    saveImages: 'api/{projectId}/images/{resourceId}/{id}/{isPreview}',
    getImages: 'api/{projectId}/sites/{siteId}/images/{imageName}',
    getImagesBlob: 'api/sites/imagesblob',
    deleteImages: 'api/{projectId}/images/{id}',
    duplicateImage: 'api/{projectId}/images/{resourceId}/{imageIdOrigin}/duplicate/{imageIdDestination}',
    duplicateImages: 'api/{projectId}/images/{resourceId}/duplicateMultiple/'
  };

  private pageCache: { [key: string]: IPage };
  private _modalRef: BzgModalRef;

  constructor(protected _restApiService: RestApiSevice,
    private _sanitizer: DomSanitizer,
    private _modalService: BzgModalService) {
    this.pageCache = {};
  }

  public deleteImage(projectId: string, imageId: string): Observable<any> {
    return this._restApiService.delete({
      url: this.endpoints.deleteImages,
      parameters: {
        projectId,
        id: imageId
      }
    });
  }

  public uploadImage(projectId: string, resourceId: string, imageId: string, imageBlob: any, 
    type: string, fileExtension = 'png', isPreview = false):
    Observable<string> {
    return this._restApiService.postFile({
      url: this.endpoints.saveImages,
      parameters: {
        resourceId,
        projectId,
        id: imageId,
        isPreview
      }
    }, imageBlob, type).pipe(map((response: any) => {
      const imageURL = `api/${projectId}/resources/${resourceId}/images/img_${imageId}.${response.extension}`;
      return imageURL;
    }));
  }

  public async duplicateImage(projectId: string, resourceId: string, imageIdOrigin: string, imageIdDestination: string) {
    const promise = await this._restApiService.post({
      url: this.endpoints.duplicateImage,
      parameters: {
        resourceId,
        projectId,
        imageIdOrigin,
        imageIdDestination
      }
    }).toPromise();

    return promise;
  }

  public  duplicateImages(projectId: string, resourceId: string, images: { [key: string]: string }) {
    return this._restApiService.post({
      url: this.endpoints.duplicateImages,
      parameters: {
        resourceId,
        projectId
      },
      body: images
    }).toPromise();
  }


  public fileConfirmationAlert() {
    this._modalRef = this._modalService.open(ConfirmDialogModalComponent, {
      backdropClass: 'cdk-overlay-dark-backdrop-custom',
      maxWidth: '500px',
      closeOnBackdropClick: false,
      data: {
         message: 'messages.error.IMAGE_EXTENSION_NOT_SUPPORTED',
         title: 'messages.error.FILE_EXTENSION_ERROR',
         confirmButtonText: 'siteeditor.global.OK',
         buttonColor: 'error',
         cancelButton: false,
        }
    });

    this._modalRef.afterClosed().subscribe(async result => {
      if (result && result.type === 'confirm') {
        this._modalRef.close();
      } else {
        this._modalRef.close();
      }
    });
  }


}
