import {Action} from '@ngrx/store';

export const siteActions = {
  LOAD_SITE: '[Site Actions] Load site',
  LOAD_SITE_SUCCESS: '[Site Actions] Load site success',
  LOAD_SITE_FAIL: '[Site Actions] Load site fail',
};

export class LoadSite implements Action {
  readonly type = siteActions.LOAD_SITE;
  constructor(public payload: string) { }
}

export class LoadSiteSuccess implements Action {
  readonly type = siteActions.LOAD_SITE_SUCCESS;
  constructor(public payload: ISite) { }
}

export class LoadSiteFail implements Action {
  readonly type = siteActions.LOAD_SITE_FAIL;
  constructor(public payload: ISite) { }
}


export type SiteActions =  LoadSite
  | LoadSiteSuccess
  | LoadSiteFail
  ;
