// Local imports
import { Component } from '@angular/core';
import { Router } from '@angular/router';

@Component({
  selector: 'bs-site-renderer',
  template: '',
})
export class SiteRenderComponent {
  private site: ISite;

  constructor(private _router: Router) {
  }

  redirectToSite(home: string) {
    this._router.navigate([this.site.url, this.site.homePageName]);
  }
}
