import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { NotFoundComponent } from 'app/common/components/not-found/not-found.component';
import { LoginComponent } from 'app/common/components/login/login.component';
import { SiteRenderComponent } from 'app/render/site-render.component';
import { PageRenderComponent } from 'app/render/page-render/page-render.component';
import { RouteActivatorRender } from 'app/common/services/oauth/route-activator-render';
import { PublicRouteActivator } from 'app/common/services/oauth/login.service';
import { SiteListRenderComponent } from 'app/render/site-list-render/site-list-render.component';
import { UnauthorizedComponent } from 'app/common/components/unauthorized/unauthorized.component';

const routes: Routes = [
  { path: 'login', component: LoginComponent },
  { path: '', component: SiteListRenderComponent, canActivate: [PublicRouteActivator] },
  { path: '404', component: NotFoundComponent },
  { path: 'unauthorized', component: UnauthorizedComponent },
  { path: ':siteName/404', component: NotFoundComponent },
  { path: ':siteName', component: SiteRenderComponent, canActivate: [RouteActivatorRender] },
  { path: ':siteName/:pageName', component: PageRenderComponent, canActivate: [RouteActivatorRender] },
  { path: '*', redirectTo: '/404' },
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule],
})
export class RenderRoutingModule {}
